<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" @click="showPop = true;datetime = $dateFormat(new Date(),'yyyy-MM-dd hh:mm')" style="color:#fff;font-size:.4rem"></i>
    </yd-navbar>
    <h3 class="water-title">枢纽运行 {{ showtime }}</h3>
    <div class="table-box">
      <div class="box-left">
        <table class="table">
          <thead>
            <tr>
              <th>
                <span>电站名</span>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(value,index) in groupList" :key="index">
              <td>
                <span>{{ value.grpname }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="box-right">
        <table class="table">
          <thead>
            <tr>
              <th v-for="(value,index) in tabletdata.name" :key="index">
                <span>{{ value }}</span>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(value,index) in tabletdata.data" :key="index">
              <td v-for="(item,list) in value" :key="list">
                <span>{{ item.value }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <yd-popup v-model="showPop" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">查询时间</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">选择时间：</span>
          <yd-datetime type="datetime" v-model="datetime" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
        </yd-cell-item>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;queryRealData()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defaultFastTime: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss"),
      fastTime: [
        {
          name: "今日",
          value: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")
        },
        {
          name: "昨日",
          value: this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd hh:mm:ss"
          )
        }
      ],
      showPop: false,
      showtime: "",
      datetime: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
      tabletdata: {
        name: this.getNameList(),
        data: this.getDataList()
      },
      groupList: this.getGroupList(),
    };
  },
  computed: {
    pointEles: function() {
      if (this.pointList.length) {
        return this.pointList[0].eles;
      } else {
        return [];
      }
    },
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
    },
    timeSelect(s) {
      this.datetime = s[0];
      this.defaultFastTime = s[0];
    },
    // 获取测站名称
    getNameList() {
      let list = [];
      this.getGroupList()[0].eles.forEach(function(v, i) {
        list.push(v.pname);
      });
      return list;
    },
    // 获取点号数据
    getDataList() {
      let list = [];
      let GroupList = this.getGroupList();
      GroupList.forEach(function(v, i) {
        let clist = [];
        if (v.eles.length) {
          v.eles.forEach(function(vv, ii) {
            clist.push({ senid: vv.elecd, value: "", querytype: vv.querytype });
          });
        }else{
          GroupList[0].eles.forEach(function(vv, ii) {
            clist.push({ senid: 0, value: "", querytype: '--' });
          });
        }
        list.push(clist);
      });
      return list;
    },
    // 获取测点数据
    getGroupList() {
      let pageConfig = this.$store.getters.pageConfig;
      let menuId = this.$route.query.menuId;
      let list = [];
      pageConfig.forEach(function(v, i) {
        if (v.menuId == menuId) list.push.apply(list, v.groups);
      });
      return list;
    },
    // 获取点号
    getsenids(type) {
      //0:实时，默认查实时,1:小时，2:日，3:月,
      let list = [];
      this.getGroupList().forEach(function(v, i) {
        v.eles.forEach(function(vv, ii) {
          if (vv.querytype == type) list.push(vv.elecd);
        });
      });
      return [...new Set(list)].join(",");
    },
    // 查询数据
    queryRealData() {
      let bt = this.defaultFastTime
        ? this.defaultFastTime
        : this.datetime + ":00";
      this.$get(
        "/appquery/queryRealData.action",
        {
          rIds: this.getsenids(0),
          hourIds: this.getsenids(1),
          dayIds: this.getsenids(2),
          monthIds: this.getsenids(3),
          // senids: this.getsenids(),
          // timeStr:'2018-02-01 20:59:59'
          timeStr: bt
        },
        data => {
          let self = this;
          self.tabletdata.data.forEach(function(v, i) {
            v.forEach(function(vv, ii) {
              switch (vv.querytype) {
                case 0:
                  let exactData0 = data.real.filter(function(item) {
                    return item.idStr == vv.senid;
                  });
                  exactData0.length
                    ? (self.tabletdata.data[i][ii].value =
                        exactData0[0].valueStr)
                    : (self.tabletdata.data[i][ii].value = "-");
                  break;
                case 1:
                  let exactData1 = data.hour.filter(function(item) {
                    return item.idStr == vv.senid;
                  });
                  exactData1.length
                    ? (self.tabletdata.data[i][ii].value =
                        exactData1[0].valueStr)
                    : (self.tabletdata.data[i][ii].value = "-");
                  break;
                case 2:
                  let exactData2 = data.day.filter(function(item) {
                    return item.idStr == vv.senid;
                  });
                  exactData2.length
                    ? (self.tabletdata.data[i][ii].value =
                        exactData2[0].valueStr)
                    : (self.tabletdata.data[i][ii].value = "-");
                  break;
                case 3:
                  let exactData3 = data.month.filter(function(item) {
                    return item.idStr == vv.senid;
                  });
                  exactData3.length
                    ? (self.tabletdata.data[i][ii].value =
                        exactData3[0].valueStr)
                    : (self.tabletdata.data[i][ii].value = "-");
                  break;
                default:
                  break;
              }
            });
          });
          this.showtime = bt;
        }
      );
    }
  },
  created() {
    this.queryRealData(); // 获取测站测点值
  }
  // 缓存渲染完成
  /* activated() {
    this.queryRealData(); // 获取测站测点值
  } */
};
</script>

<style scoped>
.icon-custom-mation {
  color: #fff;
}
.water-title {
  font-size: 0.3rem;
}
.water-title {
  padding: 0.3rem 0;
}
.water-msg {
  text-align: left;
  padding: 0.3rem 0.1rem;
}
.water-msg p {
  color: red;
  margin-top: 0.15rem;
}
.table-box {
  width: 100%;
  display: flex;
}
.box-left {
  width: 3.1rem;

}
.box-left table {
  width: 100%;
  border-right: 1px solid #ccc !important;
}
.box-right {
  width: calc(100% - 3.1rem);
  overflow: auto;
}
.box-right table {
  min-width: 100%;
  border-left: hidden;
}
.table {
  border-collapse: collapse;
  /* background: #fff; */
}
.table th,
.table td {
  border: 1px solid #ccc;
  padding: 0.1rem;
}
.table thead tr {
  background-color: #fff;
  color: #2279ff;
}
.table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.table th > span,
.table td > span {
  display: block;
  white-space: nowrap;
  height: 0.35rem;
}
/* .table th:first-of-type > span,
.table td:first-of-type > span {
  
} */
</style>




