<template>
  <div style="padding:.1rem 0;">
        <article v-for="(value,index) in queryList" :key="index">
          <section>引水站点<span>{{ value.plansite }}</span></section>
          <section>引水口<span>{{ value.planport }}</span></section>
          <section>计划引水量<span>{{ value.planrfl }}</span></section>
          <section>引水量下限<span>{{ value.planmin }}</span></section>
          <section>引水量上限<span>{{ value.planmax }}</span></section>
          <section>申请时间<span>{{ value.planreqt }}</span></section>
          <section>要求生效时间<span>{{ value.planefft }}</span></section>
        </article>
    <no-data v-if="!queryList.length"></no-data>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryList: []
    };
  },
  activated() {
    this.$post(
      "/appys/getYsPlan.action",
      {
        freqId: this.$route.params.id
      },
      data => {
        this.queryList = data.data;
      }
    );
  }
};
</script>

<style scoped>
article {
  display: flex;
  flex-wrap: wrap;
  padding: 0.3rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
  animation: fadeInUp 1s;
}
article > section {
  font-size: 0.25rem;
  width: 100%;
  text-align: left;
  padding: 0.2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
</style>




