<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff" v-if="!showDetail">
      <div slot="left" @click="goback">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <mt-index-list v-if="!showDetail">
      <mt-index-section v-for="(value,index) in addressData" :key="index" :index="value.key">
        <yd-cell-item v-for="(item,list) in value.value" :key="list" @click.native="getUserData(item)" type="link" :href="'/menu/addressList/addressDetail/'+ item.userid">
          <yd-icon slot="icon" class="address-icon" name="tongxunlu" custom></yd-icon>
          <span slot="left">{{ item.realname }}</span>
        </yd-cell-item>
      </mt-index-section>
    </mt-index-list>
    <router-view v-if="showDetail" :datauser="userData"></router-view>
  </div>

</template>


<script>
export default {
  data() {
    return {
      addressData: [],
      userData: {}
    };
  },
  computed: {
    showDetail: function() {
      if (this.$route.name == "addressList") {
        return false;
      } else {
        return true;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    getUserData(data) {
      this.userData = {
        name: data.realname,
        phone: data.phone,
        mobile: data.mobile
      };
    }
  },
  created() {
    this.$get("/appuser/queryUserList.action", {}, data => {
      this.addressData = data;
    });
  }
};
</script>

<style>
.mint-indexlist-content {
  text-align: left !important;
  margin: 0 !important;
}
.mint-indexsection-index {
  color: orange !important;
  font-size: 0.4rem !important;
}
.mint-indexlist-nav {
  border-left: none !important;
  background-color: transparent;
}
.mint-indexlist-navitem {
  padding: 2px 0.2rem !important;
  font-size: 0.4rem !important;
  font-weight: 600 !important;
}
.yd-cell-icon i.address-icon {
  font-size: 0.42rem !important;
  color: #2279ff !important;
  background-color: transparent !important;
}
</style>





