<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>

    <div v-if="!showSub">
      <yd-grids-group :rows="5" id="controlGroup">
        <yd-grids-item v-for="(bean,index) in mTypeList" :key="index" @click.native="switchReport(index,bean)">
          <span slot="text" :class="{ red:changeRed == index}">{{bean.name}}</span>
        </yd-grids-item>
      </yd-grids-group>


      <yd-search placeholder="报表名" v-model="searchValue" :on-submit="searchReport" :on-cancel="searchCancel"></yd-search>



      <!--右侧弹出选项,点击查询-->
      <yd-popup id="filterDiv" v-model="showRight" position="right" width="50px">
        <yd-grids-group title="其他类型" class="menu-item">
          <yd-cell-item v-for="(bean,index) in mOtherTypeList" :key="index" @click.native="switchReport(index,bean, true)">
            <span slot="left">{{bean.name}}</span>
          </yd-cell-item>
        </yd-grids-group>
      </yd-popup>

      <yd-cell-group class="reportCell">
        <yd-cell-item arrow v-for="(value,index) in mDataList" :key="index" type="link" :href="{name:'menuReportDetail',params:{id:value.reportid}}"> <!---->
          <span slot="left" class="report-index">{{ index+1 }}</span>
          <div slot="left" class="report-msg">
            <span>{{ value.alias }}</span> <br>
            <small>类型：{{ value.typename }}</small>
          </div>
        </yd-cell-item>
      </yd-cell-group>
    </div>
    <!-- 路由外链 -->
    <router-view v-if="showSub"></router-view>
  </div>
</template>

<script >
  export default {
    data() {
      return {
        maxShowSize: 3,// 导航最大显示数量
        preShowType:-1,// 上一次显示的类型,用于再次点击相同按钮时,不重复查询
        // [
        //   {
        //     "data":[
        //       {
        //         "firstchar":"HDSQZXLB",
        //         "id":0,
        //         "typename":"日报",
        //         "reportid":"75c65afbd2e748e6a05e27ec295700ba",
        //         "ord":0,
        //         "type":1,
        //         "reportname":"河道水情查询列表"
        //       }
        //     ],
        //     "type":1
        //   },
        //  {
        //   "data":[
        //   {
        //     "firstchar":"XQXLDSKRKSSLNTJB",
        //     "id":1,
        //     "typename":"月报",
        //     "reportid":"090ec5aa6cc047c29540f36f94080260",
        //     "ord":1,
        //     "type":2,
        //     "reportname":"汛期小浪底水库入库水沙量年统计表"
        //   }
        // ],
        //   "type":2
        // },
        // ],
        mDefaultDataAndTypeList:[],// 从数据库读取的数据

        mDataList:[],//当前显示的报表列表[{
        //     "firstchar":"XQXLDSKRKSSLNTJB",
        //     "id":1,
        //     "typename":"月报",
        //     "reportid":"090ec5aa6cc047c29540f36f94080260",
        //     "ord":1,
        //     "type":2,
        //     "reportname":"汛期小浪底水库入库水沙量年统计表",
        //     "alias":"aaa",别名
        //   },{}...{}]
        mTypeList:[
          {
            "type":1,
            "name":"日报"
          }
        ], // 最大5个,超过5个,则放在mOtherTypeList中

        mOtherTypeList:[],// 用于报表类型太多放在右侧
        changeRed:-1,// 初始不选择任何一个

        showRight:false, // 显示右侧过滤信息面板
      };
    },
    computed: {
      showSub: function () {
        if (this.$route.name === 'menuReport') {
          return false;
        } else {
          return true;
        }
      },
      ismenu:function(){
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }

      },
    },
    methods: {
      goback() {
        this.$router.go(-1);
      },

      // 其他类型按钮
      otherFilter()
      {
        if(this.mOtherTypeList && this.mOtherTypeList.length > 0)
        {
          this.showRight = !this.showRight;
        }
      },

      // 切换报表
      switchReport:function(index, value, isOther){
        // 如果是显示的其他
        // console.log("点击", index, value);
        if(value.type == 999999)
        {
          this.otherFilter();
          return;
        }

        if(!isOther)
        {
          this.changeRed = index;
        }
        else
        {
          this.changeRed = -1;
        }

        if(this.preShowType == value.type)
        {
          this.preShowType = value.type;
          if(isOther)
          {
            this.otherFilter();
          }
          return;
        }
        this.preShowType = value.type;
        // console.log("切换--:" ,index,value.name,value.type);
        let cType = value.type;

        if(this.mDefaultDataAndTypeList && this.mDefaultDataAndTypeList.length > 0)
        {
          let self = this;
          this.mDataList = [];
          this.mDefaultDataAndTypeList.forEach(function (bean) {
            if(bean.reMap && bean.reMap.length > 0)
            {
              if(bean.type == cType)
              {
                bean.reMap.forEach(function (ele) {
                  self.mDataList.push(ele);
                });
              }
            }
          });
        }


        if(isOther)
        {
          this.otherFilter();
        }
      },



      // 搜索
      searchReport(param){
        if(param != null && param.length > 0)
        {
          if(param != null && param.length > 0)
          {
            // 如果搜索得到报表,则清空之前的内容
            let self = this;
            let defaultData = this.mDefaultDataAndTypeList;
            if(defaultData)
            {
              let self = this;
              this.mDataList = [];
              this.mDefaultDataAndTypeList.forEach(function (bean) {
                if(bean.reMap && bean.reMap.length > 0)
                {
                  bean.reMap.forEach(function (ele) {
                    if(ele.alias.indexOf(param)>=0)// 用别名搜索
                    {
                      self.mDataList.push(ele);
                    }
                  });
                }
              });
            }
          }
        }
      },

      // 取消搜索
      searchCancel()
      {
        let self = this;
        let cType = this.preShowType;
        let defaultData = this.mDefaultDataAndTypeList;
        if(defaultData) {
          let self = this;
          this.mDataList = [];
          this.mDefaultDataAndTypeList.forEach(function (bean) {
            if(bean.reMap && bean.reMap.length > 0)
            {
              if(cType == -1 || bean.type == cType)
              {
                bean.reMap.forEach(function (ele) {
                  self.mDataList.push(ele);
                });
              }
            }
          });
        }
      },


      // getReportList() {
      //   this.$post(
      //     "/appreport/getReportList.action",
      //     {},
      //     (data) => {
      //       this.mDataList = data.data;
      //       // console.log("获取所有数据:", data.data);
      //     }
      //   );
      // },

      // 获取配置的报表
      getDefaultAllReport()
      {
        this.$post(
          "/report/getAllReport.action",
          {},
          (data) => {
            // this.mDataList = data.data;
            // console.log("获取配置数据:", data, JSON.stringify(data));
            if(data.result)
            {
              let oriData = data.data;
              if(oriData && oriData.length > 0)
              {
                this.mDefaultDataAndTypeList = oriData;
                let self = this;
                this.mTypeList = [];
                this.mOtherTypeList = [];
                self.mDataList = [];

                oriData.forEach(function (bean) {
                  let bb = {};
                  bb.name = bean.typename;
                  bb.type = bean.type;
                  // 超过5个放在其他type中
                  if(self.mTypeList.length > self.maxShowSize)
                  {
                    self.mOtherTypeList.push(bb);
                  }
                  else
                  {
                    self.mTypeList.push(bb);
                  }
                  if(bean.reMap && bean.reMap.length > 0)
                  {
                    // let typeName = "-";
                    bean.reMap.forEach(function (bData) {
                      // typeName = bData.typename;
                      self.mDataList.push(bData);
                    });
                    // bb.name = typeName;
                  }
                });

                // 判断是否大于最大显示行数

                // 超过5个放在其他type中
                if(self.mTypeList.length > self.maxShowSize)
                {
                  let bb = {};
                  bb.name = "其他";
                  bb.type = 999999;
                  self.mTypeList.push(bb);
                }

                // console.log(self.mOtherTypeList, self.mTypeList);
              }
            }

          }
        );
      },

    },

    activated(){
      this.getDefaultAllReport();
      this.changeRed = -1;
    },
    // 初始化
    created() {
      // console.log("mreport created 获取数据, 当前路由为:"+(this.$route.name));
    }
  }
</script>

<style>
  .red{
    color: red;
  }


  #controlGroup .yd-grids-txt {
    padding: 0 0.01rem;
    float: none;
    line-height: 0.3rem;
    overflow: inherit;
  }

  .report-index {
    display: inline-block;
    width: .5rem;
    height: .5rem;
    line-height: .5rem;
    color: #fff;
    background-color: #5A93FD;
    border-radius: 50%;
  }

  .report-msg {
    text-align: left;
    margin-left: .3rem;
  }

  .report-msg > span {
    font-size: .3rem;
    font-weight: 600;
  }

  .report-msg > small {
    font-size: .25rem;
    color: #ccc;
  }

  .reportCell .yd-cell-item  {
    width: 100%;
    display: inline-flex;
    margin-top: 0.2rem;
  }
</style>




