<template>
  <div id="login">
    <div class="login-logo">
      <div style="margin-bottom:.4rem">
        <div id="logo"></div>
      </div>
      {{ $store.state.appTitle }}
    </div>
    <yd-cell-group class="login-input">
      <yd-cell-item>
        <span slot="left">
          <i class="icon-custom-user"></i> 账号：
        </span>
        <yd-input slot="right" required v-model="userName" max="20" placeholder="请输入用户名"></yd-input>
      </yd-cell-item>
      <yd-cell-item>
        <span slot="left">
          <i class="icon-custom-password"></i> 密码：
        </span>
        <yd-input slot="right" type="password" v-model="passWord" placeholder="请输入密码"></yd-input>
      </yd-cell-item>
    </yd-cell-group>
    <yd-button-group>
      <yd-button size="large" type="primary" shape="circle" @click.native="loginin">登录</yd-button>
    </yd-button-group>
    <div style="padding:.2rem 1rem .2rem 1.24rem">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-checkbox v-model="remeber">记住信息</yd-checkbox>
        </yd-flexbox-item>
        <yd-flexbox-item v-if="$store.state.deviceBauth">
          <span @click="showPop=true" style="font-size:12px">设备授权</span>
        </yd-flexbox-item>
        <yd-flexbox-item>
          <span @click="updateServer" style="font-size:12px">服务器配置</span>
        </yd-flexbox-item>
      </yd-flexbox>
    </div>
    <yd-popup v-model="showPop" position="center" :close-on-masker="true" width="90%">
      <yd-cell-group>
        <yd-cell-item>
          <span slot="left">设备名称 ：</span>
          <yd-input
            slot="right"
            v-model="appInfo.name"
            ref="devicename"
            required
            placeholder="请输入设备名称"
          ></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">序列号 ：</span>
          <span slot="left">{{ appInfo.id?appInfo.id:'' }}</span>
        </yd-cell-item>
        <yd-button type="warning" @click.native="accredit" style="margin:10px">提交</yd-button>
      </yd-cell-group>
    </yd-popup>
  </div>
</template>

<script type="text/babel">
export default {
  data() {
    return {
      userName: "",
      passWord: "",
      remeber: false,
      showPop: false,
      appInfo: {}
    };
  },
  methods: {
    loginin() {
      // 判断用户名和密码是否填写
      if (!this.userName || !this.passWord) {
        this.$dialog.alert({ mes: "用户名或密码错误！", icon: "error" });
        return;
      }
      this.$get(
        "/appuser/userLogin.action",
        {
          userName: this.userName,
          passWord: this.passWord,
          deviceno: this.appInfo.id
        //   deviceno:'02BD5526C0657CEB640D507E5579E91D5EB1DFD9'
        },
        data => {
          if (data.result) {
            let arrValue = [];
            arrValue.push(data.data.appConfig);
            let arrs = [];
            arrs = JSON.parse(JSON.stringify(arrValue));
            for (let i = 0; i < arrs.length; i++) {
              delete arrs[i].appContact;
              delete arrs[i].appManual;
              delete arrs[i].appName;
              delete arrs[i].appTopo;
              delete arrs[i].bgImg;
              delete arrs[i].updateInfo;
              delete arrs[i].versionNo;
            }
            cordova.exec(null, null, "XLDPlugin", "setVHLiveConfig", arrs);
            //   设置app推送别名
            document.addEventListener("deviceready", function() {}, false);
            cordova.exec(null, null, "XLDPlugin", "setAlias", [
              {
                alias: data.data.userConfig.userInfo.userId
              }
            ]);

            // 判断是否有默认页信息，没有则菜单的第一个是默认页
            let routeName = data.data.userConfig.defaultPage.routeName;
            routeName
              ? (data.data.userConfig.defaultPage.routeName =
                  data.data.userConfig.defaultPage.routeName)
              : (data.data.userConfig.defaultPage.routeName =
                  data.data.userConfig.menuConfig[0].menus[0].routeName);
            // 缓存账号密码
            window.localStorage.setItem(
              "loginsave",
              JSON.stringify({
                remeber: this.remeber,
                userName: this.userName,
                passWord: this.passWord
              })
            );
            // 缓存用户登录信息
            this.$store.commit("datainit", data.data);
            this.$dialog.toast({
              mes: data.message,
              timeout: 3000,
              icon: "success",
              callback: () => {
                // 跳转默认页
                var menu = this.$store.getters.getMenuConfig;
                if (menu.includes("HOME")) {
                  this.$router.push({
                    name: "home" + data.data.userConfig.defaultPage.routeName,
                    query: {
                      menuId: data.data.userConfig.defaultPage.menuId
                    }
                  });
                } else {
                  this.$router.push("/menu");
                }
                // 添加自定义登录成功事件
                this.$emit("islogin");
              }
            });
          } else {
            this.$dialog.alert({ mes: data.message, icon: "error" });
          }
        }
      );
    },
    accredit() {
      const devicename = this.$refs.devicename;
      if (!devicename.valid) {
        this.$dialog.toast({
          mes: "设备名称" + devicename.errorMsg,
          timeout: 3000,
          icon: "error"
        });
        return;
      }
      this.$post(
        "/device/addDevice.action",
        {
          devicename: this.appInfo.name,
          deviceno: this.appInfo.id,
          devicetype: this.appInfo.type,
          deviceversion: this.appInfo.ver,
          mac: this.appInfo.mac
        },
        res => {
          if (res.result) {
            this.$dialog.toast({
              mes: res.message,
              timeout: 3000,
              icon: "success"
            });
          } else {
            this.$dialog.toast({
              mes: res.message,
              timeout: 3000,
              icon: "error"
            });
          }
        }
      );
      this.showPop = false;
    },
    updateServer() {
      this.$emit("changeServer");
    }
  },
  created() {
    var _this = this;
    // 查询app名称
    if (window.localStorage.getItem("ip")) {
      this.$axios.get("/appuser/getAppInfo.action").then(res => {
        this.$store.commit("changeAppTitle", res.data.data.appName);
        this.$store.commit("initDeviceBauth", res.data.data.deviceBauth);
        this.$store.commit("initShowVideo", res.data.data.tvFunction);
      });
    }
    // 初始化用户名和密码为上次登录的用户名和密码
    const loginsave = window.localStorage.getItem("loginsave");
    if (loginsave && JSON.parse(loginsave).remeber) {
      this.remeber = JSON.parse(loginsave).remeber;
      this.userName = JSON.parse(loginsave).userName;
      this.passWord = JSON.parse(loginsave).passWord;
    }
    // 查询设备信息
    cordova.exec(
      function(data) {
        _this.appInfo = data;
      },
      null,
      "XLDPlugin",
      "getPhoneInfo",
      []
    );
  }
};
</script>

<style>
#login {
  height: 100%;
  color: #a3b0bd;
  background: url("../../assets/login_bg.png") no-repeat;
  background-size: 100% 100%;
}
#logo {
  display: inline-block;
  width: 1rem;
  height: 1.3rem;
  background: url("../../assets/login_logo.png") no-repeat;
  background-size: 100%;
}
.login-logo {
  height: 4.5rem;
  color: #fff;
  text-align: center;
  font-size: 0.35rem;
  padding-top: 1.7rem;
  text-shadow: 0 0 10px #fff;
}
.login-input {
  padding: 0 1rem;
}
.login-input .yd-cell-left {
  color: #a3b0bd;
}
.login-input .yd-cell {
  background-color: transparent;
}
.login-input .yd-cell::after {
  border: none;
}
.login-input .yd-cell-right input {
  color: #fff !important;
}
#login .yd-button {
  padding: 0 1rem 0 1.24rem;
}
#login .yd-button > button {
  height: 0.8rem;
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid #a3b0bd;
}
#login .yd-button > button:active,
#login .yd-button > button:visited {
  background-color: rgba(255, 255, 255, 0.5);
  color: #415058;
}
#login .yd-checkbox-icon {
  width: 15px !important;
  height: 15px !important;
}
#login .yd-checkbox-text {
  font-size: 12px;
  color: #a3b0bd;
}
</style>



