<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <h3 class="water-title">{{titleTime}}</h3>
    <div v-if="showTable">
      <div class="table-box">
        <div class="box-left">
          <table class="table">
            <thead>
            <tr>
              <th>
                <span>测点</span>
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="name in this.mNameList">
              <td>
                <span>{{ name }}</span>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="box-right">
          <table class="table">
            <thead>
            <tr>
              <th v-for="time in this.mTimeList">
                <span>{{ time }}</span>
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="rowData in this.mDataList">
              <td v-for="value in rowData">
                <span>{{ value.value }}</span>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div>
      <div v-if="!showTable">
        <h3 class="water-title">此时间段数据为空</h3>
      </div>
    </div>
  </div>
</template>

<script>


  export default {
    timeType: '', // 时间类型
    startTime: '',
    endTime: '',
    // 查询的数据结果
    data() {
      return {
        mCheckedElement: [], // 需要查询的元素
        mNameList: [],
        mTimeList: [],
        mIdList: [],
        titleTime: "",
        mDataList: [[]],
        showTable: true,
        // tabletdata: [
        //   [{
        //     time: "10:00",
        //     value: "5"
        //   },
        //   {
        //     time: "13:00",
        //     value: "15"
        //   }],
        //   [{
        //     time: "10:00",
        //     value: "5"
        //   },
        //
        //     {
        //       time: "13:00",
        //       value: "15"
        //     }],
        // ]
      }
    },
    computed:{
      ismenu:function(){
        console.log("ismenu",this.$route.name,this.$route.name.indexOf('menu'));
        if (this.$route.name.indexOf('menu')) {
          console.log("return ismenu",true);
          return true;
        } else {
          console.log("return ismenu",false);
          return false;
        }

      }
    },
    mounted() {

    },

    methods: {
      goback() {
        this.$router.go(-1);
      },
      // 获取数据
      getData: function () {
        let queryParam = {
          senids: this.mIdList.join(","),
          type: this.timeType,
          startTime: this.startTime,
          endTime: this.endTime
        };
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appcomposite/queryCompositeData.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if (data.result) {
              // 得到数据和时间序列
              this.mDataList = data.data;
              this.mTimeList = data.time;
              this.showTable = true;
            }
            else {
              this.showTable = false;
              this.$dialog.alert({mes: "数据为空", icon: "error"});
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.showTable = false;
            this.$dialog.loading.close();
            this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log("查询异常", e);
          });
      },


    },
    created() {


    },
    // Called when a kept-alive component is activated.
    activated() {
      let param = this.$route.params;
      this.mCheckedElement = param.data;
      this.timeType = param.timeType;
      this.startTime = param.startTime;
      this.endTime = param.endTime;


      this.titleTime = "查询时间:";
      this.titleTime += this.startTime;
      this.titleTime += '-' + this.endTime;
      // console.log("this param is:", param.data);
      // console.log("this title is:", this.titleTime);

      // 得到名称和id号
      let senids = [];
      let names = [];
      this.mCheckedElement.forEach(function (v) {
        senids.push(v.id);
        names.push(v.name);
      });
      this.mIdList = senids;
      this.mNameList = names;

      // 获取数据
      this.getData();
    }
  }
</script>

<style scoped>
  .water-title,
  .water-msg {
    padding: 0.3rem 0;
  }

  .water-msg {
    color: red;
  }

  .table-box {
    width: 100%;
    padding: 0.1rem;
    display: flex;
  }

  .box-right {
    overflow: auto;
  }

  .table {
    border-collapse: collapse;
  }

  .table th,
  .table td {
    border: 1px solid #ccc;
    padding: 0.1rem 0.15rem;
    height: 1.5rem;
    width: 5rem;
  }

  .table thead tr {
    background-color: #fff;
    color: #2279ff;
  }

  .table tbody tr:nth-of-type(even) {
    background-color: #fff;
  }

  .table th > span,
  .table td > span {
    display: inline-block;
    width: 1rem;
  }

  .table th:first-of-type > span,
  .table td:first-of-type > span {
    width: 1.2rem;
  }
</style>



