<template>
	<div>
		<yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
			<router-link to="/mation" slot="left" v-if="!ismenu">
				<yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
			</router-link>
			<div slot="left" @click="$router.push('/menu')" v-if="ismenu">
				<yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
			</div>
			<i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" v-if="!showdetail" @click="showPop = true"></i>
		</yd-navbar>
		<div class="yd-tab-box yd-tab-nav-nomal">
			<ul class="yd-tab-nav" style="color: rgb(255, 94, 83); width: auto;">
				<li :class="{'yd-tab-nav-item':true, 'yd-tab-active':componentId==='WaterApply'?true:false}" @click="componentId='WaterApply';"><a href="javascript:">引水申请台账</a></li>
				<li :class="{'yd-tab-nav-item':true, 'yd-tab-active':componentId==='WaterData'?true:false}" @click="componentId='WaterData';"><a href="javascript:">引水数据分析</a></li>
			</ul>
		</div>
		<keep-alive>
			<component :is="componentId" :showPop.sync="showPop"></component>
		</keep-alive>
	</div>
</template>

<script>
import WaterApply from './WaterApply';
import WaterData from './WaterData';
export default {
	components: {
		WaterApply,
		WaterData
	},
	data(){
		return {
			componentId:'WaterApply',
			showPop:false
		}
	},
	computed: {
    ismenu: function() {
      if (this.$route.name.includes("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
}
</script>



