<template>
    <div>
        <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
            <router-link to="/mation" slot="left" v-if="ismenu">
                <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
            </router-link>
            <div slot="left" @click="goback" v-if="!ismenu">
                <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
            </div>
        </yd-navbar>
        <yd-tab>
            <yd-tab-panel v-for="(value,index) in hostData" :key="index" :label="value.id">
                <div class="card">
                    <div class="card-title">
                        <strong>系统信息</strong>
                    </div>
                    <div class="card-content">
                        <div style="width:80%">
                            <strong>操作系统：</strong> {{ value.content.compute.sysName }}
                        </div>
                        <div style="width:80%">
                            <strong>代理引擎IP：</strong> {{ value.content.compute.agentIP }}
                        </div>
                        <div style="width:80%">
                            <strong>启动时间：</strong> {{ value.time }}
                        </div>
                        <div style="width:80%">
                            <strong>CPU厂家：</strong> {{ value.content.compute.cpuVendor }}
                        </div>
                        <div class="mation-left">
                            <strong>CPU数量：</strong> {{ value.content.compute.cpuCount }}
                        </div>
                        <div class="mation-right">
                            <strong>进程数：</strong> {{ value.content.compute.procCount }}
                        </div>
                        <div style="width:80%">
                            <strong>内存总量：</strong> {{ value.content.compute.memSum }} M
                        </div>
                        <div style="width:80%">
                            <strong>已用内存：</strong> {{ value.content.compute.memUsed }} M
                        </div>
                    </div>
                </div>
                <div class="card">
                    <div class="card-title">
                        <strong>数据统计</strong>
                    </div>
                    <div class="card-content" style="padding:.1rem 1rem;">
                        <div v-for="(v,i) in $popArray(value.content.compute.disk.split('|'))" :key="i">
                            <strong>{{ v.slice(0,1) }}盘:</strong> {{ v.split('_')[2] }}/{{ v.split('_')[1] }}
                            <yd-progressbar type="line" :progress="v.split('_')[2]/v.split('_')[1]" trail-width="3" :trail-color="$dynamicColor(1-v.split('_')[2]/v.split('_')[1])" stroke-color="#C3E6F9"></yd-progressbar>
                        </div>
                    </div>
                    <div class="card-content" style="display:flex;justify-content: space-around;">
                        <div class="card-progress">
                            <yd-progressbar :progress="value.content.compute.cpuUsePrec" trail-width="10" :trail-color="$dynamicColor(1- value.content.compute.cpuUsePrec)" stroke-color="#C3E6F9" stroke-width="10">
                                <yd-countup :endnum="value.content.compute.cpuUsePrec * 100" :duration="1" suffix="%"></yd-countup>
                            </yd-progressbar>
                            <div style="text-align:center">
                                CPU使用率
                            </div>
                        </div>
                        <div class="card-progress">
                            <yd-progressbar :progress="value.content.compute.memUsed/value.content.compute.memSum" trail-width="10" :trail-color="$dynamicColor(1- value.content.compute.memUsed/value.content.compute.memSum)" stroke-color="#C3E6F9" stroke-width="10">
                                <yd-countup :endnum="value.content.compute.memUsed/value.content.compute.memSum * 100" :duration="1" suffix="%"></yd-countup>
                            </yd-progressbar>
                            <div style="text-align:center">
                                内存使用率
                            </div>
                        </div>
                    </div>
                </div>
                <div class="card">
                    <div class="card-title">
                        <strong>程序状态</strong>
                    </div>
                    <div class="card-table">
                        <table>
                            <thead>
                                <tr>
                                    <th>进程名称</th>
                                    <th>进程ID</th>
                                    <th>运行状态</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,list) in value.content.process" :key="list">
                                    <td>{{ item.processName }}</td>
                                    <td>{{ item.processId }}</td>
                                    <td>{{ item.running?'运行中':'停止' }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </yd-tab-panel>
        </yd-tab>
    </div>
</template>

<script>
export default {
  data() {
    return {
      hostData: [],
      progress3: 0.9,
      progress2: 0.5,
      progress1: 0.2
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    }
  },
  created() {
    this.$post("/appagent/queryAgents.action", {}, data => {
      this.hostData = data;
    });
  }
};
</script>

<style>
.icon-custom-mation {
  color: #fff;
}
h2 {
  padding: 0.2rem 0 !important;
  font-size: 0.4rem !important;
  background-color: #fff !important;
}
.card {
  margin-top: 0.1rem;
  background-color: #fff;
}
.card:first-of-type {
  margin-top: 0;
}
.card-title {
  position: relative;
  text-align: left;
  padding: 0.1rem 0;
  padding-left: 0.7rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.card-title::before {
  content: "";
  position: absolute;
  left: 0.3rem;
  width: 0.1rem;
  height: 0.3rem;
  background-color: #639af4;
}
.card-content {
  border-bottom: 1px solid #ccc;
  padding: 0 0.5rem;
  text-align: left;
}
.card-content > div {
  display: inline-block;
  padding: 0.06rem 0;
}
.mation-left {
  width: calc(60% - 0.1rem);
}
.mation-right {
  width: calc(40% - 0.1rem);
}
.card-content > div.card-progress {
  width: 33.33%;
  padding: 0.3rem;
}
.card-progress .yd-progressbar {
  height: auto;
}
.card-table {
  margin-top: 0.1rem;
}
.card-table table {
  width: 100%;
  border-collapse: collapse;
}
.card-table table th,
.card-table table td {
  border: 1px solid #ccc;
  padding: 0.1rem 0.15rem;
}
.card-table table thead tr {
  background-color: #73a8ff;
  color: #fff;
}
.card-table table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.card-table table tbody tr:nth-of-type(odd) {
  background-color: #f7f6fb;
}
.coustom-slide .yd-slider-wrapper {
  align-items: flex-start;
}
</style>




