<template>
  <div>
    <div style="height:4rem;padding:1rem;">
      <div class="logo-box">
        <div id="version-logo"></div>
        <!-- <img src="../../../static/images/login_logo.png" alt="logo" style="width:.5rem"> -->
      </div>
      <div class="app-name" v-text="appName">
        <!-- 小浪底自动化调度系统 -->
      </div>
      <div class="app-version" v-text="appVersion">
        <!-- 版本号：V1.1.0 -->
      </div>
    </div>
    <yd-accordion>
      <yd-accordion-item title="软件下载">
        <div style="padding: .24rem">
            <img src="https://s15.postimg.cc/bj7gswx3v/code.png" style="width:40%">  
        </div>
      </yd-accordion-item>
      <yd-accordion-item title="更新说明">
        <div style="padding: .24rem; text-align:left" v-text="appexplain">       
        </div>
      </yd-accordion-item>
    </yd-accordion>
  </div>

</template>

<script>
export default {
  computed: {
    appName: function() {
      return this.$store.state.logindata.appConfig.appName;
    },
    appVersion: function() {
      return this.$store.state.logindata.appConfig.versionNo;
    },
    appexplain:function(){
      return this.$store.state.logindata.appConfig.updateInfo;
    }
  }
};
</script>

<style>
#version-logo {
  display: inline-block;
  width: 0.5rem;
  height: 0.7rem;
  background: url("../../assets/login_logo.png") no-repeat;
  background-size: 100%;
}
.logo-box {
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  background-color: #2279ff;
  border-radius: 0.15rem;
  padding: 0.25rem;
}
.app-name {
  margin: 0.2rem;
  font-size: 0.3rem;
}
.app-version {
  font-size: 0.3rem;
  color: #bebec0;
}
</style>



