<template>
    <div>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left">旧密码：</span>
                <yd-input slot="right" type="password" v-model="oldpassword" placeholder="旧密码"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left">新密码：</span>
                <yd-input slot="right" type="password" v-model="newpassword" placeholder="新密码"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left">确认新密码：</span>
                <yd-input slot="right" type="password" v-model="surepassword" placeholder="确认新密码"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <yd-button-group>
            <yd-button size="large" type="primary" shape="circle">确认</yd-button>
        </yd-button-group>
    </div>
</template>

<script type="text/babel">
export default {
  data() {
    return {
      oldpassword: "",
      newpassword: "",
      surepassword:""
    };
  },
  created () {
      this.oldpassword = this.$store.state.logindata.password
  }
};
</script>


