<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>

    <yd-tab :callback="fn">
      <yd-tab-panel label="表格"></yd-tab-panel>
      <yd-tab-panel label="图形"></yd-tab-panel>
    </yd-tab>

    <h3 class="h-title">{{title}}</h3>
    <h3 class="h-sub-title">{{titleTime}}</h3>
    <div v-if="showTable">
      <table>
        <thead>
        <tr>
          <th>时间</th>
          <th>值</th>
        </tr>
        </thead>
      </table>
      <table id="data-table">
        <tbody>
        <tr v-for="bean in mDataList">
          <td>{{bean.timeStr}}</td>
          <td>{{bean.v}}</td>
        </tr>
        </tbody>
      </table>


      <!--<yd-cell-group>-->
      <!--<yd-grids-group :rows="2">-->
      <!--<yd-grids-item class="item-head">-->
      <!--<span slot="text">时间</span>-->
      <!--</yd-grids-item>-->
      <!--<yd-grids-item class="item-head">-->
      <!--<span slot="text">数据</span>-->
      <!--</yd-grids-item>-->
      <!--</yd-grids-group>-->
      <!--<yd-grids-group :rows="2" v-for="bean in mDataList">-->
      <!--<yd-grids-item class="item-time">-->
      <!--<span slot="text">{{bean.timeStr}}</span>-->
      <!--</yd-grids-item>-->
      <!--<yd-grids-item class="item-value">-->
      <!--<span slot="text">{{bean.v}}</span>-->
      <!--</yd-grids-item>-->
      <!--</yd-grids-group>-->
      <!--</yd-cell-group>-->
    </div>
    <div>
      <div id="echartContainer" width="500px" height="400px"
           :style="{visibility: isChartVisible ? 'visible' : 'hidden'}"></div>
    </div>


  </div>
</template>

<script>


  export default {
    id: '',
    name: '',
    type: '',
    dType: '',// 数据类型  2-瞬时量5-累积量
    startTime: '',
    endTime: '',


    data() {
      return {
        myEcharts: null,
        title: "",
        titleTime: "",
        mDataList: [
          {time: "实时", value: 1},
          {time: "小时", value: 2},
          {time: "日", value: 3}
        ],
        showTable: true,
      }
    },
    components: {},
    computed: {
      isChartVisible() {
        return !this.showTable
      },
      ismenu:function(){
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }

      },
    },
    mounted() {
      // 获取窗口高度和宽度
      let deviceWidth = document.documentElement.clientWidth;
      let deviceHeight = document.documentElement.clientHeight;
      // 设置chart的高度和宽度
      let chartDom = document.getElementById('echartContainer');
      chartDom.style.width = deviceWidth + 'px';
      chartDom.style.height = (deviceHeight - 200) + 'px';
      this.myEcharts = this.$echarts.init(chartDom);
      let option = {};
      this.myEcharts.setOption(option);
      console.log("width, height:", chartDom.style.width, chartDom.style.height);
    },

    methods: {
      goback() {
        this.$router.go(-1);
      },
      // 重新设置option
      drawCharts() {
        if (this.mDataList) {
          let data = this.mDataList;
          let chartType = 'line';

          if (this.dType === 2) {
            chartType = 'line';
          }
          else if (this.dType === 5) {
            chartType = 'bar';
          }
          let optionLine = {
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                animation: false,
                label: {
                  backgroundColor: '#ccc',
                  borderColor: '#aaa',
                  borderWidth: 1,
                  shadowBlur: 0,
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  textStyle: {
                    color: '#222'
                  }
                }
              },
            },
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: data.map(function (item) {
                return item.timeStr;
              }),
              axisLabel: {
                formatter: function (value, idx) {
                  let date = new Date(value);
                  return idx === 0 ? [date.getMonth() + 1, date.getDate()] : [date.getHours(), date.getMinutes()].join(':');
                }
              },
            },
            yAxis: {
              type: 'value'
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            series: [{
              type: chartType,
              data: data.map(function (item) {
                return item.v;
              }),
              hoverAnimation: true,
              symbolSize: 6,
              itemStyle: {
                normal: {
                  color: '#c23531'
                }
              },
              showSymbol: true
            }]
          };
          this.myEcharts.setOption(optionLine);

        }

      },
      // tab的返回方法
      fn(label, key) {
        if (key === 0) {
          this.showTable = true;
        }
        else {
          this.showTable = false;
        }
      },

      // 获取数据
      getData() {
        let queryParam = {
          senid: this.id,
          type: this.type,
          startTime: this.startTime,
          endTime: this.endTime
        };
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appsingle/querySingleData.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            this.mDataList = data;
            this.$dialog.loading.close();
            this.drawCharts();
          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },


    },
    created() {


    },
    // Called when a kept-alive component is activated.
    activated() {
      this.title = '';
      let param = this.$route.params;
      this.id = param.data.id;
      // this.id = '30001016502500065';
      this.name = param.data.name;
      this.dType = param.data.dtype;
      this.type = param.type;
      this.startTime = param.startTime;
      this.endTime = param.endTime;


      this.title += this.name;
      this.titleTime = "";
      this.titleTime += this.startTime;
      this.titleTime += '-' + this.endTime;
      console.log("2this param is:" + this.$qs.stringify(param.data));
      console.log("2this param is:" + this.title);

      // 获取数据
      this.getData();
    }
  }
</script>

<style scoped>
  .h-title {
    font-size: .3rem;
    color: #6f7acc;
  }

  .item-time {
    font-size: .25rem;
  }

  .item-head {
    font-size: .35rem;
    background-color: #9cc1cc;
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  table th,
  table td {
    border: 1px solid #ccc;
    width: 50%;
    padding: 0.1rem 0.15rem;
  }

  table thead tr {
    background-color: #73a8ff;
    color: #fff;
  }

  table tbody tr:nth-of-type(even) {
    background-color: #fff;
  }

  #title-table {
    position: fixed;
    top: 1.4rem;
  }

  #data-table {
    /*margin-top: .5rem;*/
  }
</style>



