<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="show4 = true"></i>
    </yd-navbar>



    <div v-if="!showSub">
      <yd-grids-group :rows="3" item-height="40px">
        <yd-grids-item @click.native="previewDay()" style="padding: 0.2rem 0.0001rem 0.2rem 0.0001rem!important">
          <span slot="text" style="float: none">{{mPreTime}}</span>
        </yd-grids-item>

        <yd-grids-item style="padding: 0.2rem 0.0001rem 0.2rem 0.0001rem!important">
          <div slot="else">
            <toggle-btn :index="1" :name1="name1" :name2="name2" @switch="switchres"></toggle-btn>
          </div>
        </yd-grids-item>
        <yd-grids-item @click.native="nextDay()" style="padding: 0.2rem 0.0001rem 0.2rem 0.0001rem!important">
          <span slot="text" style="float: none">{{mNextTime}}</span>
        </yd-grids-item>
      </yd-grids-group>

      <div v-if="!showTable">
        <yd-badge @click.native="showSelectedElement = true" shape="square" type="warning">{{title}}</yd-badge>
        <!--<h3 class="h-title">{{title}}</h3>-->
      </div>

      <div v-if="showTable">
        <yd-badge shape="square" type="warning">{{titleName}}</yd-badge>
      </div>

      <h3 class="h-sub-title">{{titleTime}}</h3>


      <div v-if="showTable">
        <table>
          <thead>
          <tr>
            <th>名称</th>
            <th>时间</th>
            <th>值</th>
          </tr>
          </thead>
        </table>
        <table id="data-table">
          <tbody>
          <tr v-for="bean in mDataList">
            <td>{{bean.name}}</td>
            <td>{{bean.timeStr}}</td>
            <td>{{bean.v}}</td>
          </tr>
          </tbody>
        </table>
      </div>
      <div>
        <div id="echartContainer" width="500px" height="400px"
             :style="{visibility: isChartVisible ? 'visible' : 'hidden'}"></div>
      </div>


      <yd-popup id="showSelectedPop" v-model="showSelectedElement" position="center" width="60%" height="30%">
        <yd-cell-group>
          <yd-cell-item v-for="(value, index) in mCheckedElement" :key="index" @click.native="switchElement(value)">
            <span slot="left">{{ value.name }}</span>
          </yd-cell-item>
        </yd-cell-group>
      </yd-popup>

    </div>



    <div>
      <yd-backtop></yd-backtop>
    </div>
    <no-data v-if="!mDataList.length"></no-data>
    <yd-popup v-model="show4" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">时间类型</h3>
        <select-button :data="mDefaultTimeTypeList" :value="mCheckedTimeTypeValue" @select="timeTypeSelect"></select-button>

        <div id="timeDiv">
          <yd-cell-item>
            <span slot="left">开始时间：</span>
            <yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">结束时间：</span>
            <yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
          </yd-cell-item>
        </div>
        <h3 class="text-left" style="padding:.2rem">水库</h3>
        <select-button :data="mDefaultReservoir" :value="mCheckedReservoirValue" :type="multiSelectType" @select="reservoirSelect"></select-button>
        <h3 class="text-left" style="padding:.2rem">数据类型</h3>
        <select-button :data="mDataTypeList" :value="mCheckedDataTypeValue" :type="multiSelectType" @select="dataTypeSelect"></select-button>
        <h3 class="text-left" style="padding:.2rem">测点</h3>
        <select-button :data="mElementList" :value="mCheckedElementValue" :type="multiSelectType"  @select="elementSelect"></select-button>

      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="show4 = false;clickQuery()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script type="text/babel">
  import ToggleBtn from "@/components/common/toggleBtn";
  export default {
    components:{
      ToggleBtn,
    },
    data() {
      return {
        multiSelectType:"check",
        name1:"表",
        name2:"图",
        show4: false,
        defaultFastTime: `${this.$dateFormat(
          new Date(),
          "yyyy-MM-dd"
        )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
        menuId: "",
        myEcharts: null,
        mSelectedOneElement: null,// 选中的某一个测点,用于切换曲线用
        preShowType: '',// 上一次显示的类型,用于再次点击相同按钮时,缩回显示

        datetimeStart: this.$dateFormat(
          new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
          "yyyy-MM-dd hh:mm"
        ), // 开始时间
        datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),// 结束时间


        mDefaultTimeTypeList: [], //从数据库中读取的默认时间类型配置
        // [{name:"实时", type:1},{name:"小时", type:2},{name:"日", type:3},{name:"旬", type:4},
        // {name:"月", type:5},{name:"年", type:6}], // 时间类型


        mDefaultReservoir: [],// 从数据库中读取的默认配置// 数据库默认数据[{reservoirId:1,name:,selected:,dataTypes:[{id:,name:,selected:}],eles:[{id:,name:,dchar:,selected:,}]}]
        mElementList: [], // 测点显示列表,根据reservoir选中情况 变化的信息点,根据水库和数据类型可以变化的{id:xx,name:xxx,dchar:xxx}
        mDataTypeList: [], // 测点类型列表, 根据reservoir选中情况变化


        mCheckedReservoir: [],// 选中的水库
        mCheckedElement: [], // 选中的测点
        mCheckedDataType: [], // 选中的数据类型[{name:"实时", type:1},{name:"小时", type:2}]


        // -----选中value, 用于selectBtn只存value
        mCheckedReservoirValue: [],// 选中的水库,用于selectBtn只存value
        mCheckedElementValue: [], // 选中的测点
        mCheckedDataTypeValue: [], // 选中的数据类型
        mCheckedTimeTypeValue: 1, // 选中的时间类型1实时, 2小时,3天,4旬,5月,6年
        // -----选中value, 用于selectBtn只存value

        showTable: true,// 显示表格true, 显示曲线false

        showSelectedElement: false,// 曲线画面,点击测点时,弹出选择框
        mDataList: [
          // {name:"xxx", time: "2018-08-12 10:10:00", v: 3.3},
        ], // 用于显示在表格中的数据

        mDataMapList: [],// 获取的数据是数组[],元素为{id:xx,data:[]}

      };
    },
    computed: {
      ismenu: function() {
        if (this.$route.name.indexOf("menu")) {
          return true;
        } else {
          return false;
        }
      },
      isChartVisible() {
        let status = !this.showTable;
        return status
      },
      mPreTime: function () {
        let temp = "上一日";
        switch (this.mCheckedTimeTypeValue) {
          case 1: // "实时"
            temp = "上一小时";
            break;
          case 2: // "小时"
            temp = "上一小时";
            break;
          case 3: // "日"
            temp = "上一日";
            break;
          case 4: // "旬"
            temp = "上一旬";
            break;
          case 5: // "月"
            temp = "上一月";
            break;
          case 6: // "年"
            temp = "上一年";
            break;
          default:
            break;
        }

        return temp;
      },
      mNextTime: function () {
        let temp = "下一日";
        switch (this.mCheckedTimeTypeValue) {
          case 1: // "实时"
            temp = "下一小时";
            break;
          case 2: // "小时"
            temp = "下一小时";
            break;
          case 3: // "日"
            temp = "下一日";
            break;
          case 4: // "旬"
            temp = "下一旬";
            break;
          case 5: // "月"
            temp = "下一月";
            break;
          case 6: // "年"
            temp = "下一年";
            break;
          default:
            break;
        }

        return temp;
      },

      titleName: function(){
        let temp = "";
        let index = 0;
        for(let ele of this.mCheckedElement)
        {
          temp += ele.name;
          index ++;
          if(index > 2)
          {
            temp += "...";
            break;
          }
          temp += ",";
        }
        return temp;
      },
      titleTime: function () {
        if (typeof(this.datetimeStart) != "undefined" && typeof(this.datetimeEnd) != "undefined") {
          //开始时间 2010-10-11 10:12:13
          // return this.datetimeStart.slice(0,10) + "---" + this.datetimeEnd.slice(0,10);
          return this.datetimeStart + "---" + this.datetimeEnd;
        }
        return "时间";
      },
      title: function () {
        // 如果当前选中的测点不为null在返回该值
        if (this.mSelectedOneElement != null && typeof(this.mSelectedOneElement) != "undefined") {
          return this.mSelectedOneElement.name;
        }
        // if (this.mCheckedElement.length > 0) {
        //   let temp = this.mCheckedElement[0].name;
        //   return temp;
        // }
        return "";
      },
    },
    methods: {
      goback() {
        this.$router.go(-1);
      },
      switchres(index,show){
        this.showTable = show;
      },
      reset() {

        // 恢复时间类型选择
        this.mCheckedTimeTypeValue = 1;
        for(let tType of this.mDefaultTimeTypeList)
        {
          if (tType.selected === "true") {
            this.mCheckedTimeTypeValue = tType.value;
            break;
          }
        }

        // 恢复流域选择
        this.mCheckedReservoirValue = [];
        for(let reservoir of this.mDefaultReservoir)
        {
          if (reservoir.selected === "true") {
            this.mCheckedReservoirValue.push(reservoir.value);
          }
        }


        // 重置数据类型value
        this.mCheckedDataTypeValue = [];
        for(let dType of this.mDataTypeList)
        {
          if (dType.selected === "true") {
            this.mCheckedDataTypeValue.push(dType.value);
          }
        }

        // 重置测点数据
        this.mCheckedElementValue = [];
        for(let ele of this.mElementList)
        {
          if (ele.selected === "true") {
            this.mCheckedElementValue.push(ele.value);
          }
        }
      },
      reservoirSelect(s) {
        console.log("选中re:", s);
        // s.shift();//移除第一个,因为第一个是观察对象

        let self = this;
        self.mCheckedReservoir = [];
        self.mCheckedReservoirValue = s;
        for(let value of s)
        {
          for(let item1 of self.mDefaultReservoir)
          {
            if(item1.value == value)
            {
              self.mCheckedReservoir.push(item1);
            }
          }
        }

        self.reservoirClickSelected();
      },
      dataTypeSelect(s) {
        this.mCheckedDataTypeValue = s ;
        this.mCheckedDataType = [];

        let self = this;
        for(let value of s)
        {
          for(let item1 of self.mDataTypeList)
          {
            if(item1.value == value)
            {
              self.mCheckedDataType.push(item1);
            }
          }
        }

        this.dataTypeClickSelected();

      },

      timeTypeSelect(s)// s是value类型值
      {
        this.mCheckedTimeTypeValue = s[0];
      },
      elementSelect(s) {

        // console.log("选中element:", s);
        // s.shift();//移除第一个,因为第一个是观察对象
        this.mCheckedElementValue = s;
        this.mCheckedElement = [];
        let self = this;
        for(let value of s)
        {
          for(let item1 of self.mElementList)
          {
            if(item1.id == value)
            {
              self.mCheckedElement.push(item1);
            }
          }
        }

        // console.log("选中的测点：", self.mCheckedElement);
      },
      // 重新设置option
      drawCharts(param) {
        // console.log("drawCharts ",param);
        if (param) {
          let data = param;
          let chartType = 'line';
          let timeType = this.mCheckedTimeTypeValue;

          // if (this.dType === 2) {
          //   chartType = 'line';
          // }
          // else if (this.dType === 5) {
          //   chartType = 'bar';
          // }
          chartType = 'line';

          let optionLine = {
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                animation: false,
                label: {
                  backgroundColor: '#ccc',
                  borderColor: '#aaa',
                  borderWidth: 1,
                  shadowBlur: 0,
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  textStyle: {
                    color: '#222'
                  }
                }
              },
            },
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: data.map(function (item) {
                return item.timeStr;
              }),
              axisLabel: {
                formatter: function (value, idx) {
                  let date = new Date(value);
                  let reValue = idx === 0 ? [date.getMonth() + 1, date.getDate()] : [date.getHours(), date.getMinutes()].join(':');

                  // [{name:"实时", type:1},{name:"小时", type:2},{name:"日", type:3},{name:"旬", type:4},
                  //   {name:"月", type:5},{name:"年", type:6}],
                  switch (timeType.type) {
                    case 3:// 日
                    case 4:// 旬
                      reValue = [date.getMonth() + 1, date.getDate()].join('-');
                      break;
                    case 5:// 月
                      reValue = [date.getFullYear(), date.getMonth() + 1].join('-');
                      break;
                    case 6:// 年
                      reValue = date.getFullYear();
                      break;
                    default:
                      break;
                  }
                  return reValue;
                }
              },
            },
            yAxis: [{type: 'value'}, {}],
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            series: [{
              type: chartType,
              data: data.map(function (item) {
                return item.v;
              }),
              hoverAnimation: true,
              symbolSize: 6,
              itemStyle: {
                normal: {
                  color: '#c23531'
                }
              },
              showSymbol: true
            }]
          };

          // console.log("drawCharts setOption",optionLine);
          this.myEcharts.setOption(optionLine);

        }
        else
        {
          this.myEcharts.clear();
        }

      },

      // 下一日
      nextDay() {
        let diff = this.getTimeDiff();

        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        // 加一天 毫秒
        let ss = begin.getTime() + diff;
        begin.setTime(ss);
        this.datetimeStart = this.$dateFormat(begin, 'yyyy-MM-dd hh:mm');
        // 结束时间
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        end.setTime(end.getTime() + diff);
        this.datetimeEnd = this.$dateFormat(end, 'yyyy-MM-dd hh:mm');
        // console.log(this.datetimeStart + "---" + this.datetimeEnd);
        this.clickQuery();
      },
      // 上一日
      previewDay() {
        let diff = this.getTimeDiff();
        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        // 减一天 毫秒
        let ss = begin.getTime() - diff;
        begin.setTime(ss);
        this.datetimeStart = this.$dateFormat(begin, 'yyyy-MM-dd hh:mm');
        // 结束时间
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        end.setTime(end.getTime() - diff);
        this.datetimeEnd = this.$dateFormat(end, 'yyyy-MM-dd hh:mm');
        // console.log(this.datetimeStart + "---" + this.datetimeEnd);
        this.clickQuery();
      },

      // 获取时间
      getTimeDiff() {
        let diff = 0;
        // [{name:"实时", type:1},{name:"小时", type:2},{name:"日", type:3},{name:"旬", type:4},
        // {name:"月", type:5},{name:"年", type:6}], // 时间类型
        switch (this.mCheckedTimeTypeValue) {
          case 1:
            diff = 60 * 60 * 1000;
            break;
          case 2:// 小时
            diff = 3600 * 1000;
            break;
          case 3://天
            diff = 24 * 3600 * 1000;//86400000;
            break;
          case 4:// 旬
            diff = 10 * 24 * 3600 * 1000;
            break;
          case 5:// 月
            diff = 30 * 24 * 3600 * 1000;
            break;
          case 6:// 年
            diff = 365 * 24 * 3600 * 1000;
            break;
        }
        return diff;
      },




      // 切换测点,用于显示不同的曲线
      switchElement(param) {
        // console.log("点击测点" + param.name);
        if (param != null) {
          this.mSelectedOneElement = param;
          let len = this.mDataMapList.length;
          let find = false;
          if (len >= 1) {
            for (let j = 0; j < len; j++) {
              let subData = this.mDataMapList[j];
              if (subData.id == param.id) {
                find = true;
                this.drawCharts(subData.data);
              }
            }
            if(!find) // 没有找到,则清空
            {
              this.drawCharts();
            }
            // 测试------
            // let subData1 = this.mDataMapList[1].data;
            // this.drawCharts(subData1);
            // 测试------
          }
          else
          {
            this.drawCharts();
          }
        }
        this.showSelectedElement = false;
      },


      // 设置初始化选项
      setDefaultSelected(reservoir) {
        // console.log("初始化数据," , reservoir);
        if (reservoir && reservoir.selected === "true") {
          // let temp = reservoir;// 把reservoir添加上value属性
          // temp.value = reservoir.id;
          // reservoir = temp;
          this.mCheckedReservoirValue.push(reservoir.value);
          this.mCheckedReservoir.push(reservoir);
          this.setDataTypeSelected(reservoir,true);
          this.setElementSelected(reservoir,true);
        }
      },


      // 数据类型选中事件
      dataTypeClickSelected() {
        this.mElementList = [];
        this.mCheckedElement = [];
        this.mCheckedElementValue = [];
        let self = this;
        this.mCheckedReservoir.forEach(function (reservoir) {
          self.setElementSelected(reservoir, true);
        });
      },


      // 水库确定按钮事件
      reservoirClickSelected() {
        this.mDataTypeList = [];
        this.mElementList = [];

        this.mCheckedDataType = [];
        this.mCheckedElement = [];


        this.mCheckedDataTypeValue = [];
        this.mCheckedElementValue = [];

        let self = this;
        this.mCheckedReservoir.forEach(function (reservoir) {
          // 过滤数据类型
          let d3 = reservoir.dataTypes;
          for(let item of d3)
          {
            // item.value = item.id;// 添加上value属性
            let find = false;
            for(let sub of self.mDataTypeList)
            {
              if(sub.value == item.value)
              {
                find = true;
                break;
              }
            }
            if(find)
            {
              continue;
            }
            self.mDataTypeList.push(item);
            if (item.selected == "true") {
              self.mCheckedDataTypeValue.push(item.value);
              self.mCheckedDataType.push(item);
            }
          }


          // 过滤测点
          let d2 = reservoir.eles;
          for(let item of d2)
          {
            if (self.mCheckedDataType && self.mCheckedDataType.length > 0) {
              for (let j = 0; j < self.mCheckedDataType.length; j++) {
                let typeId = self.mCheckedDataType[j].id + "";
                if (typeId == item.dchar) {
                  // item.value = item.dchar;// 添加value属性
                  self.mElementList.push(item);
                  if (item.selected == "true") {
                    self.mCheckedElement.push(item);
                    self.mCheckedElementValue.push(item.value);
                  }
                  break;
                }
              }
            }
          }
        });
      },

      /**
       * 根据选中水库,设置测点
       * @param reservoirSelected 选中水库
       * @param addValue 是否添加到选中value中
       */
      setElementSelected(reservoirSelected, addValue) {
        let d2 = reservoirSelected.eles;

        for(let item of d2)
        {
          if (this.mCheckedDataType && this.mCheckedDataType.length > 0) {
            for (let j = 0; j < this.mCheckedDataType.length; j++) {
              let typeId = this.mCheckedDataType[j].id + "";
              if (typeId == item.dchar) {
                // item.value = item.dchar;// 添加value属性
                this.mElementList.push(item);
                if (item.selected == "true") {
                  this.mCheckedElement.push(item);
                  if(addValue)
                  {
                    this.mCheckedElementValue.push(item.value);
                  }
                }
                break;
              }
            }
          }
        }

        // for (let i = 0; i < d2.length; i++) {
        //   let ele = d2[i];
        // }
      },

      /**
       * 根据选中水库,设置数据类型
       * @param reservoirSelected 选中水库
       */
      setDataTypeSelected(reservoirSelected, addValue) {
        let d3 = reservoirSelected.dataTypes;

        for(let item of d3)
        {
          // item.value = item.id;// 添加上value属性
          let find = false;
          for(let sub of this.mDataTypeList)
          {
            if(sub.value == item.value)
            {
              find = true;
              break;
            }
          }
          if(find)
          {
            continue;
          }
          this.mDataTypeList.push(item);
          if (item.selected == "true") {
            if(addValue)
            {
              this.mCheckedDataTypeValue.push(item.value);
            }
            this.mCheckedDataType.push(item);
          }
        }
      },

      // 清空数据
      clearInit()
      {
        this.mDefaultReservoir = [];
        this.mCheckedReservoir = [];
        this.mCheckedDataType = [];
        this.mCheckedElement = [];
        this.mCheckedReservoirValue = [];
        this.mCheckedDataTypeValue = [];
        this.mCheckedElementValue = [];
      },

      // 查询默认的时间条件
      getDefaultTime(menuId) {
        let queryParam = {
          "menuid": menuId
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/querytime/getTimeQuery.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if (data.result) {
              let self = this;
              self.mDefaultTimeTypeList = [];
              self.mCheckedTimeTypeValue = 1;
              let tTimeArr = data.data;
              tTimeArr.forEach(function (tType) {
                let b = {};
                b.name = tType.name;
                b.type = tType.type;
                b.value = tType.type;
                b.selected = tType.selected;
                self.mDefaultTimeTypeList.push(b);
                if (tType.selected === "true") {
                  self.mCheckedTimeTypeValue = b.value;
                }
              });
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },

      // 获取默认配置 从查询条件配置表中获取水库,然后再从详细条件查询获取水库下的类型和测点等
      getDefaultInit(menuId) {
        let queryParam = {
          "menuid": menuId
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/query/getQuery.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            this.clearInit();// 清空数据
            let self = this;
            if (data.result) {
              for (let i = 0; i < data.data.length; i++) {
                let ele = data.data[i];
                // console.log("缺省配置:",ele);
                let res = {
                  "id": ele.pid,
                  "name": ele.pname,
                  "selected": ele.selected,
                  "eles": [],
                  "dataTypes": [],
                };
                self.mDefaultReservoir.push(res);
              }
              // console.log("得到配置类型1:",this.mDefaultDataType);
              // console.log("得到配置测点2:",this.mDefaultElement);
              // console.log("得到配置水库3:",this.mDefaultReservoir);
              this.getReservoirAllData(self.mDefaultReservoir);
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log(e);
          });
      },


      // 获取所有的水库下配置数据, 详细条件查询获取水库下的类型和测点等
      // [{"id":ele.pid,"name":ele.pname,"selected":ele.selected,"eles":[], "dataTypes":[]}]
      getReservoirAllData(reservoirArr) {
        if (reservoirArr) {
          this.mElementList = [];
          this.mDataTypeList = [];
          for (let i = 0; i < reservoirArr.length; i++) {
            this.getSingleReservoirDetail(reservoirArr[i]);
          }
        }
      },


      // 获取单个水库下的详细配置信息
      // {"id":ele.pid,"name":ele.pname,"selected":ele.selected,"eles":[], "dataTypes":[]}
      getSingleReservoirDetail(reservoir) {
        if (reservoir) {
          let queryParam = {
            "menuid": this.menuId,
            "pid": reservoir.id
          };

          this.$dialog.loading.open();
          this.$axios
            .post(
              "/query/getDetailded.action",
              this.$qs.stringify(queryParam)
            )
            .then(res => {
              let data = res.data;
              let self = this;
              if (data.result) {
                reservoir.eles = [];
                reservoir.dataTypes = [];

                reservoir.value = reservoir.id;// 添加value属性

                let d2 = reservoir.eles;
                let d3 = reservoir.dataTypes;

                for (let i = 0; i < data.data.length; i++) {
                  let ele = data.data[i];
                  let eleArrs = JSON.parse(ele.elecds);
                  for(let item of eleArrs)// 添加value属性
                  {
                    item.value = item.id;
                  }
                  d2.push.apply(d2, eleArrs);// 测点
                  let typeArrs = JSON.parse(ele.eletypes);
                  for(let item of typeArrs)// 添加value属性
                  {
                    item.value = item.id;
                  }
                  d3.push.apply(d3, typeArrs);// 测点类型
                }

                this.setDefaultSelected(reservoir);

              }
              this.$dialog.loading.close();
            })
            .catch(e => {
              this.$dialog.loading.close();
            });
        }
      },
      // 数据结果查询
      clickQuery() {
        if (this.mCheckedElement.length == 0) {
          this.$dialog.alert({mes: "请选择测点", icon: "error"});
          return;
        }

        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        // 时间差 毫秒
        let diff = end - begin;
        // 如果是实时值,不超过2天
        let realDiff = diff / 60000; // 分钟
        let hourDiff = realDiff / 60; // 小时
        let dayDiff = hourDiff / 24; // 天

        //判断日期
        if (begin - end >= 0) {
          this.$dialog.alert({mes: "开始日期要在截止日期之前!", icon: "error"});
          return false;
        }

        let goDetail = false;
        // [{name:"实时", type:1},{name:"小时", type:2},{name:"日", type:3},{name:"旬", type:4},
        //  {name:"月", type:5},{name:"年", type:6}], // 时间类型
        switch (this.mCheckedTimeTypeValue) {
          case 1: // "实时"
            if (hourDiff <= 48) {
              goDetail = true;
            }
            break;
          case 2: // "小时"
            if (dayDiff <= 20) {
              goDetail = true;
            }
            break;
          case 3: // "日"
          case 4: // "旬"
            if (dayDiff <= 100) {
              goDetail = true;
            }
            break;
          case 5: // "月"
            if (dayDiff <= 3650) {
              goDetail = true;
            }
            break;
          case 6: // "年"
            if (dayDiff <= 36500) {
              goDetail = true;
            }
            break;
          default:
            break;
        }
        if (goDetail) {

          let checkedIds = "";
          this.mCheckedElement.forEach(function (ele) {
            checkedIds += ele.id;
            checkedIds += ",";
          });

          // checkedIds = "30001016300131000,30001120200400211";

          // console.log("查询测点ids:" + checkedIds);
          // 跳转到数据显示页面

          let tType = this.mCheckedTimeTypeValue;
          // tType = 3;
          let queryParam = {
            senids: checkedIds,
            type: tType,
            // startTime: "2016-07-09 00:00",
            // endTime: "2016-07-15 00:00",
            startTime: this.datetimeStart,
            endTime: this.datetimeEnd
          };

          console.log("请求参数为:", JSON.stringify(queryParam));
          this.$dialog.loading.open();
          this.$axios
            .post(
              "/appsingle/querySingleDatas.action",
              this.$qs.stringify(queryParam)
            )
            .then(res => {
              let data = res.data;
              let self = this;
              this.mDataMapList = data;
              if (data.length > 0) {
                this.mDataList = [];
                let subData = [];
                let formatJson = this.$qs;
                data.forEach(function (ele) {
                  ele.data.forEach(function (subEle) {
                    subData.push(subEle);
                    // console.log(formatJson.stringify(subEle));
                  });
                });
                this.mDataList = subData;
              }
              else
              {
                this.mDataList = [];
                this.$dialog.toast({mes: '没有数据', timeout: 1000});
              }
              this.$dialog.loading.close();


              // 画第一个测点曲线
              if (data.length > 0) {
                let firstSubData = data[0].data;
                self.drawCharts(firstSubData);
                // 设置选中的mSelectedOneElement
                if (self.mCheckedElement.length > 0) {
                  for(let k = 0; k < self.mCheckedElement.length; k ++)
                  {
                    if(data[0].id == self.mCheckedElement[k].id)
                    {
                      self.mSelectedOneElement = self.mCheckedElement[k];
                      break;
                    }
                  }
                }
              }
              else
              {
                self.drawCharts();
              }
            })
            .catch(e => {
              this.$dialog.loading.close();
              this.$dialog.toast({mes: '没有数据', timeout: 1000});
            });
        }
        else {
          this.$dialog.toast({mes: "为了显示的有效性查询时间间隔为,实时-24小时,小时-7天,天-100天", timeout: 1000});
          // this.$dialog.alert({ mes: "为了显示的有效性查询时间间隔为,实时-24小时,小时-7天,天-100天", icon: "error" });
        }
      },
    },
    activated() {
      // console.log("activated");

      let deviceWidth = document.documentElement.clientWidth;
      let deviceHeight = document.documentElement.clientHeight;
      // 设置chart的高度和宽度
      let chartDom = document.getElementById('echartContainer');
      chartDom.style.width = deviceWidth + 'px';
      chartDom.style.height = (deviceHeight - 300) + 'px';
      this.myEcharts = this.$echarts.init(chartDom);
      let option = {};
      this.myEcharts.setOption(option);
      // console.log("1width, height:", chartDom.style.width, chartDom.style.height);

      this.menuId = this.$route.query.menuId;
      if (this.menuId) {
        // 查询时间条件
        this.getDefaultTime(this.menuId);
        this.getDefaultInit(this.menuId);
      }
    },
    created() {

      // console.log("created");
      let currentDate = new Date();
      currentDate.setHours(0);
      currentDate.setMinutes(0);
      currentDate.setSeconds(0);
      let currentDay = currentDate.getDate();
      // 设置开始时间
      currentDate.setDate(currentDay - 1);
      this.datetimeStart = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');
      // 设置结束时间
      currentDate.setDate(currentDay + 1);
      this.datetimeEnd = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');

    }
  };
</script>
<style scoped>

  .checkDiv {
    font-family: "Segoe UI Semilight", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    border: aqua;
    height: 5rem;
    width: 100%;
    overflow: scroll;
    /*overflow-scrolling: overflow:scroll;*/
    /* margin-top: 60px; */
  }

  .h-title {
    font-size: .3rem;
    color: #6f7acc;
  }

  .h-sub-title {
    font-size: .2rem;
    color: #29cc10;
  }

  #sureItem {
    background-color: red;
    font-size: large;
  }

  #cancelItem {
    font-size: large;
  }

  .yd-cell-item {
    width: 50%;
    display: inline-flex;
    margin-top: 0.2rem;
  }

  #timeDiv .yd-cell-item {
    width: 100%;
    /*display: inline-flex;*/
    /*margin-top: 0.2rem;*/
  }

  #showSelectedPop .yd-cell-item {
    width: 100%;
  }

  #filterDiv .yd-cell-item {
    width: 100%;
    /*display: inline-flex;*/
    /*margin-top: 0.2rem;*/
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  table th,
  table td {
    border: 1px solid #ccc;
    width: 33%;
    padding: 0.1rem 0.15rem;
  }

  table thead tr {
    background-color: #73a8ff;
    color: #fff;
  }

  table tbody tr:nth-of-type(even) {
    background-color: #fff;
  }

</style>



