<template>
    <div>
        <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
            <router-link to="/mation" slot="left" v-if="ismenu">
                <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
            </router-link>
            <div slot="left" @click="goback" v-if="!ismenu">
                <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
            </div>
        </yd-navbar>
        <yd-tab v-model="tab" :prevent-default="false" :item-click="itemClick">
            <yd-tab-panel label="网络拓扑">
                <img class="preview-img" v-gallery:groupName v-for="(item, key) in imageList" :key="key" :src="ipAndPort + item">
            </yd-tab-panel>
            <yd-tab-panel label="运维手册">
                <yd-cell-group>
                    <yd-cell-item arrow @click.native="showPDF(ipAndPort + pdf2,'运维手册')">
                        <yd-icon slot="icon" name="order" size=".42rem"></yd-icon>
                        <span slot="left">PDF</span>
                    </yd-cell-item>
                </yd-cell-group>
            </yd-tab-panel>
            <yd-tab-panel label="运维联系">
                <yd-cell-group>
                    <yd-cell-item arrow @click.native="showPDF(ipAndPort + pdf1,'运维联系')">
                        <yd-icon slot="icon" name="order" size=".42rem"></yd-icon>
                        <span slot="left">PDF</span>
                    </yd-cell-item>
                </yd-cell-group>
            </yd-tab-panel>
        </yd-tab>
    </div>
</template>

<script>
export default {
  data() {
    return {
      tab:0
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    },
    imageList: function() {
      // 获取焦点图路径
      const topoImg = this.$store.state.logindata.appConfig.appTopo;
      return topoImg.split(",");
    },
    pdf1: function() {
      return this.$store.state.logindata.appConfig.appContact;
    },
    pdf2: function() {
      return this.$store.state.logindata.appConfig.appManual;
    },
    ipAndPort: function() {
      // 获取图片服务地址
      return (
        "http://" +
        window.localStorage.getItem("ip") +
        ":" +
        window.localStorage.getItem("port") +
        "/" +
        window.localStorage.getItem("projectName")
      );
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    itemClick(key){
      this.tab = key;
      if(key === 1){
        this.showPDF(this.ipAndPort + this.pdf2,'运维手册')
      }
      if(key === 2){
        this.showPDF(this.ipAndPort + this.pdf1,'运维联系')
      }
    },
    showPDF(src,name){
      cordova.exec(null, null, 'XLDPlugin', 'openDocument', [{
        url:src,
        name:name
      }]);
    }
    // openPdf(src) {
    //   let iframe = document.createElement("iframe");
    //   iframe.src = src;
    //   iframe.className = "pdf-frame";
    //   document.body.appendChild(iframe);
    //   this.showBtn = true;
    // },
    // closePdf() {
    //   this.showBtn = false;
    //   let iframe = document.getElementsByTagName("iframe")[0];
    //   iframe.remove();
    // }
  }
};
</script>

<style>
.preview-img {
  width: 48%;
  margin:3px;
}
.pdf-frame {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  z-index: 101;
}
.close-pdf {
  position: fixed;
  width: 2rem;
  height: 0.7rem;
  line-height: 0.7rem;
  color: #fff;
  left: 50%;
  bottom: 0.5rem;
  transform: translateX(-50%);
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 102;
}
</style>





