<template>
  <div style="margin-top:1.45rem">
    <yd-navbar :title="this.$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff" v-if="isparent">
      <router-link to="/mation" slot="left" v-if="isparent">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <router-link to="/menu/addressList" slot="right" v-if="isparent">
        <yd-icon name="tongxunlu" custom style="color:#fff"></yd-icon>
      </router-link>
    </yd-navbar>
    <div :class="{'main-view':isparent}">
      <div v-if="isparent">
        <yd-slider autoplay="3000">
          <yd-slider-item v-for="(value,index) in imageList" :key="index">
            <img :src=" ipAndPort + value">
          </yd-slider-item>
        </yd-slider>
        <yd-grids-group :rows="2" v-for="(value,index) in menuList" :key="index" :title="value.groupName" class="menu-item">
          <yd-grids-item v-for="(item,list) in value.menus" :key="list" :link="'/menu/'+ item.routeName+'?menuId='+ item.menuId">
            <yd-icon slot="icon" :name="item.routeName" custom></yd-icon>
            <span slot="text">{{ item.menuName }}</span>
          </yd-grids-item>
        </yd-grids-group>
      </div>
      <keep-alive exclude="orderDetail">
        <router-view v-if="!isparent"></router-view>
      </keep-alive>
    </div>
  </div>

</template>

<script type="text/babel">
export default {
  computed: {
    menuList: function() {
      //获取菜单信息
      return this.$store.state.logindata.userConfig.menuConfig;
    },
    imageList: function() {
      // 获取焦点图路径
      const bgImg = this.$store.state.logindata.appConfig.bgImg;
      return bgImg.split(",");
    },
    ipAndPort: function() {
      // 获取图片服务地址
      return (
        "http://" +
        window.localStorage.getItem("ip") +
        ":" +
        window.localStorage.getItem("port") +
        "/" +
        window.localStorage.getItem("projectName")
      );
    },
    isparent: function() {
      if (this.$route.name === "menu") {
        return true;
      } else {
        return false;
      }
    },
    isaddress: function() {
      if (this.$route.name === "addressList") {
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style>
.yd-slider-item img {
  height: 4rem !important;
}
.yd-slider-pagination-item {
  width: 20px !important;
  height: 3px !important;
  border-radius: 1px !important;
  background-color: #fff !important;
}
.yd-slider-pagination-item-active {
  background-color: #2279ff !important;
}
.yd-gridstitle {
  position: relative;
  padding-left: 0.4rem !important;
}
.yd-gridstitle::before {
  content: "";
  position: absolute;
  left: 0.2rem;
  top: 0.4rem;
  height: 0.3rem;
  width: 0.1rem;
}
.menu-item:first-of-type .yd-gridstitle::before {
  background-color: #2279ff;
}
.menu-item:nth-of-type(2) .yd-gridstitle::before {
  background-color: #ff6602;
}
.menu-item:nth-of-type(3) .yd-gridstitle::before {
  background-color: #ffc44f;
}
.menu-item:nth-of-type(4) .yd-gridstitle::before {
  background-color: #e07fdf;
}
.menu-item:nth-of-type(5) .yd-gridstitle::before {
  background-color: #2279ff;
}
.yd-grids-item {
  padding: 0.2rem 0.4rem !important;
}
.yd-grids-icon {
  float: left;
}
.yd-grids-icon i {
  display: block;
  width: 0.6rem;
  height: 0.6rem;
  font-size: 0.4rem !important;
  border-radius: 50%;
  color: #fff;
}
.yd-grids-txt {
  float: left;
  line-height: 0.68rem;
}
select {
  outline: none;
  border: none;
  color: #fff;
  font-family: "Segoe UI Semilight", Helvetica, Arial, sans-serif;
}
.icon-custom-water {
  background-color: #ff8539;
}
.icon-custom-river {
  background-color: #569adc;
}
.icon-custom-waterAndElectricity {
  background-color: #569adc;
}
.icon-custom-singleQuery {
  background-color: #2279ff;
}
.icon-custom-hostWatch {
  background-color: #5b7b93;
}
.icon-custom-composite {
  background-color: #63ca59;
}
.icon-custom-mreport {
  background-color: #ffc450;
}
.icon-custom-curve {
  background-color: #e07fdf;
}
.icon-custom-damMonitor {
  background-color: #2279ff;
}
.icon-custom-unitData {
  background-color: #ff863a;
}
.icon-custom-recondition {
  background-color: #597a92;
}
.icon-custom-orderQuery {
  background-color: #ffc44f;
}
.icon-custom-drinkManagement {
  background-color: #63ca59;
}
.icon-custom-logBook {
  background-color: #ffc44f;
}
.icon-custom-workManagement {
  background-color: #597a92;
}
.icon-custom-lockGate {
  background-color: #569adc;
}
.icon-custom-watcher{
  background-color: #63ca59;
}
.icon-custom-StaInformation{
  background-color: #e07fdf;
}
.icon-custom-adminiManual{
  background-color: #ff863a;
}
</style>



