<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="show4 = true"></i>
    </yd-navbar>

    <div v-if="!showSub">

      <toggle-btn :index="1" :name1="name1" :name2="name2" @switch="switchres"></toggle-btn>
      <!--<yd-search placeholder="曲线名" v-model="searchValue" :on-submit="searchCurve" :on-cancel="searchCancel"></yd-search>-->

      <h3 class="h-title">{{title}}</h3>
      <h3 class="h-sub-title">{{versionTitle}}</h3>

      <div v-if="showTable">
        <table id="data-table">
          <thead>
          <tr>
            <th>{{column0}}</th>
            <th>{{column1}}</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="bean in mDataList">
            <td>{{bean.v0}}</td>
            <td>{{bean.v1}}</td>
          </tr>
          </tbody>
        </table>
      </div>
      <div>
        <div id="echartContainer" width="500px" height="400px"
             :style="{visibility: isChartVisible ? 'visible' : 'hidden'}"></div>
      </div>
    </div>
    <div>
      <yd-backtop></yd-backtop>
    </div>
    <no-data v-if="!mDataList.length"></no-data>
    <yd-popup v-model="show4" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">水库</h3>
        <select-button :data="mReservoirList" :value="mCheckedReservoirValue" :type="multiSelectType"
                       @select="reservoirSelect"></select-button>
        <h3 class="text-left" style="padding:.2rem">类型</h3>
        <select-button :data="mTypeList" :value="mCheckedCurveTypeValue" :type="multiSelectType"
                       @select="curveTypeSelect"></select-button>
        <h3 class="text-left" style="padding:.2rem">曲线</h3>
        <select-button :data="mCurveList" :value="mCheckedCurveValue" @select="curveSelect"></select-button>
        <h3 class="text-left" style="padding:.2rem">版本</h3>
        <select-button :data="mVersionList" :value="mCheckedVersionValue" @select="versionSelect"></select-button>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="show4 = false;clickQuery()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
  import ToggleBtn from "@/components/common/toggleBtn";

  export default {
    components: {
      ToggleBtn,
    },
    data() {
      return {
        multiSelectType: "check",
        name1: "表",
        name2: "图",
        show4: false,
        myEcharts: null,

        searchValue: "",// 搜索的内容
        preShowType: '',// 上一次显示的类型,用于再次点击相同按钮时,缩回显示

        mDefaultCurveAndVersion: [], // 读取的全部曲线和版本信息,曲线数据里面包含版本数据

        mReservoirList: [],// 水库[{pname: "西霞院", pid: "2"}]
        mTypeList: [],// 类型[{typename: "NHQ曲线", type: "11"}]
        mVersionList: [], // 版本
        mCurveList: [], // 曲线


        mCheckedCurve: {}, // 选中的曲线
        mCheckedVersion: {}, // 选中的版本号

        mCheckedReservoirValue: [],//水库
        mCheckedCurveTypeValue: [],//曲线类型
        mCheckedCurveValue: "",//曲线
        mCheckedVersionValue: 0,//版本类型

        mDefaultSelectedCurveId:"",// 默认的选中曲线号  用于reset
        mDefaultSelectedVersionId:"",// 默认的选中曲线版本号 用于reset


        mDataList: [
          // {time: "实时", value: 1},
          // {time: "小时", value: 2},
          // {time: "日", value: 3}
        ], // 曲线数据

        showReservoirSelect: false, // 显示水库选择面板
        showTypeSelect: false,// 显示曲线类型选择面板
        showVersionSelect: false,// 版本选择面板
        showCurveSelect: false,// 曲线选择面板
        showBtn: false,// 重置,确定按钮


        showTable: true,// 显示表格true, 显示曲线false

        // mVersion: {name: "", id:1},
        // mCurve: {name: ""},
        picked: ''
      }
    },
    computed: {
      showSub: function () {
        // console.log(this.$route.name);
        if (this.$route.name === 'menuCurve') {
          return false;
        } else {
          return true;
        }
      },
      ismenu: function () {
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }
      },
      isChartVisible() {
        return !this.showTable
      },

      column0: function () {
        if (typeof(this.mCheckedCurve.v0) != "undefined") {
          let temp = this.mCheckedCurve.v0;
          return temp;
        }
        return "v0值";
      },
      column1: function () {
        if (typeof(this.mCheckedCurve.v1) != "undefined") {
          let temp = this.mCheckedCurve.v1;
          return temp;
        }
        return "v1值";
      },

      title: function () {
        if (typeof(this.mCheckedCurve.name) != "undefined") {
          let temp = this.mCheckedCurve.name;
          return temp;
        }
        return "";
      },
      versionTitle: function () {
        if (typeof(this.mCheckedVersion.name) != "undefined") {
          let temp = this.mCheckedVersion.name;
          return temp;
        }
        return "";
      },
    },

    mounted() {
      // 获取窗口高度和宽度
      this.initEcharts();
      // console.log("width, height:", chartDom.style.width, chartDom.style.height);
    },

    methods: {
      goback() {
        this.$router.go(-1);
      },


      initEcharts() {
        // 获取窗口高度和宽度
        let deviceWidth = document.documentElement.clientWidth;
        let deviceHeight = document.documentElement.clientHeight;
        // 设置chart的高度和宽度
        let chartDom = document.getElementById('echartContainer');
        chartDom.style.width = deviceWidth + 'px';
        chartDom.style.height = (deviceHeight - 300) + 'px';
        this.myEcharts = this.$echarts.init(chartDom);
        let option = {};
        this.myEcharts.setOption(option);
      },
      // 重新设置option
      drawCharts() {
        if (this.mDataList) {
          let data = this.mDataList;
          let chartType = 'line';
          let v0 = this.mCheckedCurve.v0 + ":";
          let v1 = this.mCheckedCurve.v1 + ":";
          let optionLine = {
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                animation: false,
                label: {
                  backgroundColor: '#ccc',
                  borderColor: '#aaa',
                  borderWidth: 1,
                  shadowBlur: 0,
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  textStyle: {
                    color: '#222'
                  }
                }
              },
              formatter: function (params) {
                let res = v0 + params[0].name + '<br/>';
                res += v1 + params[0].value + '<br/>';
                return res;
              }
            },
            xAxis: {
              name: this.mCheckedCurve.v0,
              type: 'category',
              boundaryGap: false,
              data: data.map(function (item) {
                return item.v0;
              }),
              axisLabel: {
                rotate: 45,
                formatter: function (value) {
                  // let date = new Date(value);
                  // return idx === 0 ? [date.getMonth() + 1, date.getDate()] : [date.getHours(), date.getMinutes()].join(':');
                  return value;
                }
              },
            },
            yAxis: {
              type: 'value',
              name: this.mCheckedCurve.v1,
            },
            grid: {
              left: '3%',
              right: '16%',
              bottom: '3%',
              containLabel: true
            },
            series: [{
              type: chartType,
              data: data.map(function (item) {
                return item.v1;
              }),
              hoverAnimation: true,
              symbolSize: 6,
              itemStyle: {
                normal: {
                  color: '#c23531'
                }
              },
              showSymbol: true
            }]
          };
          this.myEcharts.setOption(optionLine);
        }
      },

      // 取消搜索
      searchCancel() {
        let self = this;
        let defaultData = this.mDefaultCurveAndVersion;
        if (defaultData) {
          self.mCurveList = [];
          self.mVersionList = [];
          defaultData.forEach(function (abean) {
            self.mCurveList.push(abean);
            if (abean.curveVersionBeans) {
              abean.curveVersionBeans.forEach(function (ver) {
                self.mVersionList.push(ver);
              });
            }
          });

          if (self.mCurveList.length > 0) {
            self.mCheckedCurve = self.mCurveList[0];
          }
          if (self.mVersionList.length > 0) {
            self.mCheckedVersion = self.mVersionList[0];
          }
          // self.clickQuery();
        }
      },


      // 搜索曲线
      searchCurve(param) {
        // console.log("搜索内容:", param);
        if (param != null && param.length > 0) {
          // 如果搜索得到曲线,则清空之前的内容
          let self = this;
          let defaultData = this.mDefaultCurveAndVersion;
          if (defaultData) {
            self.mCurveList = [];
            self.mVersionList = [];
            defaultData.forEach(function (abean) {
              if (abean.name.indexOf(param) >= 0) {
                self.mCurveList.push(abean);
                if (abean.curveVersionBeans) {
                  abean.curveVersionBeans.forEach(function (ver) {
                    self.mVersionList.push(ver);
                  });
                }
              }
            });

            if (self.mCurveList.length > 0) {
              self.mCheckedCurve = self.mCurveList[0];
            }
            if (self.mVersionList.length > 0) {
              self.mCheckedVersion = self.mVersionList[0];
            }
            self.clickQuery();
          }

          // this.mDataList = [];
        }
      },

      //图标切换
      switchres(index, show) {
        this.showTable = show;
      },

      // 重置按钮
      reset() {

        // 重置流域选择
        this.mCheckedReservoirValue = [];
        for (let reservoir of this.mReservoirList) {
          if (reservoir.paselected === "true")// 选中
          {
            this.mCheckedReservoirValue.push(reservoir.pid);
          }
        }

        // 重置曲线类型选择
        this.mCheckedCurveTypeValue = [];
        for (let cType of this.mTypeList) {
          if (cType.typeselected === "true") // 选中
          {
            this.mCheckedCurveTypeValue.push(cType.type);
          }
        }

        this.mCurveList = [];
        this.mVersionList = [];
        this.mCheckedCurveValue = "";//初始化选中的曲线,此id为字符
        this.mCheckedVersionValue = 0;//初始化选中的曲线版本,此id为数字

        let defaultData = this.mDefaultCurveAndVersion;
        for (let abean of defaultData) {
          this.mCurveList.push(abean);
          if (abean.curveId === this.mDefaultSelectedCurveId) // 选中曲线
          {
            this.mCheckedCurve = abean;
            this.mCheckedCurveValue = abean.value;
            this.mVersionList = abean.curveVersionBeans;
            for (let ver of abean.curveVersionBeans)
            {
              if (ver.id.toString() === this.mDefaultSelectedVersionId) // 选中版本
              {
                this.mCheckedVersion = ver;
                this.mCheckedVersionValue = ver.value;
              }
            }
          }
        }
      },

      reservoirSelect(s) {
        // console.log("选中水库:", s);
        this.mCheckedReservoirValue = s;
        this.filterCurveAndVersion(this.mCheckedReservoirValue, this.mCheckedCurveTypeValue);
      },
      curveTypeSelect(s) {
        // console.log("选中类型:", s);
        this.mCheckedCurveTypeValue = s;
        this.filterCurveAndVersion(this.mCheckedReservoirValue, this.mCheckedCurveTypeValue);
      },
      versionSelect(s)// s是value类型值
      {
        this.mCheckedVersion = {};// 清空版本
        this.mCheckedVersionValue = s[0];// 版本
        this.mDataList = [];
        for (let version of this.mVersionList) {
          if (version.id == s[0])//找到对应的版本
          {
            this.mCheckedVersion = version;
            break;
          }
        }
      },
      curveSelect(s) {
        this.mCheckedCurve = {};// 曲线
        this.mCheckedCurveValue = s[0];


        this.mCheckedVersion = {};// 清空版本
        this.mCheckedVersionValue = 0;
        this.mVersionList = [];

        this.mDataList = [];// 数据
        for (let curve of this.mCurveList) {
          if (curve.curveId == s[0])//找到对应的曲线并设置版本列表
          {
            this.mCheckedCurve = curve;
            this.mVersionList = curve.curveVersionBeans;
            break;
          }
        }
        this.mCheckedVersionValue = 0;
      },
      //
      /**
       * 根据水库和曲线类型筛选曲线和类型
       * @param reservoirs 选中的水库value列表
       * @param types 选中的曲线类型value列表
       * */
      filterCurveAndVersion(reservoirs, types) {
        let filterCurve = [];// 过滤出来的曲线
        // 先设置find属性为false,添加后设置为true,用于筛选过滤
        this.mDefaultCurveAndVersion.forEach(function (abean) {
          abean.find = false;
        });

        let self = this;
        self.mCurveList = [];
        self.mVersionList = [];
        if (reservoirs.length === 0 || types.length === 0)// 都为空则无法选择
        {
          this.mCheckedCurve = {};
          this.mCheckedVersion = {};
          this.mCheckedVersionValue = 0;
          this.mCheckedCurveValue = "";
          return;
        }


        this.mDefaultCurveAndVersion.forEach(function (abean) {
          for (let reservoir of reservoirs) {
            if (reservoir == abean.resesviorId && !abean.find) {
              for (let cType of types) {
                if (cType == abean.type && !abean.find) {
                  abean.find = true;
                  filterCurve.push(abean);
                }
              }
            }
          }
        });
        // console.log("过滤后数据", filterCurve);
        this.mCurveList = filterCurve;
        this.mCheckedCurve = {};
        this.mCheckedVersion = {};
        this.mCheckedVersionValue = 0;
        this.mCheckedCurveValue = "";
      },

      // 数据结果查询
      clickQuery() {
        let goToDetail = false;
        let param = {};
        if (this.mCheckedVersion.name) {
          goToDetail = true;
          param.vName = this.mCheckedVersion.name;
          param.vId = this.mCheckedVersion.id;
          param.name = this.mCheckedCurve.name;
          param.id = this.mCheckedCurve.id;
          param.dimname = this.mCheckedCurve.dimname;
          param.v0 = this.mCheckedCurve.v0;
          param.v1 = this.mCheckedCurve.v1;
          param.v2 = this.mCheckedCurve.v2;
        }
        else {
          if (this.mCheckedCurve.name) {
            goToDetail = true;
            param.name = this.mCheckedCurve.name;
            param.id = this.mCheckedCurve.id;
            param.dimname = this.mCheckedCurve.dimname;
            param.v0 = this.mCheckedCurve.v0;
            param.v1 = this.mCheckedCurve.v1;
            param.v2 = this.mCheckedCurve.v2;
          } else {
            goToDetail = true;
            this.$dialog.alert({mes: "请选择曲线", icon: "error"});
          }
        }

        if (goToDetail) {
          // console.log("查询参数:", param);
          this.getData(param);
        }
      },

      // 获取数据
      getData(param) {
        let curveId = null;
        if (param.vId) {
          curveId = param.vId;
        }
        else {
          curveId = param.id;
        }
        let queryParam = {
          curveId: curveId
        };
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appcurve/getCurveData.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if (data.result) {
              this.mDataList = data.data;
              this.drawCharts();
            }
            else {
              this.$dialog.alert({mes: "数据为空", icon: "error"});
            }
            this.$dialog.loading.close();

          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },

      // 获取默认配置
      getDefaultInit(menuId) {
        let queryParam = {
          "menuid": menuId
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/curve/getDefaultCurves.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if (data.result) {
              let self = this;
              // console.log("曲线:", data.data);
              let defaultData = data.data;
              this.mTypeList = defaultData.curvetype;
              this.mReservoirList = defaultData.reservoir;


              if (defaultData.reservoir) {
                this.mCheckedReservoirValue = [];

                for (let reservoir of defaultData.reservoir) {
                  reservoir.name = reservoir.pname;//补充name
                  reservoir.value = reservoir.pid;//补充value
                  if (reservoir.paselected === "true")// 选中
                  {
                    self.mCheckedReservoirValue.push(reservoir.pid);
                  }
                }
              }

              if (defaultData.curvetype) {
                this.mCheckedCurveTypeValue = [];
                for (let cType of defaultData.curvetype) {
                  cType.value = cType.type;//补充value
                  cType.name = cType.typename;//补充name
                  if (cType.typeselected === "true") // 选中
                  {
                    this.mCheckedCurveTypeValue.push(cType.type);
                  }
                }
              }


              let mDefaultCurve = defaultData.curve;//曲线配置信息
              let mDefaultVersion = defaultData.edition;//版本配置信息

              if (mDefaultCurve.length > 0 || mDefaultVersion > 0) {
                // 根据曲线号和曲线版本查询曲线
                let curveIds = [];
                let versionIds = [];
                let selectedCurveId = "";
                let selectedVersionId = "";
                mDefaultCurve.forEach(function (curve) {
                  curveIds.push(curve.curveid);
                  if (curve.curveselected === "true" && selectedCurveId.length < 1)// 如果被选中
                  {
                    selectedCurveId = curve.curveid;
                  }
                });

                mDefaultVersion.forEach(function (version) {
                  versionIds.push(version.editionid);
                  if (version.editionselected === "true" && selectedVersionId.length < 1)// 如果被选中
                  {
                    selectedVersionId = version.editionid;
                  }
                });
                this.mDefaultSelectedCurveId = selectedCurveId;
                this.mDefaultSelectedVersionId = selectedVersionId;
                this.getTotalData(curveIds, versionIds, selectedCurveId, selectedVersionId);
              }
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log(e);
          });
      },


      // 根据配置获取完整曲线和版本号数据
      /**
       *
       * @param curveIdArr
       * @param versionIdArr
       * @param selectedCurveId 选中的曲线号
       * @param selectedVersionId 选中的版本号
       */
      getTotalData(curveIdArr, versionIdArr, selectedCurveId, selectedVersionId) {

        let curveIds = "";
        let versionIds = "";
        if (curveIdArr.length > 0) {
          curveIds = curveIdArr.join(",");
        }
        else {
          return;
        }
        if (versionIdArr.length > 0) {
          versionIds = versionIdArr.join(",");
        }


        let queryParam = {
          "curveIds": curveIds,
          "versionIds": versionIds,
        };

        // console.log("曲线完整数据参数:",queryParam);

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appcurve/getCurveByDefaultParam.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;

            if (data.result) {
              let self = this;
              self.mCurveList = [];
              self.mVersionList = [];
              self.mCheckedCurveValue = "";//初始化选中的曲线,此id为字符
              self.mCheckedVersionValue = 0;//初始化选中的曲线版本,此id为数字
              self.mDefaultCurveAndVersion = data.curve;

              let defaultData = data.curve;
              // console.log("曲线完整数据:", defaultData);
              for (let abean of defaultData) {
                abean.value = abean.curveId;//补充value属性
                self.mCurveList.push(abean);
                if (abean.curveId == selectedCurveId) // 选中曲线
                {
                  self.mCheckedCurve = abean;
                  self.mCheckedCurveValue = abean.value;
                  if (abean.curveVersionBeans) {
                    abean.curveVersionBeans.forEach(function (ver) {
                      if (ver.id == selectedVersionId) // 选中版本
                      {
                        self.mCheckedVersion = ver;
                        self.mCheckedVersionValue = ver.value;
                      }
                      self.mVersionList.push(ver);
                    });
                  }
                }
                if (abean.curveVersionBeans) {//补充value属性
                  abean.curveVersionBeans.forEach(function (ver) {
                    ver.value = ver.id;
                  });
                }

              }

              // console.log("曲线完整数据2:", defaultData);
              this.clickQuery();
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log(e);
          });
      },

    },
    // Called when a kept-alive component is activated.
    activated() {

      this.initEcharts();
      // 获取所有水库
      // this.getAllReservoir();
      // this.getAllCurveType();
      let menuId = this.$route.query.menuId;
      if (menuId) {
        this.getDefaultInit(menuId);
      }

    }

  }

</script>

<style scoped>

  #controlGroup .yd-grids-txt {
    padding: 0 0.01rem;
    float: none;
    line-height: 0.3rem;
    overflow: inherit;
  }

  .checkDiv {
    font-family: "Segoe UI Semilight", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    border: aqua;
    height: 5rem;
    width: 100%;
    overflow: scroll;
    /*overflow-scrolling: overflow:scroll;*/
    /* margin-top: 60px; */
  }

  #reservoirDiv .yd-cell-item {
    width: 50%;
    display: inline-flex;
    margin-top: 0.2rem;
  }

  #curveTypeDiv .yd-cell-item {
    width: 100%;
  }

  #versionDiv .yd-cell-item {
    width: 100%;
  }

  #curveDiv .yd-cell-item {
    width: 100%;
  }

  #sureItem {
    background-color: red;
    font-size: large;
  }

  #cancelItem {
    font-size: large;
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  table th,
  table td {
    border: 1px solid #ccc;
    width: 33%;
    padding: 0.1rem 0.15rem;
  }

  table thead tr {
    background-color: #73a8ff;
    color: #fff;
  }

  table tbody tr:nth-of-type(even) {
    background-color: #fff;
  }
</style>



