<template>
    <yd-cell-group>
        <yd-cell-item type="radio" v-for="(value,index) in menuList" :key="index">
            <span slot="left">{{ value.menuName }}</span>
            <input slot="right" type="radio" :value="value.routeName+'/'+ value.menuId" v-model="picked" @change="changeRsult(value.menuId,value.routeName)" />
        </yd-cell-item>
    </yd-cell-group>
</template>

<script type="text/babel">
export default {
  data() {
    return {
      picked: "",
    };
  },
  computed:{
    menuList:function(){  //获取菜单数据
      let menu = this.$store.state.logindata.userConfig.menuConfig;
      let menuList = [];
      menu.forEach((v,i) => {
        v.menus.forEach((l,u) => {
          menuList.push(l)
        })
      });
      return menuList;
    }
  },
  created() {
    //初始化页面状态
    this.picked = this.$store.getters.defaultPageName+'/'+this.$store.getters.defaultPageId;
  },
  methods: {
    changeRsult(id,route) {
      this.$post('/home/updHomePage.action',{
        userid:this.$store.state.logindata.userConfig.userInfo.userId,
        username:this.$store.state.logindata.userConfig.userInfo.userName,
        menuid:id
      },res => {
        this.picked = route+'/'+id;
        this.$dialog.toast({
              mes: res.message,
              timeout: 1000,
              icon: "success",
        })
      })
    }
  },
  watch: {
    picked: function() {
      var changeData = this.picked.split('/')
      // 更改默认页面状态
      this.$store.commit("changeHomePage",{name:changeData[0],id:changeData[1]});
    }
  }
};
</script>


