<template>
    <div style="margin-top:1.45rem;margin-bottom:1rem">
        <yd-navbar title="警示信息" :fixed="true" bgcolor="#2279FF" color="#fff">
            <router-link to="/mation" slot="left">
                <i class="icon-custom-mation" style="font-size:.54rem;color:#fff"></i>
            </router-link>
            <i class="icon-custom-shaixuan" slot="right" @click="show4 = true" style="color:#fff;font-size:.4rem"></i>
        </yd-navbar>
        <yd-infinitescroll :callback="loadList" ref="infinitescrollDemo" class="alert-scroll">
            <yd-list theme="4" slot="list">
                <yd-list-item v-for="(value,index) in alertList" :key="index" @click.native="getDetailIndex(index)">
                    <yd-icon slot="img" name="jinggao" class="alert-icon" custom></yd-icon>
                    <span slot="title">{{value.tname}}</span>
                    <yd-list-other slot="other">
                        {{ value.content.substring(0,20)}}...
                    </yd-list-other>
                </yd-list-item>
            </yd-list>
            <span slot="doneTip">没有更多数据了^_^</span>
            <svg slot="loadingTip" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <circle cx="84" cy="50" r="0" fill="#f3b72e">
                    <animate attributeName="r" values="10;0;0;0;0" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                    <animate attributeName="cx" values="84;84;84;84;84" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                </circle>
                <circle cx="35.0893" cy="50" r="10" fill="#E8574E">
                    <animate attributeName="r" values="0;10;10;10;0" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="-0.85s"></animate>
                    <animate attributeName="cx" values="16;16;50;84;84" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="-0.85s"></animate>
                </circle>
                <circle cx="16" cy="50" r="5.61449" fill="#43A976">
                    <animate attributeName="r" values="0;10;10;10;0" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="-0.425s"></animate>
                    <animate attributeName="cx" values="16;16;50;84;84" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="-0.425s"></animate>
                </circle>
                <circle cx="84" cy="50" r="4.38551" fill="#304153">
                    <animate attributeName="r" values="0;10;10;10;0" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                    <animate attributeName="cx" values="16;16;50;84;84" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                </circle>
                <circle cx="69.0892" cy="50" r="10" fill="#f3b72e">
                    <animate attributeName="r" values="0;0;10;10;10" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                    <animate attributeName="cx" values="16;16;16;50;84" keyTimes="0;0.25;0.5;0.75;1" keySplines="0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1;0 0.5 0.5 1" calcMode="spline" dur="1.7s" repeatCount="indefinite" begin="0s"></animate>
                </circle>
            </svg>
        </yd-infinitescroll>
        <yd-backtop></yd-backtop>
        <no-data v-if="!alertList.length"></no-data>
        <yd-popup v-model="show1" position="center" class="alert-popup" width="90%">
            <div class="alert-detail">
                <yd-flexbox>
                    <yd-flexbox-item>
                        <strong>报警类型：</strong> {{ alertList[detailIndex] ? alertList[detailIndex].tname:'' }}</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>
                        <strong>确认人：</strong>{{ alertList[detailIndex] ? alertList[detailIndex].confirmman:'' }}</yd-flexbox-item>
                    <yd-flexbox-item>
                        <strong>报警级别：</strong>{{ alertList[detailIndex] ? alertList[detailIndex].allevel:'' }}级</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>
                        <strong>报警时间：</strong>{{ alertList[detailIndex] ? alertList[detailIndex].time:'' }}</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>
                        <strong>确认时间：</strong>{{ alertList[detailIndex] ? alertList[detailIndex].confirmtime:'' }}</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>
                        <strong>详细内容：</strong>{{ alertList[detailIndex] ? alertList[detailIndex].content:'' }}</yd-flexbox-item>
                </yd-flexbox>
            </div>
        </yd-popup>
        <yd-popup v-model="show4" position="right" class="home-pop">
            <div class="pop-custom">
              <h3 class="text-left" style="padding:.2rem">报警记录时间</h3>
              <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
              <yd-cell-item>
                <span slot="left">开始时间：</span>
                <yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
              </yd-cell-item>
              <yd-cell-item >
                <span slot="left">结束时间：</span>
                <yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
              </yd-cell-item>
              <h3 class="text-left" style="padding:.2rem">报警类型</h3>
              <select-button :data="typeList" :value="selectType" @select="typeSelect"></select-button>
              <h3 class="text-left" style="padding:.2rem">报警等级</h3>
              <select-button :data="LevelList" :value="selectLevel" @select="levelSelect"></select-button>
            </div>
            <div class="pop-button">
              <div @click="reset">重置</div>
              <div @click="show4=false;filterMsg()">确定</div>
            </div>
        </yd-popup>
    </div>

</template>

<script type="text/babel">
export default {
  data() {
    return {
      page: 2,
      detailIndex: 0,
      show1: false,
      defaultFastTime: `${this.$dateFormat(
        new Date(),
        "yyyy-MM-dd"
      )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
      fastTime: [
        {
          name: "今日",
          value: `${this.$dateFormat(
            new Date(),
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        },
        { name: "昨日",
          value:`${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 23:59:59`
        },
        { name: "最近三天",
          value: `${this.$dateFormat(
            new Date().getTime() - 3 * 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        }
      ],
      show4: false,
      datetimeStart: this.$dateFormat(
        new Date().getTime() - 7 * 24 * 60 * 60 * 1000,
        "yyyy-MM-dd hh:mm"
      ),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
      selectType: '0',
      typeList: [],
      selectLevel: '0',
      LevelList:[{
        name:'全部',value:'0',
      },{
        name:'1级',value:1,
      },{
        name:'2级',value:2,
      },{
        name:'3级',value:3,
      }],
      alertList: []
    };
  },
  watch: {
    datetimeStart: function() {
      let interval =
        this.$fixTime(this.datetimeEnd) - this.$fixTime(this.datetimeStart);
      if (interval > 7 * 24 * 60 * 60 * 1000) {
        this.datetimeEnd = this.$dateFormat(
          this.$fixTime(this.datetimeStart) + 7 * 24 * 60 * 60 * 1000,
          "yyyy-MM-dd hh:mm"
        );
      }
    },
    datetimeEnd: function() {
      let interval =
        this.$fixTime(this.datetimeEnd) - this.$fixTime(this.datetimeStart);
      if (interval > 7 * 24 * 60 * 60 * 1000) {
        this.datetimeStart = this.$dateFormat(
          this.$fixTime(this.datetimeEnd) - 7 * 24 * 60 * 60 * 1000,
          "yyyy-MM-dd hh:mm"
        );
      }
    }
  },
  methods: {
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
      this.selectType = '0';
      this.selectLevel = '0';
    },
    timeSelect(s){
      this.defaultFastTime = s[0];
    },
    typeSelect(s) {
      this.selectType = s[0];
    },
    levelSelect(s){
      this.selectLevel = s[0];
    },
    getDetailIndex(index) {
      this.detailIndex = index;
      this.show1 = true;
    },
    // 根据条件筛选数据
    filterMsg() {
      this.$dialog.loading.open();
      let bt = this.defaultFastTime? this.defaultFastTime.split(',')[0]:this.datetimeStart;
      let et = this.defaultFastTime?this.defaultFastTime.split(',')[1]:this.datetimeEnd;
      this.$get(
        "/alarmquery/getAlarmRecordList.action",
        {
          startTime: bt,
          endTime: et,
          alarmType: +this.selectType ? this.selectType : "",
          alarmLevel: +this.selectLevel ? this.selectLevel : "",
          pageSize: 12,
          pageIndex: 1
        },
        data => {
          this.$dialog.loading.close();
          // 初始化加载页
          this.page = 2;
          // 初始化滚动条
          document.querySelector('#app').scrollTop = 0;
          // 初始化滚动加载
          this.$refs.infinitescrollDemo.$emit("ydui.infinitescroll.reInit");
          if (data.data) {
            this.alertList = data.data;
          } else {
            this.alertList = [];
          }
          this.show4 = false;
        }
      );
    },
    loadList() {
      let bt = this.defaultFastTime? this.defaultFastTime.split(',')[0]:this.datetimeStart;
      let et = this.defaultFastTime?this.defaultFastTime.split(',')[1]:this.datetimeEnd;
      this.$axios
        .get("/alarmquery/getAlarmRecordList.action", {
          params: {
            startTime: bt,
            endTime: et,
            alarmType: +this.selectType ? this.selectType : "",
            alarmLevel: +this.selectLevel ? this.selectLevel : "",
            pageSize: 10,
            pageIndex: this.page
          }
        })
        .then(res => {
          if (res.data.data.length) {
            this.alertList = [...this.alertList, ...res.data.data];
            this.page++;
          } else {
            this.$refs.infinitescrollDemo.$emit(
              "ydui.infinitescroll.loadedDone"
            );
          }
          this.$refs.infinitescrollDemo.$emit("ydui.infinitescroll.finishLoad");
        });
    }
  },
  created() {
    //   初始化报警信息（查询全部）
    this.filterMsg();
    //   获取报警类型数据
    this.$get("/alarmquery/queryAlarmType.action", {}, data => {
      let self = this;
      data.forEach(function(v,i){
        self.typeList.push({name:v.name,value:v.type})
      });
      this.typeList.unshift({name:'全部',value:'0'});
    });
  }
};
</script>

<style>
.yd-list-img .alert-icon {
  color: red;
}
.alert-scroll .yd-list-theme4 .yd-list-item .yd-list-img {
  height: auto;
  width: auto;
  padding: 0 0.2rem;
}
.alert-popup .yd-popup-content{
  border-radius: 0.15rem;
}
.alert-detail {
  font-size: 0.28rem;
  background-color: #fff;
  padding: 0.2rem;
  text-align: left;
}
.alert-detail .yd-flexbox.yd-flexbox-horizontal {
  margin: 0.1rem 0;
}
</style>



