<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" @click="showPop = true;datetime = $dateFormat(new Date(),'yyyy-MM-dd hh:mm')" style="color:#fff;font-size:.4rem"></i>
    </yd-navbar>

	<yd-tab :horizontal-scroll="items.length>5?true:false" :callback="getGateMsg">
        <yd-tab-panel v-for="(item,index) in items" :key="index" :label="item.pname"></yd-tab-panel>
    </yd-tab>
    <table class="table">
      <thead>
        <tr>
          <th v-for="(value,index) in tableHead" :key="index">{{value}}</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="(item,index) in tableArr" :key="index">
          <td v-for="(value,key) in item" :key="key" :id="index" v-if="(key !== 'kaidu')">{{ value }}</td>
          <td v-for="(value,key) in item" :key="key" :id="index" v-if="(key == 'kaidu')">
           <span v-for="(values,key) in value">
               {{values}}
           </span>
          </td>
        </tr>
      </tbody>
    </table>

    <yd-popup v-model="showPop" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">查询时间</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">选择时间：</span>
          <yd-datetime type="datetime" v-model="datetime" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
        </yd-cell-item>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;getRealData()">确定</div>
      </div>
    </yd-popup>

  </div>
</template>

<script>

export default {
  data() {
    return {
      defaultFastTime: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss"),
      fastTime: [
        {
          name: "今日",
          value: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")
        },
        { name: "昨日",
          value:this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd hh:mm:ss"
          )
        }
      ],
      showPop: false,
      datetime: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
      ids:"",
      items:[],
      // tableData:[],
      //tbodyData:[],
      tableHead:[],
      tableAll:[],
      tabDataArr:[],
      tableArr:[],
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
    },
    timeSelect(s){
      this.datetime = s[0];
      this.defaultFastTime = s[0];
    },
	// 根据水库获取获取闸门数据
	getGateMsg(label, key){
    var reservoirId = this.items[key].pid;
    this.$post('/lock/getnLockGate.action',{
    	pid:reservoirId
    },res => {
      this.tableArr = []
      this.tabDataArr = []
      this.ids =[]
      let data = res.data
      let typenames = []
      let typenamesIndex = []
      data[0].eleList.forEach(item=>{
        typenamesIndex.push(item.typename)
      })
      for (let i = 0;i < data.length;i++){
        let groupname = data[i].groupname
        let liuLiangId = ""
        let kaiDuId = ""
        let statusId = ""
        let kaiduName = []

        for (let j = 0;j < data[i].eleList.length;j++){
          typenames.push(data[i].eleList[j].typename)
          if (data[i].eleList[j].typename===[...new Set(typenamesIndex)][1]){
            liuLiangId = data[i].eleList[j].elecd
          }
          if (data[i].eleList[j].typename===[...new Set(typenamesIndex)][2]){
            if (!kaiDuId) {
              kaiDuId += `${data[i].eleList[j].elecd}`
            }else {
              kaiDuId += `,${data[i].eleList[j].elecd}`
            }
            kaiduName.push(data[i].eleList[j].title)

          }
          if (data[i].eleList[j].typename===[...new Set(typenamesIndex)][0]){
            statusId += `${data[i].eleList[j].elecd}`
          }
          this.ids += `${data[i].eleList[j].elecd},`

        }

        this.tabDataArr.push({
          groupname:groupname,
          liuLiangId:liuLiangId,
          kaiDuId:kaiDuId,
          statusId:statusId,
          kaiduName:kaiduName
        })
      }
      this.tableHead = [...new Set(typenames)]
      this.tableHead.unshift('孔洞')
      this.getRealData();
    });

	},
	// 获取水库信息
	getReservoir(){
		this.$post('/lock/getPid.action',{},res => {
			this.items = res.data;
			this.getGateMsg("",0);
		})
	},

	// 获取配置中的数据
	getRealData(){
		if(this.ids.length){
			let bt = this.defaultFastTime ? this.defaultFastTime :this.datetime + ":00";
			this.$post('/appquery/queryRealData.action',{
				rIds:this.ids,
				timeStr:bt
			},res => {
        let resData = res.real
        let tabDataArr = this.tabDataArr
        for (let i = 0;i < tabDataArr.length;i++){
          for (let j = 0;j < resData.length;j++){
            if (tabDataArr[i].liuLiangId===resData[j].idStr){
              tabDataArr[i].liuLiang = resData[j].valueStr
            }
            if (tabDataArr[i].statusId===resData[j].idStr){
              tabDataArr[i].status = resData[j].valueStr
            }

          }
          let kaiduIds = tabDataArr[i].kaiDuId.split(",")
          let kaidu = []

          for (let j = 0;j<kaiduIds.length;j++){
            for (let k = 0;k < resData.length;k++){
              if (kaiduIds[j]===resData[k].idStr){
                  kaidu.push(`${tabDataArr[i].kaiduName[j]}：${resData[k].valueStr}`)

              }

            }
          }
          tabDataArr[i].kaidu = kaidu
        }
        let arrs = []
        arrs = JSON.parse(JSON.stringify(tabDataArr))
        for (let i = 0;i<arrs.length;i++){
          delete arrs[i].liuLiangId
          delete arrs[i].kaiDuId
          delete arrs[i].kaiduName
          delete arrs[i].statusId
        }
        this.tableArr = arrs

			})
		}

	}
  },
  // 缓存渲染完成
  created() {
	 this.getReservoir();
  }
};
</script>

<style scoped>

.icon-custom-mation {
  color: #fff;
}
.table {
  border-collapse: collapse;
  min-width: 100%;
  /* background: #fff; */
}
.table th,
.table td {
  border: 1px solid #ccc;
  padding: 0.1rem;
}
.table thead tr {
  background-color: #fff;
  color: #2279ff;
}
.table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.table th > span,
.table td > span {
  display: block;
  white-space: nowrap;
  height: 0.35rem;
}


</style>



