<template>
	<div ref='listbox' style="overflow:auto">
		<order-card v-for="(value,index) in data" :key="index" :id="value.cmd.id" :page="ismenu?'menu':'home'">
			<section>
				<div>
					水库：{{ value.cmd.cmdTar }}
				</div>
				<div>
					命令单号：{{ value.cmd.cmdNo }}
				</div>
			</section>
			<section>
				<div>
					下发人：{{ value.cmd.cmdSu }}
				</div>
				<div>
					下发时间：{{ value.cmd.cmdSt }}
				</div>
			</section>
			<section>
				<div>
					接收人：{{ value.cmd.cmdRecu }}
				</div>
				<div>
					接收时间：{{ value.cmd.cmdRect }}
				</div>
			</section>
			<section slot="des" v-for="(list,item) in value.cmdInfos" :key="item">
				<div>
					操作编号：{{ list.mem2 }}
				</div>
				<div>
					孔洞名称：{{ list.mem0 }}
				</div>
				<div>
					闸门名称：{{ list.mem1 }}
				</div>
				<div>
					开度（米）：{{ list.frKd !== '0'?(list.frKd==='-1'?'全开':list.frKd):'全关' }} - {{ list.toKd !=='0'?(list.toKd==='-1'?'全开':list.toKd):'全关' }}
				</div>
				<div style="width:100%">
					限定操作时间：{{ list.rqSt }} ~ {{ list.rqEt }}
				</div>
			</section>
		</order-card>
		<yd-backtop></yd-backtop>
		<no-data v-if="!data.length"></no-data>
	</div>
</template>

<script>
import OrderCard from "./newOrderCard";
export default {
  props: ["data"],
  components: {
    OrderCard
  },
  computed: {
    ismenu: function() {
      if (/(menu)/.test(this.$route.name)) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted() {
    var appHeight = document.querySelector("#app").clientHeight;
    var heardHeight = document.querySelector("header").clientHeight;
    var tabHeight = document.querySelector(".yd-tab-box").clientHeight;
    this.$refs.listbox.style.height =
      appHeight - heardHeight - tabHeight + "px";
  }
};
</script>





