<template>
	<div ref='cardbox'  style="overflow:auto">
		<opration-card v-for="(value,index) in data" :key="index">
			<section>操作编号
				<span>{{ value.opNo }}</span>
			</section>
			<section>闸门名称
				<span>{{ value.mem1 }}</span>
			</section>
			<section>孔洞名称
				<span>{{ value.mem0 }}</span>
			</section>
			<section>开度（米）
				<span>{{ value.frKd !== '0'?(value.frKd==='-1'?'全开':value.frKd):'全关' }} - {{ value.toKd !=='0'?(value.toKd==='-1'?'全开':value.toKd):'全关' }}</span>
			</section>
			<section>限定操作时间
				<span>{{ value.rqSt }} ~ {{ value.rqEt }}</span>
			</section>
			<section slot="des">实际执行开度
				<span>{{ value.resKd }}</span>
			</section>
			<section slot="des">实际开始时间
				<span>{{ value.resSt }}</span>
			</section>
			<section slot="des">实际完成时间
				<span>{{ value.resEt }}</span>
			</section>
			<section slot="des">操作人
				<span>{{ value.cmdSu }}</span>
			</section>
			<section slot="des">备注
				<span>{{ value.rqTip }}</span>
			</section>
			<div class="time-line-title" slot="des">命令状态</div>
			<yd-timeline style="text-align:left;width:100%" slot="des">
				<yd-timeline-item v-if="value.cmdSu">
					<i class="icon-custom-send" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
					<div class="time-line-content">
						<h2>水调已下发</h2>
						<p>下发人
							<span>{{ value.cmdSu }}</span>
						</p>
						<p>下发时间
							<span>{{ value.cmdSt }}</span>
						</p>
					</div>

				</yd-timeline-item>
				<yd-timeline-item v-if="value.cmdRecu">
					<i class="icon-custom-accept" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
					<div class="time-line-content">
						<h2>水工已接收</h2>
						<p>接收人
							<span>{{ value.cmdRecu }}</span>
						</p>
						<p>接收时间
							<span>{{ value.cmdRect }}</span>
						</p>
					</div>
				</yd-timeline-item>
				<yd-timeline-item v-if="value.cmdOpu">
					<i class="icon-custom-return" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
					<div class="time-line-content">
						<h2>水工已返回</h2>
						<p>返回人
							<span>{{ value.cmdOpu }}</span>
						</p>
						<p>返回时间
							<span>{{ value.cmdOpt }}</span>
						</p>
					</div>
				</yd-timeline-item>
				<yd-timeline-item v-if="value.cmdOpu">
					<i class="icon-custom-ensure" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
					<div class="time-line-content">
						<h2>水调已确认</h2>
					</div>
				</yd-timeline-item>
			</yd-timeline>
		</opration-card>
		<yd-backtop></yd-backtop>
		<no-data v-if="!data.length"></no-data>
	</div>
</template>

<script>
import OprationCard from "./oprationCard";
export default {
	props:['data'],
	components: {
		OprationCard
	},
	mounted() {
		var appHeight = document.querySelector('#app').clientHeight;
		var heardHeight = document.querySelector('header').clientHeight;
		var tabHeight = document.querySelector('.yd-tab-box').clientHeight;
		this.$refs.cardbox.style.height = appHeight - heardHeight - tabHeight + 'px';
	},
};
</script>





