<template>
	<div>
		<yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
			<router-link to="/mation" slot="left" v-if="!ismenu && !showdetail">
				<yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
			</router-link>
			<div slot="left" @click="goback" v-if="ismenu || showdetail">
				<yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
			</div>
			<i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" v-if="!showdetail" @click="show4 = true"></i>
		</yd-navbar>
		<div class="yd-tab-box yd-tab-nav-nomal">
			<ul class="yd-tab-nav" style="color: rgb(255, 94, 83); width: auto;">
				<li :class="{'yd-tab-nav-item':true, 'yd-tab-active':componentId==='OrderList'?true:false}" @click="componentId='OrderList'"><a href="javascript:">命令单</a></li>
				<li :class="{'yd-tab-nav-item':true, 'yd-tab-active':componentId==='OprationList'?true:false}" @click="componentId='OprationList'"><a href="javascript:">操作票</a></li>
			</ul>
		</div>
		<component :is="componentId" :data="orderList"></component>
		<yd-popup v-model="show4" position="right" class="full-pop">
			<div class="pop-custom">
				<h3 class="text-left" style="padding:.2rem">命令下达时间</h3>
				<select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
				<yd-cell-item>
					<span slot="left">开始时间：</span>
					<yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<yd-cell-item>
					<span slot="left">结束时间：</span>
					<yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<h3 class="text-left" style="padding:.2rem">水库</h3>
				<select-button :data="reservoirData" :value="chooseReservoir" @select="reservoirSelect"></select-button>
				<h3 class="text-left" style="padding:.2rem">命令单状态</h3>
				<select-button :data="orderStatu" :value="chooseOrder" @select="orderSelect"></select-button>
				<h3 class="text-left" style="padding:.2rem">闸门操作状态</h3>
				<select-button :data="gateStatu" :value="chooseGate" @select="gateSelect"></select-button>
			</div>
			<div class="pop-button">
				<div @click="reset">重置</div>
				<div @click="show4 = false;queryOrderList()">确定</div>
			</div>
		</yd-popup>
	</div>
</template>

<script>
import OrderList from "@/components/common/orderList";
import OprationList from "@/components/common/oprationList";

export default {
  components: {
    OprationList,
	OrderList,
  },
  data() {
    return {
      defaultFastTime: `${this.$dateFormat(
        new Date(),
        "yyyy-MM-dd"
      )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
      fastTime: [
        {
          name: "今日",
          value: `${this.$dateFormat(
            new Date(),
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        },
        {
          name: "昨日",
          value: `${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 23:59:59`
        },
        {
          name: "最近三天",
          value: `${this.$dateFormat(
            new Date().getTime() - 3 * 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        }
      ],
      chooseOrder: "10",
      orderStatu: [
        {
          name: "全部",
          value: "10"
        },
        {
          name: "临时方案",
          value: "0"
        },
        {
          name: "下发未接收",
          value: "1"
        },
        {
          name: "已接收",
          value: "2"
        }
      ],
      chooseGate: "10",
      gateStatu: [
        {
          name: "全部",
          value: "10"
        },
        {
          name: "等待",
          value: "0"
        },
        {
          name: "下发未接收",
          value: "1"
        },
        {
          name: "已接收",
          value: "8"
        },
        {
          name: "返回",
          value: "9"
        },
        {
          name: "销毁",
          value: "-1"
        }
      ],
      chooseReservoir: "",
      reservoirData: [],
      show4: false,
      datetimeStart: this.$dateFormat(
        new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
        "yyyy-MM-dd hh:mm"
      ),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
      orderList: [],
      oprationList: [],
	  componentId:'OrderList'
    };
  },
  computed: {
    ismenu: function() {
      if (/(menu)/.test(this.$route.name)) {
        return true;
      } else {
        return false;
      }
    },
    showdetail: function() {
      if (/(orderQuery)/.test(this.$route.name)) {
        return false;
      } else {
        return true;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
      this.chooseReservoir = this.reservoirData[0].value;
      this.chooseOrder = "10";
      this.chooseGate = "10";
    },
    timeSelect(s) {
      var tiems = s[0].split(',');
      this.datetimeStart = tiems[0];
      this.datetimeEnd = tiems[1];
      this.defaultFastTime = s[0];
    },
    reservoirSelect(s) {
      this.chooseReservoir = s[0];
    },
    orderSelect(s) {
      this.chooseOrder = s[0];
    },
    gateSelect(s) {
      this.chooseGate = s[0];
    },
    // 切换选项调整scroll到顶部
    fn(label, key) {
      let tabBox = document.querySelector(".yd-tab-panel");
      tabBox.scrollTop = 0;
    },
    queryOrderList() {
      this.oprationList = [];
      let bt = this.defaultFastTime
        ? this.defaultFastTime.split(",")[0]
        : this.datetimeStart + ":00";
      let et = this.defaultFastTime
        ? this.defaultFastTime.split(",")[1]
        : this.datetimeEnd + ":00";
      this.$post(
        "/appgate/getGateInfos.action",
        {
          cmdTars: this.chooseReservoir,
          cmdFlags: this.chooseOrder,
          cmdinfoFlags: this.chooseGate,
          timeBeginStr: bt,
          timeEndStr: et
        },
        data => {
          this.orderList = data.data;
          data.data.forEach(e => {
            if (e.cmdInfos)
              this.oprationList = [...this.oprationList, ...e.cmdInfos];
          });
        }
      );
    }
  },
  mounted() {
    this.queryOrderList();
  },
  created() {
    this.$get(
      "/query/getQuery.action",
      {
        menuid: this.$route.query.menuId
      },
      data => {
        let allname = [];
        data.data.forEach(e => {
          this.reservoirData.push({
            name: e.pname,
            value: e.pfirstchar
          });
          allname.push(e.pfirstchar);
        });
        console.log(allname.join(","))
        this.chooseReservoir = allname.join(",")+',';

        this.reservoirData.unshift({
          name: "全部",
          value: allname.join(",")+','
        });
      }
    );
  }
};
</script>

<style>
.yd-tab-panel {
  background-color: #f5f5f5 !important;
  overflow-y: auto;
}
.time-line-title {
  width: 100%;
  text-align: center;
  padding: 0.1rem 0;
  color: orange;
}
.time-line-content {
  padding-left: 0.2rem;
}
.time-line-content > h2 {
  font-size: 0.3rem;
}
.time-line-content > p {
  margin-top: 0.1rem;
}
.time-line-content > p span {
  float: right;
}
</style>





