<template>
	<div ref='listbox' style="overflow:auto">
		<toggle-btn index="1" @switch="switchres"></toggle-btn>
		<ve-line :data="chartData" :grid="grid"  :extend="extend" :dataZoom="dataZoom" v-show="showChart"></ve-line>
		<div v-show="!showChart">
			<table>
				<thead>
					<tr>
						<th v-for="(value,index) in chartData.columns" :key="index">{{ value }}</th>
					</tr>
				</thead>
			</table>
		</div>

		<div v-show="!showChart" style="height:calc(100% - 62px);overflow:auto">
			<table>
				<tbody>
					<tr v-for="(value,index) in chartData.rows" :key="index">
            <td v-for="(item) in value">{{item}}</td>
            <!--<td>{{value}}}</td>-->
						<!--<td>{{ value['时间']}}</td>-->
						<!--<td :class="{'text-info':value['申请实时流量']}">{{ value['申请实时流量'] }}</td>-->
						<!--<td :class="{'text-info':value['指令实时流量']}">{{ value['指令实时流量'] }}</td>-->
					</tr>
				</tbody>
			</table>
		</div>

		<yd-popup v-model="showPop" position="right" class="full-pop">
			<div class="pop-custom">
				<yd-cell-item>
					<span slot="left">开始时间：</span>
					<yd-datetime type="date" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<yd-cell-item>
					<span slot="left">结束时间：</span>
					<yd-datetime type="date" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<h3 class="text-left" style="padding:.2rem">申请单位</h3>
        <!--<select-buttons :data="unitData" :value="chooseUnit" @select="unitSelect"></select-buttons>-->

        <select-button :data="unitData" :value="chooseUnit" @select="unitSelect"></select-button>
				<!-- <h3 class="text-left" style="padding:.2rem">引水申请指标</h3>
				<select-button :data="targetData" :value="chooseTarget" @select="targetSelect"></select-button> -->
				<h3 class="text-left" style="padding:.2rem">参考指标</h3>
        <select-button :data="refertData" :value="chooseRefer" @select="referSelect"></select-button>
        <!--<select-buttons :data="refertData" :value="chooseRefer" @select="referSelect"></select-buttons>-->
			</div>
			<div class="pop-button">
				<div @click="reset">重置</div>
				<div @click="showPop = false;initChartData()">确定</div>
			</div>
		</yd-popup>
	</div>
</template>

<script>
import ToggleBtn from "@/components/common/toggleBtn";
import VeLine from "v-charts/lib/line";
export default {
	components:{
		ToggleBtn,
		VeLine
	},
	props:{
		'showPop':Boolean
	},
	data(){
		this.grid = {
			top:30,
			left: 10
		};
    this.extend = {
      series (v) {
        v.forEach(i => {
          if(i.name == '申请实时流量' || i.name == '指令实时流量'){
            i.step=true
          }else {
            i.step=false
          }
        })
        return v
      }

    }
		this.dataZoom = [
			{
			type: 'slider',
			start: 0,
			end: 100
			}
		]
		return{
			datetimeStart: this.$dateFormat(
				new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
				"yyyy-MM-dd"
			),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd"),
			unitData:[],
			chooseUnit:'',
			refertData:[

			],
      //新
      getYsReqdata:[],
			chooseRefer:'',
			showChart:true,
			 chartData:{
				columns: ["时间","申请实时流量","指令实时流量"],
				rows: []
			}
		}
	},
	watch: {
		'showPop':function(val){
			if(val == false){
				this.$emit('update:showPop', false)
			}
		}
	},
	methods: {
		switchres(index,show){
			this.showChart = show;
		},
		reset(){
			// this.defaultFastTime = this.fastTime[0].value;
			// this.chooseUnit = this.unitData[0].value;
			// this.chooseRefer = this.refertData[0].value;
			console.log(this.refertData)
		},
		timeSelect(s){
			this.defaultFastTime = s[0];
		},
		unitSelect(s){
			this.chooseUnit = s[0]
		},
		referSelect(s){
			this.chooseRefer = s[0]
		},
		// 获取申请单位
		getUnitData(){
			var units = this.$store.state.unitData;
			var unitsid = this.$store.state.typeData
			units.forEach((v,i)=>{
			  console.log(v)
				this.unitData.push({name:v.name,value:v.name});
				if(v.uselected == 'true'){
					this.chooseUnit = v.name;
				}
			});
		},
		// 获取参考指标
		getDotData(){
			return new Promise((resolve,reject) => {
				this.$post('/query/getQuery.action',{
					menuid:this.$route.query.menuId
				},res => {

					let data = [];
					res.data.forEach((v,i) => {
						data = [...data,...JSON.parse(v.elecds)];
					});
					let goladName = '';
          console.log(data)
          data.forEach((v,i)=>{
						this.refertData.push({name:v.name,value:v.id});
						goladName += v.id+',';
					})
          //注意
          // goladName = goladName.replace(',','');
          console.log(goladName)
          this.refertData.unshift({name:'全部', value:goladName});

          this.chooseRefer =this.refertData[0].value;
					resolve('success');
				});
			});
		},
    orrder(data,name,vs){
      this.chartData.rows.forEach((v,i)=>{
        if(v['时间']==data){
           v[name] = vs
        }

      })

    },
		initChartData(){
			// 初始化图表数据
      let  chartDatacolumns = ["时间","申请实时流量","指令实时流量"]
			this.chartData.columns = chartDatacolumns;
			this.chartData.rows = [];
			var start = new Date(this.datetimeStart).getTime();
			var end = new Date(this.datetimeEnd).getTime();
			while (start<end) {
				this.chartData.rows.push({
					"时间":this.$dateFormat(start,'yyyy-MM-dd'),
					"申请实时流量":0,
					"指令实时流量":0
				});
				start = start + 24 * 60 * 60 * 1000;
			}
			// 请求申请台账数据注入图表数据中chooseType
			this.$post('/appys/getYsReq.action',{
				timeBeginStr:this.datetimeStart+' 00:00:00',
				timeEndStr:this.datetimeEnd+' 00:00:00',
				unit:this.chooseUnit,
				type:this.$store.state.waterType
			},res=>{
			  //新
        this.getYsReqdata = []
				if(res.data.length>0){
					res.data.forEach(item=>{
            //方法-:
            var arr = item.req_time.split(/[- : \/]/);
            var d = new Date(arr[0], arr[1]-1, arr[2], arr[3], arr[4], arr[5]);
            //d.getTime()
            //方法二：
            //var arr = item.req_time.replace(/\-/g, '/')
            //var d =new Date(arr)
            //	d.getTime()

	            if(item.req_flow >= 0){
	              this.getYsReqdata.push({
	                name:'申请实时流量',
	                time:d.getTime(),
	                v:item.req_flow
	              })
	            }
	            if(item.req_order >= 0){
								var date = new Date(item.req_time);
	              this.getYsReqdata.push({
	                name:'指令实时流量',
	                time:d.getTime(),
	                v:item.req_order
	              })
	            }
	        })
				}else{
					var start = new Date(this.datetimeStart).getTime();
					var starts= new Date(this.datetimeStart).getTime();

					var end = new Date(this.datetimeEnd).getTime();

					while (start<end) {

						this.getYsReqdata.push({
							name:'指令实时流量',
							time:start,
							v:0
						})
						this.getYsReqdata.push({
							name:'申请实时流量',
							time:start,
							v:0
						})

						start = start + 24 * 60 * 60 * 1000;
					}
				}

				this.refertData.forEach(item=>{
          if(item.name == '全部'){
          }else {
            this.chartData.columns.push(item.name)
          }

				})
			});

			// 请求配置点号数据
			this.$post('/appquery/querySpanData.action',{
				senids:this.chooseRefer,
				timeBeginStr:this.datetimeStart+' 00:00:00',
				timeEndStr:this.datetimeEnd+' 00:00:00',
				type:2
			},rest => {
			  if(this.getYsReqdata.length>0){
          var arr3 = this.getYsReqdata
        }else {
          var arr3 =[]
        }

        let arr  = this.chooseRefer.split(",")
        if(arr.length > 1){
        }else {
          var spanData = rest[arr[0]];
        }
        for(var index in rest){
          for(var indexs in rest[index]){
            arr3.push(rest[index][indexs])
          }
        }
        arr3.forEach(item=>{

					if(!item.time){

					}else{
						let date = this.$dateFormat(item.time,'yyyy-MM-dd');
						this.orrder(date,item.name,item.v,item.v)
					}

        })
			})
	 }
	},
	created() {
		this.getUnitData();
		this.getDotData().then(() => {
			this.initChartData();
		});
	},
	mounted() {
		var appHeight = document.querySelector("#app").clientHeight;
		var heardHeight = document.querySelector("header").clientHeight;
		var tabHeight = document.querySelector(".yd-tab-box").clientHeight;
		this.$refs.listbox.style.height =
		appHeight - heardHeight - tabHeight + "px";
	},
}
</script>


<style scoped>
table {
  min-width: 100%;
  border-collapse: collapse;
}
table th,
table td {
  border: 1px solid #ccc;
  padding: 0.1rem 0.15rem;
  width: 2rem;
}
table td > div {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
table thead tr {
  background-color: #73a8ff;
  color: #fff;
}
table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
table tbody tr:nth-of-type(odd) {
  background-color: #f7f6fb;
}
.text-info {
	color:orangered;
	font-weight: 600;
}
</style>



