<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="show4 = true"></i>
    </yd-navbar>
    <div class="wrapper" ref="wrapper">
      <Custom-card v-for="(value,index) in bookList" :key="index" :index="index">
        <section>日志类型
          <span>{{ value.type }}</span>
        </section>
        <section>时间
          <span>{{ value.t }}</span>
        </section>
        <section>日志记录人
          <span>{{ value.editor }}</span>
        </section>
        <section>日志内容：{{ value.logInfo }}。
        </section>
      </Custom-card>
      <yd-backtop></yd-backtop>
    </div>
    <no-data v-if="!bookList.length"></no-data>
    <yd-popup v-model="show4" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">日志记录时间</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">开始时间：</span>
          <yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="timePick"></yd-datetime>
        </yd-cell-item>
        <yd-cell-item >
          <span slot="left">结束时间：</span>
          <yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="timePick"></yd-datetime>
        </yd-cell-item>
        <h3 class="text-left" style="padding:.2rem">日志类型</h3>
        <select-button :data="typeList" :value="allType" @select="typeSelect"></select-button>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="show4=false;getBookList()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
import CustomCard from "@/components/common/card";
export default {
  data() {
    return {
      defaultFastTime: `${this.$dateFormat(
        new Date(),
        "yyyy-MM-dd"
      )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
      fastTime: [
        {
          name: "今日",
          value: `${this.$dateFormat(
            new Date(),
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        },
        { name: "昨日",
          value:`${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 23:59:59`
        },
        { name: "最近三天",
          value: `${this.$dateFormat(
            new Date().getTime() - 3 * 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        }
      ],
      allType: "",
      typeList: [],
      show4: false,
      datetimeStart: this.$dateFormat(
        new Date().getTime() - 30 * 24 * 60 * 60 * 1000,
        "yyyy-MM-dd hh:mm"
      ),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
      bookList: []
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
      this.allType = this.typeList[0].value;
    },
    timeSelect(s){
      var tiems = s[0].split(',');
      this.datetimeStart = tiems[0];
      this.datetimeEnd = tiems[1];
      this.defaultFastTime = s[0];
    },
    typeSelect(s) {
      this.allType = s[0];
    },
    timePick(){
      this.defaultFastTime = null;
    },
    getBookList() {
      let bt = this.defaultFastTime? this.defaultFastTime.split(',')[0]:this.datetimeStart + ":00";
      let et = this.defaultFastTime?this.defaultFastTime.split(',')[1]:this.datetimeEnd + ":00";
      this.$get(
        "/appduty/getDutyLog.action",
        {
          timeBeginStr: bt,
          timeEndStr:et,
          logType: this.allType
        },
        data => {
          let bookData = [];
          for (let key in data.data) {
            bookData.push.apply(bookData, data.data[key]);
          }
          this.bookList = bookData;
        }
      );
    }
  },
  components: {
    "Custom-card": CustomCard
  },
  created() {
    this.$axios.get("/appduty/getDutyTimeAndType.action").then(res => {
      let types = [];
      for (let item of res.data.data2) {
        types.push(item.value);
        this.typeList.push({
          name: item.value,
          value: item.value
        });
      }
      this.typeList.unshift({
        name: "全部",
        value: types.join(",")
      });
      this.allType = types.join(",");
      this.getBookList();
    });
  }
};
</script>

<style scoped>
.wrapper {
  overflow: hidden;
}
.log-content {
  padding: 0.1rem 0;
}
</style>



