<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="show4 = true"></i>
    </yd-navbar>


    <div v-if="!showSub">
      <yd-grids-group :rows="2" item-height="40px">
        <yd-grids-item @click.native="previewDay()" style="padding: 0.2rem 0.0001rem 0.2rem 0.0001rem!important">
          <span slot="text" style="float: none">{{mPreTime}}</span>
        </yd-grids-item>

        <yd-grids-item @click.native="nextDay()" style="padding: 0.2rem 0.0001rem 0.2rem 0.0001rem!important">
          <span slot="text" style="float: none">{{mNextTime}}</span>
        </yd-grids-item>
      </yd-grids-group>


      <div>
        <yd-badge shape="square" type="warning">{{titleName}}</yd-badge>
      </div>

      <h3 class="h-sub-title">{{titleTime}}</h3>
      <div class="table-responsive">
        <table v-bind:style="{width: totalWidthData}">
          <thead>
          <tr>
            <th v-bind:style="{width: widthData}">水库</th>
            <th v-bind:style="{width: widthData}">时间</th>
            <th v-bind:style="{width: widthData}" v-for="bean in mGroups">{{bean.grpname}}</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="bean in mDataList">
            <td v-bind:style="{width: widthData}" v-for="sub in bean">{{sub.value}}</td>
            <!--<td v-for = "(item,key) in bean">{{item}}</td>-->
            <!--<td>{{bean.timeStr}}</td>-->
            <!--<td>{{bean.v}}</td>-->
          </tr>
          </tbody>
        </table>
      </div>

      <div>
        <yd-backtop></yd-backtop>
      </div>
      <no-data v-if="!mDataList.length"></no-data>

    </div>

    <yd-popup v-model="show4" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">时间类型</h3>
        <select-button :data="mTimeTypeList" :value="mCheckedTimeTypeValue" @select="timeTypeSelect"></select-button>

        <div id="timeDiv">
          <yd-cell-item>
            <span slot="left">开始时间：</span>
            <yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">结束时间：</span>
            <yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
          </yd-cell-item>
        </div>
        <h3 class="text-left" style="padding:.2rem">水库</h3>
        <select-button :data="mReservoirList" :value="mCheckedReservoirValue" :type="multiSelectType" @select="reservoirSelect"></select-button>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="show4 = false;clickQuery()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        multiSelectType:"check",
        show4: false,
        preShowType: '',// 上一次显示的类型,用于再次点击相同按钮时,缩回显示

        defaultFastTime: `${this.$dateFormat(
          new Date(),
          "yyyy-MM-dd"
        )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,

        datetimeStart: this.$dateFormat(
          new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
          "yyyy-MM-dd hh:mm"
        ), // 开始时间
        datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),// 结束时间


        columnCount: 4, // 列数

        queryArr: {}, // 查询参数{startTime:"", endTime:"", timeType:3,
        // data:[{pid: "1", pname: "小浪底", ids: "30001010200600201,30001016300120601,30001016300120603,30001016300120604"}
        // ,{pid: "1", pname: "小浪底", ids: "30001010200600201,30001016300120601,30001016300120603,30001016300120604"}]
        // }

        mGroups: [],// 从数据库读取的配置信息


        mReservoirList: [],// 水库,从配置中获取,不是获取所有水库{name, id}
        mTimeTypeList: [{name: "小时", type: 2, value:2}, {name: "日", type: 3, value:3}, {name: "旬", type: 4, value:4},
          {name: "月", type: 5, value: 5}, {name: "年", type: 6, value:6}], // 时间类型

        mCheckedReservoir: [],// 选中的水库{id:,name:}

        // -----选中value, 用于selectBtn只存value
        mCheckedReservoirValue: [],// 选中的水库,用于selectBtn只存value
        mCheckedTimeTypeValue: 2, // 选中的时间类型 2小时,3天,4旬,5月,6年
        // -----选中value, 用于selectBtn只存value


        showTimeSelect: false,// 时间选择面板
        showBtn: false,// 重置,确定按钮

        mDataList: [
          // [
          //   {name:"name", value: "小浪底"},
          //   {name:"time", value: "2018-10-09 10:00:00"},
          //   {name:"value", value: "3.3"},
          //   {name:"value", value: "4.4"},
          //   {name:"value", value: "5.5"},
          //   {name:"value", value: "6.6"}
          // ],
          // [
          //   {name:"name", value: "西夏元"},
          //   {name:"time", value: "2018-10-10 10:00:00"},
          //   {name:"value", value: "3.3"},
          //   {name:"value", value: "4.4"},
          //   {name:"value", value: "5.5"},
          //   {name:"value", value: "6.6"}
          // ],
        ], // 用于显示在表格中的数据
      }
    },
    computed: {
      showSub: function () {
        if (this.$route.name === 'menuComposite') {
          return false;
        } else {
          return true;
        }
      },

      widthData: function () {
        // console.log("this.columnCount", this.columnCount);
        return '3rem';
      },

      totalWidthData: function () {
        // console.log("this.columnCount", this.columnCount);
        return 1.8 * this.columnCount + 'rem';
      },
      ismenu: function () {
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }
      },
      mPreTime: function () {
        let temp = "上一日";
        switch (this.mCheckedTimeTypeValue) {
          case 2: // "小时"
            temp = "上一小时";
            break;
          case 3: // "日"
            temp = "上一日";
            break;
          case 4: // "旬"
            temp = "上一旬";
            break;
          case 5: // "月"
            temp = "上一月";
            break;
          case 6: // "年"
            temp = "上一年";
            break;
          default:
            break;
        }

        return temp;
      },
      mNextTime: function () {
        let temp = "下一日";
        switch (this.mCheckedTimeTypeValue) {
          case 2: // "小时"
            temp = "下一小时";
            break;
          case 3: // "日"
            temp = "下一日";
            break;
          case 4: // "旬"
            temp = "下一旬";
            break;
          case 5: // "月"
            temp = "下一月";
            break;
          case 6: // "年"
            temp = "下一年";
            break;
          default:
            break;
        }

        return temp;
      },
      titleName: function(){
        let temp = "";
        let index = 0;
        for(let ele of this.mCheckedReservoir)
        {
          temp += ele.name;
          index ++;
          if(index > 2)
          {
            temp += "...";
            break;
          }
          temp += ",";
        }
        return temp;
      },
      titleTime: function () {
        if (typeof(this.datetimeStart) != "undefined" && typeof(this.datetimeEnd) != "undefined") {
          //开始时间 2010-10-11 10:12:13
          // return this.datetimeStart.slice(0, 10) + "---" + this.datetimeEnd.slice(0, 10);
          return this.datetimeStart + "---" + this.datetimeEnd;
        }
        return "";
      },
    },
    methods: {
      goback() {
        this.$router.go(-1);
      },
// 获取时间
      getTimeDiff() {
        let diff = 0;
        // [{name:"实时", type:1},{name:"小时", type:2},{name:"日", type:3},{name:"旬", type:4},
        // {name:"月", type:5},{name:"年", type:6}], // 时间类型
        switch (this.mCheckedTimeTypeValue) {
          case 1:
            diff = 60 * 60 * 1000;
            break;
          case 2:// 小时
            diff = 3600 * 1000;
            break;
          case 3://天
            diff = 24 * 3600 * 1000;//86400000;
            break;
          case 4:// 旬
            diff = 10 * 24 * 3600 * 1000;
            break;
          case 5:// 月
            diff = 30 * 24 * 3600 * 1000;
            break;
          case 6:// 年
            diff = 365 * 24 * 3600 * 1000;
            break;
        }
        return diff;
      },
      // 下一日
      nextDay() {
        let diff = this.getTimeDiff();
        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        // 加一天 毫秒
        let ss = begin.getTime() + diff;
        begin.setTime(ss);
        this.datetimeStart = this.$dateFormat(begin, 'yyyy-MM-dd hh:mm');
        // 结束时间
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        end.setTime(end.getTime() + diff);
        this.datetimeEnd = this.$dateFormat(end, 'yyyy-MM-dd hh:mm');
        // console.log(this.datetimeStart + "---" + this.datetimeEnd);
        this.clickQuery();
      },
      // 上一日
      previewDay() {
        let diff = this.getTimeDiff();
        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        // 减一天 毫秒
        let ss = begin.getTime() - diff;
        begin.setTime(ss);
        this.datetimeStart = this.$dateFormat(begin, 'yyyy-MM-dd hh:mm');
        // 结束时间
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        end.setTime(end.getTime() - diff);
        this.datetimeEnd = this.$dateFormat(end, 'yyyy-MM-dd hh:mm');
        // console.log(this.datetimeStart + "---" + this.datetimeEnd);
        this.clickQuery();
      },

      // 流域选择
      reservoirSelect(s) {
        // console.log("选中reservoir:", s);

        let self = this;
        self.mCheckedReservoir = [];
        self.mCheckedReservoirValue = s;
        for(let value of s)
        {
          for(let item1 of self.mReservoirList)
          {
            if(item1.value == value)
            {
              self.mCheckedReservoir.push(item1);
            }
          }
        }
      },

      // 时间类型选择
      timeTypeSelect(s)// s是value类型值
      {
        this.mCheckedTimeTypeValue = s[0];
      },

      reset(){
        // 恢复时间类型选择
        this.mCheckedTimeTypeValue = 2;
        for(let tType of this.mTimeTypeList)
        {
          if (tType.selected === "true") {
            this.mCheckedTimeTypeValue = tType.value;
            break;
          }
        }

        // 恢复流域选择
        this.mCheckedReservoirValue = [];
        for(let reservoir of this.mReservoirList)
        {
          if (reservoir.selected === "true") {
            this.mCheckedReservoirValue.push(reservoir.value);
          }
        }
      },

      // 重置按钮
      resetClick() {
        // 时间选择
        if (this.showTimeSelect) {
          let currentDate = new Date();
          currentDate.setHours(0);
          currentDate.setMinutes(0);
          currentDate.setSeconds(0);
          let currentDay = currentDate.getDate();
          // 设置开始时间
          currentDate.setDate(currentDay - 1);
          this.datetimeStart = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');
          // 设置结束时间
          currentDate.setDate(currentDay);
          this.datetimeEnd = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');
        }
      },

      // 查询默认的时间条件
      getDefaultTime(menuId) {
        let queryParam = {
          "menuid": menuId
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/querytime/getTimeQuery.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if (data.result) {
              this.mTimeTypeList = [];
              this.mCheckedTimeTypeValue = 2;//缺省时是小时
              let self = this;
              let tTimeArr = data.data;
              for(let tType of tTimeArr)
              {
                let b = {};
                b.name = tType.name;
                b.type = tType.type;
                b.value = tType.type;
                b.selected = tType.selected;
                self.mTimeTypeList.push(b);
                if (tType.selected === "true") {
                  self.mCheckedTimeTypeValue = b.value;
                }
              }
            }
            // console.log("时间类型:", this.mTimeTypeList);
            // console.log("选中时间类型:", this.mCheckedTimeTypeValue);
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },
      // 获取默认配置 从查询条件配置表中获取水库,然后再从详细条件查询获取水库下的类型和测点等
      getDefaultInit(menuId) {
        let queryParam = {
          "menuid": menuId
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/query/getQuery.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            let self = this;
            self.mCheckedReservoir = [];
            self.mCheckedReservoirValue = [];
            if (data.result) {
              for (let i = 0; i < data.data.length; i++) {
                let ele = data.data[i];
                for (let j = 0; j < self.mReservoirList.length; j++) {
                  let reservoir = self.mReservoirList[j];
                  let id = ele.pid;
                  if (id === reservoir.id) {
                    reservoir.selected = "true";//设置selected属性
                    self.mCheckedReservoir.push(reservoir);
                    self.mCheckedReservoirValue.push(reservoir.value);// 添加check
                  }
                }
              }
              self.clickQuery();
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },

      // 数据结果查询
      clickQuery() {
        // console.log("选中流域:", this.mCheckedReservoir);
        // console.log("时间类型:", this.mTimeType);
        // console.log("开始时间:", this.datetimeStart);
        // console.log("结束时间:", this.datetimeEnd);
        if (!this.mCheckedReservoir) {
          this.$dialog.alert({mes: "请选择测点", icon: "error"});
          return;
        }

        let ptest = {
          type: this.mCheckedTimeTypeValue,
          // startTime: "2018-01-20 08:00",
          // endTime: "2018-01-21 08:00",
          startTime: this.datetimeStart,
          endTime: this.datetimeEnd,
          data: []
        };

        let self = this;
        this.queryArr.data.forEach(function (subEle) {
          // console.log("1查询参数",subEle);
          for (let index in self.mCheckedReservoir) {
            if (self.mCheckedReservoir[index].id == subEle.pid) {
              ptest.data.push(subEle);
              break;
            }
          }
        });

        if (ptest.data.length === 0) {
          return;
        }

        // console.log("4查询参数", JSON.stringify(ptest));

        let begin = new Date(this.datetimeStart.replace(/-/g, "/"));
        let end = new Date(this.datetimeEnd.replace(/-/g, "/"));
        // 时间差 毫秒
        let diff = end - begin;
        // 如果是实时值,不超过2天
        let realDiff = diff / 60000; // 分钟
        let hourDiff = realDiff / 60; // 小时
        let dayDiff = hourDiff / 24; // 天

        // console.log("时间类型:", realDiff, hourDiff, dayDiff, begin, end);

        //判断日期
        if (begin - end >= 0) {
          this.$dialog.alert({mes: "开始日期要在截止日期之前!", icon: "error"});
          return false;
        }

        let goDetail = false;
        switch (this.mCheckedTimeTypeValue) {
          case 2: // 小时
            if (dayDiff <= 20) {
              goDetail = true;
            }
            break;
          case 3:// 日
          case 4: // "旬"
            if (dayDiff <= 100) {
              goDetail = true;
            }
            break;
          case 5:// 月
            if (dayDiff <= 3650) {
              goDetail = true;
            }
            break;
          case 6: // "年"
            if (dayDiff <= 36500) {
              goDetail = true;
            }
            break;
          default:
            break;
        }
        if (goDetail) {

          this.$dialog.loading.open();
          this.$axios
            .post(
              "/appcomposite/querySpanDataByParam.action",
              this.$qs.stringify(ptest, {allowDots: true})
            )
            .then(res => {
              let data = res.data;
              if (data.result) {
                // 得到数据和时间序列
                this.mDataList = data.data;
              }
              else {
                this.mDataList = [];
                // this.$dialog.alert({mes: "数据为空", icon: "error"});
              }
              this.$dialog.loading.close();
            })
            .catch(e => {
              this.$dialog.loading.close();
              console.log("查询异常", e);
            });
        }
        else {
          this.mDataList = [];
          this.$dialog.alert({ mes: "超过最大允许查询时间间隔,小时-20天,天-100天,月-120个月", icon: "error" });
        }
      },
    },
    // Called when a kept-alive component is activated.
    activated() {
      let menuId = this.$route.query.menuId;
      // console.log("1menuId is " + menuId);
      if (menuId) {
        this.getDefaultTime(menuId);
      }


      this.mReservoirList = [];
      this.mCheckedReservoir = [];
      this.mCheckedReservoirValue = [];
      this.queryArr = {};

      let self = this;

      if (this.$store.state.logindata.userConfig != null) {
        let pageConfigs = this.$store.state.logindata.userConfig.pageConfig;
        // 旋转数组
        let arr2 = [];
        pageConfigs.forEach(function (subEle) {
          if (menuId == subEle.menuId) {
            let gData = subEle.groups;
            self.columnCount = 2 + gData.length;
            self.mGroups = gData;

            // 用group中eles确定arr2的行数
            for (let j = 0; j < gData[0].eles.length; j++) {
              // arr2[j] = [];
              let row = {};
              row.data = [];
              arr2[j] = row;

            }

            for (let j = 0; j < gData.length; j++) {
              for (let k = 0; k < gData[j].eles.length; k++) {
                let sonEle = gData[j].eles[k];
                let transData = {};
                transData.name = sonEle.elename;
                transData.id = sonEle.elecd;
                transData.pname = sonEle.pname;

                arr2[k].data[j] = transData;
                arr2[k].pid = sonEle.pid;
                arr2[k].pname = sonEle.pname;
              }
            }


            self.queryArr.data = [];

            for (let j = 0; j < arr2.length; j++) {
              let oriEle = arr2[j];
              let queryParam = {};
              let subIds = [];
              for (let k = 0; k < oriEle.data.length; k++) {
                let sub = oriEle.data[k];
                subIds.push(sub.id);
              }
              queryParam.pid = oriEle.pid;
              queryParam.pname = oriEle.pname;
              queryParam.ids = subIds.join(",");
              self.queryArr.data.push(queryParam);
            }


            // console.log("arr2 is ", arr2);
            // console.log("queryArr is ", self.queryArr);
            // console.log("groups is ", gData);

            subEle.groups.forEach(function (group) {
              let eles = group.eles;
              eles.forEach(function (ele) {

                let find = false;
                let count = 0;
                for (let index in self.mReservoirList) {
                  if (self.mReservoirList[index].id === ele.pid) {
                    find = true;
                    break;
                  }
                }

                if (!find) {
                  let res = {};
                  res.name = ele.pname;
                  res.id = ele.pid;
                  res.value = ele.pid;//添加value字段
                  res.selected = "false";//添加selected字段
                  self.mReservoirList.push(res);
                  // self.mCheckedReservoir.push(res);
                }
              });
            });
          }
        });
        if (menuId) {
          self.getDefaultInit(menuId);
        }
      }
    },
    created() {
      // console.log("created",this.datetimeStart, this.datetimeEnd);
      let currentDate = new Date();
      currentDate.setHours(0);
      currentDate.setMinutes(0);
      currentDate.setSeconds(0);
      let currentDay = currentDate.getDate();
      // 设置开始时间
      currentDate.setDate(currentDay - 1);
      this.datetimeStart = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');
      // 设置结束时间
      currentDate.setDate(currentDay);
      this.datetimeEnd = this.$dateFormat(currentDate, 'yyyy-MM-dd hh:mm');

    }

  }

</script>

