<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <div id="container"></div>
    <img id="satelliteBtn" v-bind:class="[mapClass, isEarth ? earthClass : '']" v-on:click="select($event)"/>

  </div>
</template>

<script>
  export default {
    data() {

      return {
        isEarth: false,
        mapClass: 'mapStyle',
        earthClass: 'earthStyle',
        gMap: '',
        infoWindow: '',
        satelliteLayer: null,
        mStationList: [], // 站点列表
        mElementIdList: [], // 测点列表
        mMarkerList: [],// 添加的地图markers
        mLngLatBounds: [], // 边界区域
        mPolygon:null,
      }

    },
    computed: {
      ismenu: function () {
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }
      },
      // 定时器
      timeOut: {
        set(val) {
          this.$store.state.timeout.riverTimeout = val;
        },
        get() {
          return this.$store.state.timeout.riverTimeout;
        }
      },
    },
    mounted() {
      console.log("mounted");
      const deviceWidth = document.documentElement.clientWidth;
      const deviceHeight = document.documentElement.clientHeight;

      const mapContainer = document.getElementById('container');

      mapContainer.style.width = deviceWidth + 'px';
      mapContainer.style.height = deviceHeight + 'px';
      // mapContainer.style.height = '400px';

      this.loadmap();     //加载地图和相关组件

    },
    created() {
      // console.log("created");
    },
    activated() {
      console.log("activated");
      this.infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, 0)});
      // 获取地图边界
      this.getAreaBounds();
      // 获取站点
      this.getStations();
      // 启动定时刷新
      // 如果已经有了,则要删除
      if (this.timeOut) {
        clearTimeout(this.timeOut);
      }
      // 刷新实时数据
      this.refreshData();
    },
    methods: {
      // 初始化地图
      loadmap: function () {
        this.gMap = new AMap.Map('container', {
          zoom: 9,
          dragEnable: true,
          doubleClickZoom: false,
          scrollWheel: true,
        });

        let bottomMap = this.gMap;
        this.satelliteLayer = new AMap.TileLayer.Satellite({
          map: bottomMap,
        });
        this.satelliteLayer.hide();
      },
      goback: function () {
        this.$router.go(-1);
      },


      // div选中事件
      select: function (event) {
        let targetId = event.currentTarget.id;
        if (targetId === 'satelliteBtn') {
          console.log(targetId);
          this.isEarth = !this.isEarth;
          if (this.isEarth) {
            this.satelliteLayer.show();
          }
          else {
            this.satelliteLayer.hide();
          }
        }
      },


      // 添加站点
      addMarker: function (param) {
        let marker = new AMap.Marker({
          position: [param.x, param.y],
          // position: [118.4669,28.6355], // 测试数据

          offset: new AMap.Pixel(0, 0),
          zIndex: param.level,
          clickable: true,
          map: this.gMap,
          extData:{id:param.idStr,data:[]},// marker的站点信息自定义数据
        });
        // 设置label标签
        marker.setLabel({//label默认蓝框白底左上角显示，样式className为：amap-marker-label
          offset: new AMap.Pixel(-14, 33),//修改label相对于maker的位置
          content: param.name,
        });

        if(param.elements != null && param.elements)
        {
          // 遍历测点
          for (let bean of param.elements) {
            this.mElementIdList.push(bean.id);// 添加测点id
          }
        }

        let _this = this;
        // let win = this.infoWindow;
        // let gdMap = this.gMap;
        //鼠标点击marker弹出自定义的信息窗体
        AMap.event.addListener(marker, 'click', function () {
          if (_this.infoWindow) {
            let p = marker.getPosition();
            let eData = marker.getExtData();
            // console.log("position:", p, eData);
            if(eData.data)
            {
              let contentLabel = "<div>";
              let index = 0;
               for(let b of eData.data)
               {
                 if(index%2 === 0)
                 {
                   contentLabel += "<h4 style='color:black; background-color:yellow;'>";
                   contentLabel += b.name;
                   contentLabel += ": ";
                   contentLabel += b.value;
                   contentLabel += "</h4>";
                 }
                 else
                 {
                   contentLabel += "<h4 style='color:white; background-color:blue;'>";;
                   contentLabel += b.name;
                   contentLabel += ": ";
                   contentLabel += b.value;
                   contentLabel += "</h4>";
                 }
                 index ++;

               }
              contentLabel += "</div>";
              // console.log("contentLabel:" + contentLabel);
              _this.infoWindow.setContent(contentLabel);
            }
            _this.infoWindow.open(_this.gMap, p);
          }
        });

        // 添加到marklist
        this.mMarkerList.push(marker);
      },

      // 获取measlog地图站点
      getStations: function () {
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appgis/getAreaStations.action"
          )
          .then(res => {
            let data = res.data;
            this.mStationList = data;
            let _this = this;
            // 清空markers
            this.mMarkerList = [];
            // 清空element id
            this.mElementIdList = [];
            for (let bean of this.mStationList) {
              // console.log("站点信息:", bean);
              _this.addMarker(bean);
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            // this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log(e);
          });
      },

      // 获取地图边界
      getAreaBounds: function () {
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appgis/getAreaBounds.action"
          )
          .then(res => {
            let data = res.data;
            // 得到边界
            let bounds = data.data;
            if(bounds)
            {
              // console.log("边界点信息:", bounds);
              // 重新初始化
              this.mLngLatBounds = [];
              for (let bean of bounds) {
                let ll = new AMap.LngLat(bean.lng, bean.lat);
                // console.log("边界点信息:", bean);
                this.mLngLatBounds.push(ll);
              }

              let _this = this;

              this.mPolygon = new AMap.Polygon({
                map: _this.gMap,
                path: _this.mLngLatBounds,
                fillOpacity:0,// 填充色透明度
                strokeColor: "#3366FF", //线颜色
                strokeOpacity: 1,       //线透明度
                strokeWeight: 5,        //线宽
                strokeStyle: "solid",   //线样式
                strokeDasharray: [10, 5] //补充线样式
              });

              this.gMap.setBounds(this.mPolygon.getBounds());
            }


            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            this.$dialog.alert({mes: "查询异常", icon: "error"});
            console.log(e);
          });
      },

      // 刷新数据
      refreshData: function () {
        // 这里是一个http的异步请求
        if (this.$route.name === "menuRiver") {
          let _this = this;
          this.timeOut = setTimeout(() => {
            // 获取实时数据
            _this.getRealData();
            // 重新设定timeout
            _this.refreshData();
          }, 50000);
        } else {
          this.timeOut = '';
        }
      },

      // 获取当前时间
      getNowFormatDate: function () {
        let date = new Date();
        let s1 = "-";
        let s2 = ":";

        let seconds = date.getSeconds();
        if(seconds >= 0 && seconds <= 9)
        {
          seconds = "0" + seconds;
        }
        let min = date.getMinutes();
        if(min >= 0 && min <= 9)
        {
          min = "0" + min;
        }
        let hour = date.getHours();
        if(hour >= 0 && hour <= 9)
        {
          hour = "0" + hour;
        }
        let day = date.getDate();
        if (day >= 0 && day <= 9) {
          day = "0" + day;
        }
        let month = date.getMonth() + 1;
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }


        return date.getFullYear() + s1 + month + s1 + day
          + " " + hour + s2 + min
          + s2 + seconds;
      },

      // 获取实时数据
      getRealData: function () {
        // console.log("获取实时数据...");
        // 获取当前时间
        let tt = this.getNowFormatDate();
        // 判断测点
        if (!this.mElementIdList.length > 0) {
          this.$dialog.toast({mes: "没有可查询的数据点", timeout: 1000});
          return;
        }
        let idsStr = this.mElementIdList.join(',');

        let param = {
          ids: idsStr,
          timeStr: tt,
        };

        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appgis/getGisRealData.action",
            this.$qs.stringify(param)
          )
          .then(res => {
            let data = res.data;
            let _this = this;

            for (let marker of this.mMarkerList) {
              let extOr = marker.getExtData();
              // console.log("marker信息:", extOr);
              // 有对应的数据
              for (let bean of data)  {
                if(bean.id === extOr.id)
                {
                  let extData = {};
                  extData.id = bean.id;
                  extData.data = bean.data;
                  marker.setExtData(extData);
                  break;
                }
              }
            }
            this.$dialog.loading.close();
          })
          .catch(e => {
            this.$dialog.loading.close();
            this.$dialog.toast({
              mes: '获取数据失败！',
              timeout: 1000
            });
            console.log(e);
          });
      },
    },
  }
</script>

<style scoped>
  .mapStyle {
    /*background-color: #c247a3;*/
    /*border: #5CC26F 1px solid;*/
    background: url("../../assets/map_map.png") no-repeat;
    background-size: 100%;
    /*border-radius: 0.8rem;*/

    bottom: 1.2rem;
    font-size: 10.5pt;
    right: 0.1rem;
    height: 1.3rem;
    position: fixed;
    width: 1.8rem;
  }

  .earthStyle {
    background: url("../../assets/map_earth.png") no-repeat;
    background-size: 100%;
    /*background-size: cover;*/
    /*-webkit-background-size: cover;*/
    /*-o-background-size: cover;*/
  }

  .bottomLabel {
    right: 0.1rem;
    bottom: 0.1rem;
    position: absolute;
  }

  .marker {
    color: #ff6600;
    padding: 0.1rem 0.1rem;
    border: 0.1rem solid #fff;
    white-space: nowrap;
    font-size: 1rem;
    background-color: #0066ff;
  }

</style>


