<template>
    <article :class="{'active':showDes}" :style="{'animation-delay':index*50+'ms'}" @click="changeStatus">
        <slot name="left">
            <span></span>
        </slot>
        <slot></slot>
        <section class="card-des" v-show="showDes">
            <slot name="des"></slot>
        </section>
    </article>
</template>

<script>
export default {
  props: {
    index: Number
  },
  data() {
    return {
      showDes: false
    };
  },
  methods: {
    changeStatus() {
      this.showDes = !this.showDes;
    }
  },
  activated() {
    this.showDes = false;
  }
};
</script>

<style scoped>
article {
  display: flex;
  flex-wrap: wrap;
  padding: 0.3rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
  animation: fadeInUp 1s;
}

article > section {
  font-size:.26rem;
  width: 100%;
  text-align: left;
  padding: .2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
article > .card-des {
  width: 100%;
  text-align: left;
  display: flex;
  padding: 0;
  flex-wrap: wrap;
}
.card-des > section {
  font-size:.26rem;
  width: 100%;
  text-align: left;
  padding: .2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
@keyframes fadeInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
</style>





