<template>
    <article>
        <slot></slot>
        <section class="card-des" v-show="showDes">
            <slot name="des"></slot>
        </section>
        <div class="expand-tag" @click="changeStatus">{{ showDes?'收起':'详情' }} <i :class="'icon-custom-'+ (showDes?'up':'down')"></i> </div>
    </article>
</template>

<script>
export default {
  data() {
    return {
      showDes: false
    };
  },
  methods: {
    changeStatus() {
      this.showDes = !this.showDes;
    }
  },
  activated() {
    this.showDes = false;
  }
};
</script>

<style scoped>
article {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0.3rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
}

article > section {
  font-size:.26rem;
  width: 100%;
  text-align: left;
  padding: .2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
article > .card-des {
  width: 100%;
  text-align: left;
  display: flex;
  padding: 0;
  flex-wrap: wrap;
}
.card-des > section {
  font-size:.26rem;
  width: 100%;
  text-align: left;
  padding: .2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
.card-des > section.flex-none{
	display: block;
}
.flex-none>div:first-of-type{
	margin-bottom: .2rem;
}
.expand-tag {
    width: 100%;
  color: #697bdd;
  padding: 0.1rem 0;
}
</style>





