<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" @click="showPop = true;datetime = $dateFormat(new Date(),'yyyy-MM-dd hh:mm')" style="color:#fff;font-size:.4rem"></i>
    </yd-navbar>
    <h3 class="water-title">电站信息 {{ defaultFastTime?defaultFastTime:datetime }}</h3>
    <div class="table-box">
      <div class="box-left" >
        <table class="table">
          <thead>
            <tr>
              <th>
                <span>电站</span>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(value,index) in groupList" :key="index">
              <td>
                <span>{{ value.grpname }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="box-right">
        <table class="table">
          <thead>
            <tr>
              <th v-for="(value,index) in tabletdata.name" :key="index">
                <span>{{ value }}</span>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(value,index) in tabletdata.data" :key="index">
              <td v-for="(item,list) in value" :key="list">
                <span :id="item.senid?item.senid:''">{{ item.value }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <yd-popup v-model="showPop" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">查询时间</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">选择时间：</span>
          <yd-datetime type="date" v-model="datetime" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
        </yd-cell-item>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;initData()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defaultFastTime: this.$dateFormat(new Date(), "yyyy-MM-dd"),
      fastTime: [
        {
          name: "今日",
          value: this.$dateFormat(new Date(), "yyyy-MM-dd")
        },
        {
          name: "昨日",
          value: this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )
        }
      ],
      showPop: false,
      datetime: this.$dateFormat(new Date(), "yyyy-MM-dd"),
      tabletdata: {
        name: this.getNameList(),
        data: this.getDataList()
      },
      groupList: this.getGroupList()
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
    },
    timeSelect(s) {
      this.datetime = s[0];
      this.defaultFastTime = s[0];
    },
    // 获取测站名称
    getNameList() {
      let list = [];
      this.getGroupList()[0].eles.forEach(function(v, i) {
        list.push(v.typename);
      });
      return list;
    },
    // 获取点号数据
    getDataList() {
      let list = [];
      let GroupList = this.getGroupList();
      GroupList.forEach(function(v, i) {
        let clist = [];
        if (v.eles.length) {
          v.eles.forEach(function(vv, ii) {
            let time = 12 * 60 * 60 * 1000;
            if (vv.param) {
              let timeData = JSON.parse(vv.param).data;
              for (let key in timeData) {
                if (timeData[key]) {
                  switch (key) {
                    case "hour":
                      time = timeData[key] * 60 * 60 * 1000;
                      break;
                    case "day":
                      time = timeData[key] * 24 * 60 * 60 * 1000 + 12 * 60 * 60 * 1000;
                      break;
                    case "month":
                      time = timeData[key];
                      break;
                    case "year":
                      time = timeData[key] * 12 * 30 * 24 * 60 * 60 * 1000 + 12 * 60 * 60 * 1000;
                      break;
                    default:
                      break;
                  }
                }
              }
            }
            clist.push({
              senid: vv.elecd,
              value: "",
              querytype: vv.querytype,
              time: time
            });
          });
        } else {
          GroupList[0].eles.forEach(function(vv, ii) {
            clist.push({ senid: 0, value: "", querytype: "--", time: 0 });
          });
        }
        list.push(clist);
      });
      return list;
    },
    // 获取测点数据
    getGroupList() {
      let pageConfig = this.$store.getters.pageConfig;
      let menuId = this.$route.query.menuId;
      let list = [];
      pageConfig.forEach(function(v, i) {
        if (v.menuId == menuId) list.push.apply(list, v.groups);
      });
      return list;
    },
    // 获取点号
    getsenids(type) {
      //0:实时，默认查实时,1:小时，2:日，3:月,
      let list = [];
      this.getGroupList().forEach(function(v, i) {
        v.eles.forEach(function(vv, ii) {
          if (vv.querytype == type) list.push(vv.elecd);
        });
      });
      return [...new Set(list)].join(",");
    },
    // 查询所有配置数据
    initData(){
      let _this = this;
      this.tabletdata.data.forEach((v, i) => {
        v.forEach((vv, ii) => {
          if (vv.senid) _this.queryData(vv.querytype, vv.senid, vv.time);
        });
      });
    },
    // 查询一个配置的数据
    queryData(type, id, time) {
      let bt = this.defaultFastTime ? this.defaultFastTime : this.datetime;
      let stime;
      if(time<=0 && time>= -12){
        let month  = new Date(bt).getMonth();
        stime = this.$dateFormat(new Date(bt).setMonth(month+time),'yyyy-MM-dd') ;
      }else{
        bt = this.$fixTime(bt + " 00:00:00") + time;
        stime = this.$dateFormat(bt);
      }
      this.$axios.get("/appquery/queryRealData.action", {
        params: {
          rIds: type === 0 ? id : "",
          hourIds: type === 1 ? id : "",
          dayIds: type === 2 ? id : "",
          monthIds: type === 3 ? id : "",
          timeStr: stime
        }
      }).then(data => {
        data = data.data;
        switch (type) {
          case 0:
          renderData(data.real[0])
            break;
          case 1:
          renderData(data.hour[0])  
            break;
          case 2:
          renderData(data.day[0])  
            break;
          case 3:
          renderData(data.month[0])  
            break;
          default:
            break;
        }
        function renderData(data){
          if(data){
            document.getElementById(id).innerText = data.valueStr;
          }else{
            document.getElementById(id).innerText = '-';
          }
        }
      });
    }
  },
  mounted() {
    this.initData();
  },
};
</script>
<style scoped>
.icon-custom-mation {
  color: #fff;
}
.water-title {
  font-size: 0.3rem;
}
.water-title {
  padding: 0.3rem 0;
}
.water-msg {
  color: red;
  text-align: left;
  padding: 0.3rem 0.1rem;
}
.table-box {
  width: 100%;
  display: flex;
}
.box-left {
  width: 2rem;
}
/*改动点*/
.box-left table {
  width: 100%;

}
.box-right {
  width: calc(100% - 2rem);
  overflow: auto;

}
/*改动点*/
.box-right table {
  min-width: 100%;
  border-left: hidden;
}
.table {
  border-collapse: collapse;
}
.table th,
.table td {
  border: 1px solid #ccc;
  padding: 0.1rem;
}
.table thead tr {
  background-color: #fff;
  color: #2279ff;
}
.table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.table th > span,
.table td > span {
  display: block;
  white-space: nowrap;
  height: 0.35rem;
}

</style>




