<template>
  <div style="padding:.1rem 0;">
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <article v-for="(value,index) in dataList" :key="index" :style="{'animation-delay':index*50+'ms'}">
      <section>操作编号
        <span>{{ value.opNo }}</span>
      </section>
      <section>闸门名称
        <span>{{ value.mem1 }}</span>
      </section>
      <section>孔洞名称
        <span>{{ value.mem0 }}</span>
      </section>
      <section>开度（米）
        <span>{{ value.frKd !== '0'?(value.frKd==='-1'?'全开':value.frKd):'全关' }} - {{ value.toKd !=='0'?(value.toKd==='-1'?'全开':value.toKd):'全关' }}</span>
      </section>
      <section>限定操作时间
        <span>{{ value.rqSt }} ~ {{ value.rqEt }}</span>
      </section>
      <section class="text-green">实际执行开度
        <span>{{ value.resKd }}</span>
      </section>
      <section class="text-green">实际开始时间
        <span>{{ value.resSt }}</span>
      </section>
      <section class="text-green">实际完成时间
        <span>{{ value.resEt }}</span>
      </section>
      <section class="text-green">操作人
        <span>{{ value.cmdSu }}</span>
      </section>
      <section class="text-green">超时
        <span v-show="value.opIsout == 0">不超</span>
        <span v-show="value.opIsout == 1">超时</span>
      </section>
      <section class="text-green">超时原因
        <span>{{ value.resTip }}</span>
      </section>
      <section class="text-green">备注
        <span>{{ value.rqTip }}</span>
      </section>
      <div class="time-line-title">命令状态</div>
      <yd-timeline style="text-align:left;width:100%">
        <yd-timeline-item v-if="value.cmdSu">
          <i class="icon-custom-send" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
          <div class="time-line-content">
            <h2>水调已下发</h2>
            <p>下发人<span>{{ value.cmdSu }}</span></p>
            <p>下发时间<span>{{ value.cmdSt }}</span></p>
          </div>
          
        </yd-timeline-item>
        <yd-timeline-item v-if="value.cmdRecu">
          <i class="icon-custom-accept" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
          <div class="time-line-content">
            <h2>水工已接收</h2>
            <p>接收人<span>{{ value.cmdRecu }}</span></p>
            <p>接收时间<span>{{ value.cmdRect }}</span></p>
          </div>
        </yd-timeline-item>
        <yd-timeline-item v-if="value.cmdOpu">
          <i class="icon-custom-return" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
          <div class="time-line-content">
            <h2>水工已返回</h2>
            <p>返回人<span>{{ value.cmdOpu }}</span></p>
            <p>返回时间<span>{{ value.cmdOpt }}</span></p>
          </div>
        </yd-timeline-item>
        <yd-timeline-item v-if="value.cmdOpu">
          <i class="icon-custom-ensure" slot="icon" style="color:orange;font-size:.5rem;background-color:#fff"></i>
          <div class="time-line-content">
            <h2>水调已确认</h2>
            <!-- <p>确认人<span></span></p>
            <p>确认时间<span></span></p> -->
          </div>
        </yd-timeline-item>
      </yd-timeline>
    </article>
    <yd-backtop></yd-backtop>
    <no-data v-if="!dataList.length"></no-data>
  </div>
</template>

<script>
export default {
  name:'orderDetail',
  data() {
    return {
      dataList: []
    };
  },
  methods:{
    goback() {
      this.$router.go(-1);
    },
  },
  created() {
    if (!this.$route.params.id) {
      return;
    }
    this.$post(
      "/appgate/getGateCmdInfo.action",
      {
        cmdId: this.$route.params.id
      },
      data => {
        console.log(data.data)
        this.dataList = data.data;
      }
    );
  }
};
</script>

<style scoped>
article {
  display: flex;
  flex-wrap: wrap;
  padding: 0.3rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
  animation: fadeInUp 1s;
}
.text-green {
  color:rgb(131, 211, 12);
}
article > section {
  font-size: 0.26rem;
  width: 100%;
  text-align: left;
  padding: 0.2rem 0;
  border-bottom: 0.5px solid #eaeaea;
  display: flex;
  justify-content: space-between;
}
.time-line-title {
  width: 100%;
  text-align: center;
  padding: .1rem 0;
  color:orange;
}
.time-line-content {
  padding-left: .2rem;
}
.time-line-content>h2 {
  font-size:.3rem;
}
.time-line-content>p {
  margin-top: .1rem;
}
.time-line-content>p span {
  float: right;
}
@keyframes fadeInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
</style>






