<template>
  <div class="main-view" style="margin-top:1.45rem">
      <keep-alive>
        <router-view :selected="selectValue"></router-view>
      </keep-alive>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectValue: "1",
      stationList: [],
    };
  },
  computed: {
    isWaterAndEle: function() {
      if (this.$route.name === "homewaterAndElectricity") {
        return true;
      } else {
        return false;
      }
    },
  },
  methods:{

  },
  created() {
    if (this.$route.name === "homewaterAndElectricity") {
      this.$get(
        "/appquery/queryReservoirs.action?basinId=1",
        {},
        data => {
          console.log(data);
          this.stationList = data;
        }
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #fff;
}
</style>



