<template>
	<div>
		<yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
			<router-link to="/mation" slot="left" v-if="ismenu">
				<yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
			</router-link>
			<div slot="left" @click="goback" v-if="!ismenu">
				<yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
			</div>
			<i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="show4 = true"></i>
		</yd-navbar>
		<div>
			<recond-card v-for="(value,index) in data" :key="index">
				<section>编号
					<span>{{ value.pno }}</span>
				</section>
				<section>检修状态
					<span>{{ value.jx_flag | getStatus }}</span>
				</section>
				<section>检修水库
					<span>{{ value.jx_site }}</span>
				</section>
				<section>检修孔洞
					<span>{{ value.jx_hole }}</span>
				</section>
				<section slot="des" class="flex-none">
					<div>工作内容</div>
					<div>{{ value.jx_cont }}</div>
				</section>
				<section slot="des">批准开始时间
					<span>{{ value.jx_pt }}</span>
				</section>
				<section slot="des">批准结束时间
					<span>{{ value.b_ptime }}</span>
				</section>
				<section slot="des">申请人
					<span>{{ value.jx_ru }}</span>
				</section>
				<section slot="des">申请时间
					<span>{{ value.jx_rt }}</span>
				</section>
				<section slot="des">批准人
					<span>{{ value.jx_pu }}</span>
				</section>
				<section slot="des">批准时间
					<span>{{ value.jx_pt }}</span>
				</section>
				<section slot="des">有无延期
					<span>{{ value.jx_delay | getDelay }}</span>
				</section>
				<section slot="des">有无违规
					<span>{{ value.iswg?"有":"没有"}}</span>
				</section>
				<section slot="des">有无票
					<span>{{ value.fileidx?"有":"没有"}}</span>
				</section>
			</recond-card>
			<yd-backtop></yd-backtop>
		</div>
		<no-data v-if="!data.length"></no-data>
		<yd-popup v-model="show4" position="right">
			<div class="pop-custom">
				<h3 class="text-left" style="padding:.2rem">计划时间</h3>
				<select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
				<yd-cell-item>
					<span slot="left">开始时间：</span>
					<yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<yd-cell-item>
					<span slot="left">结束时间：</span>
					<yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<h3 class="text-left" style="padding:.2rem">水库</h3>
				<select-button :data="reservoirData" :value="chooseReservoir" @select="reservoirSelect"></select-button>
				<h3 class="text-left" style="padding:.2rem">孔洞</h3>
				<select-button :data="orderStatu" :value="chooseOrder" @select="orderSelect"></select-button>
			</div>
			<div class="pop-button">
				<div @click="reset">重置</div>
				<div @click="show4 = false;getRecond()">确定</div>
			</div>
		</yd-popup>
	</div>
</template>

<script type="text/babel">
import recondCard from "@/components/common/recondCard";
export default {
  components: {
    recondCard
  },
  data() {
    return {
      data: [],
	  show4: false,
	  defaultFastTime: `${this.$dateFormat(
        new Date(),
        "yyyy-MM-dd"
      )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
      fastTime: [
        {
          name: "今日",
          value: `${this.$dateFormat(
            new Date(),
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        },
        {
          name: "昨日",
          value: `${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 23:59:59`
        },
        {
          name: "最近三天",
          value: `${this.$dateFormat(
            new Date().getTime() - 3 * 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
        }
      ],
      datetimeStart: this.$dateFormat(
        new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
        "yyyy-MM-dd hh:mm"
      ),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
	  reservoirData: [
        {
          name: "小浪底",
          value: "小浪底"
        },
        {
          name: "西霞院",
          value: "西霞院"
        }
	  ],
	  chooseReservoir: "小浪底",
	  orderStatu: [],
	  chooseOrder:"",
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
	reset() {
      this.defaultFastTime = this.fastTime[0].value;
      this.chooseReservoir = this.reservoirData[0].value;
      this.chooseOrder =this.orderStatu[0].value;;
    },
	timeSelect(s) {
      var tiems = s[0].split(',');
      this.datetimeStart = tiems[0];
      this.datetimeEnd = tiems[1];
      this.defaultFastTime = s[0];
    },
	reservoirSelect(s) {
      this.chooseReservoir = s[0];
    },
	orderSelect(s) {
       this.chooseOrder = s[0] ;
    },
	// 获取孔洞信息
	getHole(){
		this.$post('/hole/getHolebyteamd.action',{},res=>{
			var all = [];
			res.data.forEach((v,i) => {
				this.orderStatu.push({name:v.hole,value:v.hole})
				all.push(v.hole)
			});
			this.orderStatu.unshift({name:'全部',value:all.join(',')});
			this.chooseOrder = all.join(',');
			console.log(this.orderStatu)
			this.getRecond();
		})
	},
	// 获取工作票
	getRecond(){
		let bt = this.defaultFastTime
			? this.defaultFastTime.split(",")[0]
			: this.datetimeStart + ":00";
		let et = this.defaultFastTime
			? this.defaultFastTime.split(",")[1]
			: this.datetimeEnd + ":00";
		this.$post('/appys/getJXReal.action',{
			site:this.chooseReservoir,
			holes:this.chooseOrder,
			timeBeginStr:bt,
			timeEndStr:et
		},res => {
			this.data = res.data;
		})
	}
  },
  filters:{
	  getStatus(v){
		  switch (v) {
			case 0:
				  return '待批准';
			break;
		  	case 1:
				  return '执行中';
			break;
			case 9:
				  return '已完成';
			break;
			case -1:
				  return '已作废';
			break;
		  }
	  },
	  getDelay(v){
		  switch (v) {
			case -1:
				 return '不延期' 
			break;
			case 0:
				 return '延期待批准' 
			break;
			case 1:
				 return '延期批准' 
			break;
		  }
	  }
  },
  created() {
	  this.getHole();
  }
};
</script>



