<template>
	<div ref='listbox' style="overflow:auto">
		<ul class="list" v-for="(value,index) in applyData" :key="index">
			<li class="list-item">
				<span>文号</span>
				<span>{{ value.req_no }}</span>
			</li>
			<li class="list-item">
				<span>申请流量</span>
				<span>{{ value.req_flow }} m³/s</span>
			</li>
			<li class="list-item">
				<span>指令流量</span>
				<span>{{ value.req_order }} m³/s</span>
			</li>
			<li class="list-item">
				<span>申请单位</span>
				<span>{{ value.opuser }}</span>
			</li>
			<li class="list-item">
				<span>申请类型</span>
				<span>{{ typeData[value.req_type].name }}</span>
			</li>
			<li class="list-item">
				<span>申请时间</span>
				<span>{{ value.req_time }}</span>
			</li>
		</ul>
		<yd-backtop></yd-backtop>
		<no-data v-if="!applyData.length"></no-data>
		<yd-popup v-model="showPop" position="right" class="full-pop">
			<div class="pop-custom">
				<h3 class="text-left" style="padding:.2rem">申请时间</h3>
				<select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
				<yd-cell-item>
					<span slot="left">开始时间：</span>
					<yd-datetime type="datetime" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<yd-cell-item>
					<span slot="left">结束时间：</span>
					<yd-datetime type="datetime" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
				</yd-cell-item>
				<h3 class="text-left" style="padding:.2rem">申请单位</h3>
				<select-button :data="unitData" :value="chooseUnit" @select="unitSelect"></select-button>
				<h3 class="text-left" style="padding:.2rem">申请类型</h3>
				<select-button :data="typeData" :value="chooseType" @select="typeSelect"></select-button>
			</div>
			<div class="pop-button">
				<div @click="reset">重置</div>
				<div @click="showPop = false;getWaterApply()">确定</div>
			</div>
		</yd-popup>
	</div>
</template>

<script>
export default {
	props:{
		'showPop':Boolean
	},
	data(){
		return {
			applyData:[],
			defaultFastTime: `${this.$dateFormat(
				new Date(),
				"yyyy-MM-dd"
			)} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`,
      		fastTime: [
				{
				name: "今日",
				value: `${this.$dateFormat(
					new Date(),
					"yyyy-MM-dd"
				)} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
				},
				{
				name: "昨日",
				value: `${this.$dateFormat(
					new Date().getTime() - 24 * 60 * 60 * 1000,
					"yyyy-MM-dd"
				)} 00:00:00,${this.$dateFormat(
					new Date().getTime() - 24 * 60 * 60 * 1000,
					"yyyy-MM-dd"
				)} 23:59:59`
				},
				{
				name: "最近三天",
				value: `${this.$dateFormat(
					new Date().getTime() - 3 * 24 * 60 * 60 * 1000,
					"yyyy-MM-dd"
				)} 00:00:00,${this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm:ss")}`
				}
			],
			datetimeStart: this.$dateFormat(
				new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
				"yyyy-MM-dd hh:mm"
			),
      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd hh:mm"),
			unitData:[],
			chooseUnit:'',
			typeData:[],
			chooseType:''
		}
	},
	watch: {
		'showPop':function(val){
			if(val == false){
				this.$emit('update:showPop', false)
			}
		}
	},
	
	methods:{
		reset(){
			this.defaultFastTime = this.fastTime[0].value;
			this.chooseUnit = this.unitData[0].value;
			this.chooseType = this.typeData[0].value;
      this.$store.commit('changeWaterType',this.chooseType)
		},
		timeSelect(s){
      var tiems = s[0].split(',');
      this.datetimeStart = tiems[0];
      this.datetimeEnd = tiems[1];
			this.defaultFastTime = s[0];
		},
		unitSelect(s){
			this.chooseUnit = s[0]
		},
		typeSelect(s){
			this.chooseType = s[0];
      this.$store.commit('changeWaterType',this.chooseType)
		},
		// 获取申请单位和申请类型
		getTypeAdnUnit(){
			this.$post('/typeunit/getTypeUnitByTeamd.action',{},res=>{
				var units = JSON.parse(res.data[0].units);
				this.$store.commit('initUnit',units);

				units.forEach((v,i)=>{
					this.unitData.push({name:v.name,value:v.name});
					if(v.uselected == 'true'){
						this.chooseUnit = v.name;
					}
				});
				var types = JSON.parse(res.data[0].types);
				types.forEach((v,i)=>{
          this.typeData.push({name:v.name,value:v.id});
					if(v.tselected == 'true'){

						this.chooseType = String(v.id) ;
						this.$store.commit('changeWaterType',this.chooseType)
					}
				});
				this.getWaterApply();
			})
		},
		// 获取台账信息
		getWaterApply(){
			let bt = this.defaultFastTime
			? this.defaultFastTime.split(",")[0]
			: this.datetimeStart + ":00";
			let et = this.defaultFastTime
				? this.defaultFastTime.split(",")[1]
				: this.datetimeEnd + ":00";
			this.$post('/appys/getYsReq.action',{
				timeBeginStr:bt,
				timeEndStr:et,
				unit:this.chooseUnit,
				type:this.chooseType
			},res=>{
				this.applyData = res.data;
			})
		}
	},
	created(){
		this.getTypeAdnUnit();
	},
	mounted() {
		var appHeight = document.querySelector("#app").clientHeight;
		var heardHeight = document.querySelector("header").clientHeight;
		var tabHeight = document.querySelector(".yd-tab-box").clientHeight;
		this.$refs.listbox.style.height =
		appHeight - heardHeight - tabHeight + "px";
	},
}
</script>

<style scoped>
.list {
	padding: 0;
	list-style: none;
	background-color: #fff;
	margin-bottom: .2rem;
	border-top:1px solid #dfdfdf;
}
.list .list-item {
	display: flex;
	justify-content: space-between;
	padding: .15rem .2rem;
	border-bottom: 1px solid #dfdfdf;
}
</style>




