<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" @click="showPop = true;datetime = $dateFormat(new Date(),'yyyy-MM-dd hh:mm')" style="color:#fff;font-size:.4rem"></i>
    </yd-navbar>

    <yd-tab :prevent-default="true" :callback="itemClick">
      <yd-tab-panel v-for="(value,index) in groupList"   :label="value.grpname" >

        <toggle-btn :index="index" @switch="switchres"></toggle-btn>

        <div class="card-table" v-show="!switchData[index]">
          <table>
            <thead>
              <tr>
                <th>名称</th>
                <th>时间</th>
                <th>值</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item) in tableDates" :key="list">
                <td>
                  <div style="width:3rem">{{ item.name }}</div>
                </td>
                <td>
                  <div style="width:2.5rem">{{ item.timest  }}</div>
                </td>
                <td>{{ item.v }}</td>
              </tr>
            </tbody>
          </table>
        </div>

        <ve-line :data="chart[index]?chart[index]:chartData" :grid="grid"  :dataZoom="dataZoom" v-if="switchData[index]" :settings="chartSettings"></ve-line>
      </yd-tab-panel>
    </yd-tab>

    <yd-popup v-model="showPop" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">查询时间</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">开始时间：</span>
          <yd-datetime type="date" v-model="datetimeStart" :end-date="datetimeEnd" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">结束时间：</span>
          <yd-datetime type="date" v-model="datetimeEnd" :start-date="datetimeStart" slot="right" @click.native="defaultFastTime = null;"></yd-datetime>
        </yd-cell-item>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;realuData()">确定</div>
      </div>
    </yd-popup>
  </div>

</template>

<script>
import ToggleBtn from "@/components/common/toggleBtn";
import VeLine from "v-charts/lib/line";
export default {
  components: {
    ToggleBtn,
    VeLine
  },
  data() {
    this.grid = {
      top: 30,
      left: 10
    };
    this.axisLine={       //y轴
          "show":true

        },
    this.label = ''
    this.dataZoom = [
      {
        type: 'slider',
        start: 0,
        end: 100
      }
    ]
    this.chartSettings = {
      xAxisType: 'time',
      yAxisType: ['normal'],
      }
    this.tableDates = []
    return {
      datetimeStart: this.$dateFormat(
        new Date().getTime() - 10 * 24 * 60 * 60 * 1000,
        "yyyy-MM-dd"
      ),

      datetimeEnd: this.$dateFormat(new Date(), "yyyy-MM-dd"),

      fastTime: [
        {
          name: "今日",
          value: this.$dateFormat(new Date(), "yyyy-MM-dd")
        },
        {
          name: "昨日",
          value: this.$dateFormat(
            new Date().getTime() - 24 * 60 * 60 * 1000,
            "yyyy-MM-dd"
          )
        }
      ],
      defaultFastTime: this.$dateFormat(new Date(), "yyyy-MM-dd"),
      datetime: this.$dateFormat(new Date(), "yyyy-MM-dd"),

      groupList: this.getGroupList(),
      switchData: [],
      showPop:false,
      chartData: {
        columns: ["时间", "值"],
        rows: []
      },
      chart: []
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    itemClick(label,key) {

      this.label = label
      this.publicmethod(label)
    },
    orrder(data,name,vs){

      this.chartData.rows.forEach((v,i)=>{
        if(v['时间']==data){

          v[name] = vs

        }
      })
    },
    realuData(){
      if(this.label){
         this.publicmethod(this.label)
      }else {
        this.queryRealData()
      }
    },
    goback() {
      this.$router.go(-1);
    },
    switchres(index, show) {
      this.$set(this.switchData, index, show);
    },
    getGroupList() {
      let pageConfig = this.$store.getters.pageConfig;
      let menuId = this.$route.query.menuId;
      let list = [];
      pageConfig.forEach(function(v, i) {
        if (v.menuId == menuId) {
          list.push.apply(list, v.groups);
        }
      });
      return list;
    },
    getsenids(type) {
      //0:实时，默认查实时,1:小时，2:日，3:月,
      let list = [];
      this.getGroupList().forEach(function(v, i) {

        v.eles.forEach(function(vv, ii) {

          if (vv.querytype == type) list.push(vv.elecd);
        });
      });
      return [...new Set(list)].join(",");
    },
    publicmethod(label){
      let list = [];
      let listname = ["时间"]
      this.groupList.forEach(item=>{
        if(item.grpname == label){

          item.eles.forEach(v=>{

            listname.push(v.elename)
            list.push(v.elecd)
          })
        }
      })


      let  chartDatacolumns = ["时间"]
      this.chartData.columns = chartDatacolumns;
      this.chartData.rows = [];

      var start = new Date(this.datetimeStart).getTime();
      var end = new Date(this.datetimeEnd).getTime();
      while (start<end) {
        this.chartData.rows.push({
          "时间":this.$dateFormat(start,'yyyy-MM-dd'),
           '小浪底塔前淤积高程':0
        });
        start = start + 24 * 60 * 60 * 1000;
      }
      this.$post('/appquery/querySpanData.action',{
          senids:list.join(","),
          timeBeginStr:this.datetimeStart+' 00:00:00',
          timeEndStr:this.datetimeEnd+' 00:00:00',
          type:2
        },
        data => {
          let self = this;
          this.chartData.columns = listname

          if(JSON.stringify(data) == "{}"){

            self.tableDates=[]
          }else {

            var arr =[]
            for(var index in data){
              for(var indexs in data[index]){
                arr.push(data[index][indexs])
              }
            }
            self.tableDates=[]
            arr.forEach(item=>{
              item.timest = self.$dateFormat(item.time, "yyyy-MM-dd");
            })
            self.tableDates = arr

            arr.forEach(item=>{
              var data = self.$dateFormat(item.time, "yyyy-MM-dd");
              self.orrder(data,item.name,item.v)
            })
          }

        }
      );
    },
    queryRealData() {
      if(!this.label){
        let  chartDatacolumns = ["时间"]
        this.chartData.columns = chartDatacolumns;
        this.chartData.rows = [];
        var start = new Date(this.datetimeStart).getTime();
        var end = new Date(this.datetimeEnd).getTime();

        while (start<end) {
          this.chartData.rows.push({
            "时间":this.$dateFormat(start,'yyyy-MM-dd'),
          });
          start = start + 24 * 60 * 60 * 1000;
        }
        this.$post('/appquery/querySpanData.action',{
            senids:this.getsenids(0),
            timeBeginStr:this.datetimeStart+' 00:00:00',
            timeEndStr:this.datetimeEnd+' 00:00:00',
            type:2
          },
          data => {
            let self = this;
            self.groupList.forEach(function(v, i) {
              self.switchData.push(true);
              var arr =[]
              v.eles.forEach(function(vv, ii) {

                switch (vv.querytype) {
                  case 0:
                    self.chartData.columns.push(vv.elename)

                    for(var index in data){
                      for(var indexs in data[index]){
                        arr.push(data[index][indexs])
                      }
                    }
                    self.tableDates=[]
                    arr.forEach(item=>{
                      item.timest = self.$dateFormat(item.time, "yyyy-MM-dd");
                    })
                    self.tableDates = arr

                    arr.forEach(item=>{
                      var data = self.$dateFormat(item.time, "yyyy-MM-dd");
                      self.orrder(data,item.name,item.v)
                    })
                    break;
                  case 1:
                    console.log(1)
                    break;
                  case 2:
                    break;
                  case 3:
                    break;
                }
              });
            });
          }
        );
      }else {

      }

    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
    },
    timeSelect(s) {
      this.datetimeStart = s[0];
    },
  },
  /* created() {
    this.queryRealData(); // 获取测站测点值
  } */
  activated() {
    this.queryRealData(); // 获取测站测点值
  }
};
</script>


<style>
.icon-custom-mation {
  color: #fff;
}
.dam-title {
  text-align: left;
  padding: 0.2rem 0.3rem;
  background-color: #2279ff;
  margin-top: -1px;
  color: #fff;
  font-size: 0.35rem;
}
.dam-title img {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 0.4rem;
}
.card-table {
  margin-top: 1px;
}
.card-table table {
  width: 100%;
  border-collapse: collapse;
}
.card-table table th,
.card-table table td {
  border: 1px solid #ccc;
  padding: 0.1rem 0.15rem;
}
.card-table table td > div {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.card-table table thead tr {
  background-color: #73a8ff;
  color: #fff;
}
.card-table table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.card-table table tbody tr:nth-of-type(odd) {
  background-color: #f7f6fb;
}
</style>



