<template>
  <div style="margin-top:calc(1.45rem + 50px)">
    <yd-navbar title="视频监测" :fixed="true" bgcolor="#2279FF" color="#fff">
      <!-- <router-link to="/mation" slot="left">
                <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
            </router-link> -->
      <i class="icon-custom-refresh" slot="left" @click="refresh" style="color:#fff;font-size:0.4rem"></i>
      <i class="icon-custom-sifang" slot="right" @click="changeMode" style="color:#fff;font-size:0.5rem"></i>
    </yd-navbar>
    <yd-search v-model="value1" id="videoSearch" :on-submit="submitHandler"></yd-search>
    <div class="main-view">
      <yd-cell-group v-if="list.length">
        <yd-cell-item arrow v-for="(value,index) in showList" :key="index" @click.native="videoPlay(value.id,value.name)">
          <span slot="left" class="video-index">{{ index+1 }}</span>
          <div slot="left" class="video-msg">
            <span>{{ value.name }}</span> <br>
          </div>
        </yd-cell-item>
      </yd-cell-group>
    </div>
    <no-data v-if="!list.length"></no-data>
  </div>
</template>

<script type="text/babel">
export default {
  data() {
    return {
      value:'',
      showList:[],
      list: []
    };
  },
  methods: {
    submitHandler(value) {
      this.$dialog.toast({ mes: `搜索：${value}` });
      let filterList = this.list.filter((v)=>{
        return v.name.includes(value)
      });
      console.log(filterList);
      this.showList = filterList;
    },
    refresh() {
      var self = this;
      cordova.exec(
        function(data) {
          self.list = data;
          self.showList = data;
        },
        null,
        "XLDPlugin",
        "getVideoLiveList",
        []
      );
    },
    changeMode() {
      cordova.exec(null, null, "XLDPlugin", "fourLivePlay", []);
    },
    videoPlay(id, name) {
      cordova.exec(null, null, "XLDPlugin", "videoLivePlayer", [
        {
          id: id,
          name: name
        }
      ]);
    }
  },
  mounted() {
    this.refresh();
  }
};
</script>
<style>
.video-index {
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
  line-height: 0.5rem;
  color: #fff;
  background-color: #5a93fd;
  border-radius: 50%;
}
.video-msg {
  text-align: left;
  margin-left: 0.3rem;
}
.video-msg > span {
  font-size: 0.3rem;
  font-weight: 600;
}
#videoSearch {
  position: fixed;
  top:1.45rem;
  left:0;
  width: 100%;
  z-index: 999;
}
</style>



