<template>
    <article>
        <div class="card-top">
            <div @click="showDetails(id,page)">
                <i class="icon-custom-stag" style="font-size:.34rem;color:#EA2A2A;vertical-align:center;"></i> 命令详情
                <i class="icon-custom-right"></i>
            </div>
            <div style="line-height:.55rem;color:orange;">水工已接收</div>
        </div>
        <slot></slot>
        <section class="card-des" v-show="showDes">
            <slot name="des"></slot>
        </section>
        <div class="expand-tag" @click="changeStatus">{{ showDes?'收起':'内容' }} <i :class="'icon-custom-'+ (showDes?'up':'down')"></i> </div>
    </article>
</template>

<script>
export default {
  props: {
    id: String,
    page: String
  },
  data() {
    return {
      showDes: false
    };
  },
  methods: {
    changeStatus() {
      this.showDes = !this.showDes;
    },
    showDetails(id, page) {
      this.$router.push({ name: page + "orderDetail", params: { id: id } });
    }
  }
};
</script>

<style scoped>
article {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  padding: 0 0.3rem;
  padding-bottom: 0;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
  overflow: hidden;
  justify-content: center;
  -webkit-justify-content: center;
  -moz-justify-content: center;
  -ms-justify-content: center;
}

article > section,.card-top,.card-des>section{
  font-size: 0.26rem;
  width: 100%;
  text-align: left;
  padding-bottom: 0.2rem;
  /* border-bottom: 0.5px solid #eaeaea; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
article > section>div:nth-of-type(1){
    width: 40%;
}
article > section>div:nth-of-type(2){
    width: 60%;
}
.expand-tag {
  color: #697bdd;
  padding: 0.1rem 0;
}
.card-des {
  display: block;
  border-top:0.5px solid #ccc;
  color:#c1c1c1;
  padding-bottom: 0;
}
.card-des>section {
  margin-bottom: .1rem;
  background-color: #fdfdfd;
}
.card-des div {
  width: 50%;
  margin-top: .1rem;
}
</style>





