<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <h3 class="h-title">{{title}}</h3>
    <h3 class="h-sub-title">{{subTitle}}</h3>
    <div>
      <div id="echartContainer" width="500px" height="400px"
           :style="{visibility: isChartVisible ? 'visible' : 'hidden'}"></div>
    </div>
  </div>
</template>

<script>


  export default {
    data() {
      return {
        myEcharts: null,
        title: "",
        subTitle: "",
        mCurveParam: {}, // 曲线参数
        mDataList: [
          {time: "实时", value: 1},
          {time: "小时", value: 2},
          {time: "日", value: 3}
        ],
        showTable: true,
      }
    },
    components: {},
    computed: {
      isChartVisible() {
        return true;
      },
      ismenu:function(){
        if (this.$route.name.indexOf('menu')) {
          return true;
        } else {
          return false;
        }

      },
    },
    mounted() {
      // 获取窗口高度和宽度
      let deviceWidth = document.documentElement.clientWidth;
      let deviceHeight = document.documentElement.clientHeight;
      // 设置chart的高度和宽度
      let chartDom = document.getElementById('echartContainer');
      chartDom.style.width = deviceWidth + 'px';
      chartDom.style.height = (deviceHeight - 200) + 'px';
      this.myEcharts = this.$echarts.init(chartDom);
      let option = {};
      this.myEcharts.setOption(option);
      console.log("curve width, height:", chartDom.style.width, chartDom.style.height);
    },

    methods: {
      goback() {
        this.$router.go(-1);
      },
      // 重新设置option
      drawCharts() {
        if (this.mDataList) {
          let data = this.mDataList;
          let chartType = 'line';
          let v0 = this.mCurveParam.v0 + ":";
          let v1 = this.mCurveParam.v1 + ":";
          let optionLine = {
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                animation: false,
                label: {
                  backgroundColor: '#ccc',
                  borderColor: '#aaa',
                  borderWidth: 1,
                  shadowBlur: 0,
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  textStyle: {
                    color: '#222'
                  }
                }
              },
              formatter: function(params) {
                let res = v0+params[0].name+'<br/>';
                res+= v1+params[0].value+'<br/>';
                return res;
              }
            },
            xAxis: {
              name: this.mCurveParam.v0,
              type: 'category',
              boundaryGap: false,
              data: data.map(function (item) {
                return item.v0;
              }),
              axisLabel: {
                rotate:45,
                formatter: function (value) {
                  // let date = new Date(value);
                  // return idx === 0 ? [date.getMonth() + 1, date.getDate()] : [date.getHours(), date.getMinutes()].join(':');
                  return value;
                }
              },
            },
            yAxis: {
              type: 'value',
              name:  this.mCurveParam.v1,
            },
            grid: {
              left: '3%',
              right: '16%',
              bottom: '3%',
              containLabel: true
            },
            series: [{
              type: chartType,
              data: data.map(function (item) {
                return item.v1;
              }),
              hoverAnimation: true,
              symbolSize: 6,
              itemStyle: {
                normal: {
                  color: '#c23531'
                }
              },
              showSymbol: true
            }]
          };
          this.myEcharts.setOption(optionLine);
        }
      },


      // 获取数据
      getData() {
        let curveId = null;
        if(this.mCurveParam.vId)
        {
          curveId = this.mCurveParam.vId;
        }
        else
        {
          curveId = this.mCurveParam.id;
        }
        let queryParam = {
          curveId: curveId
        };
        this.$dialog.loading.open();
        this.$axios
          .post(
            "/appcurve/getCurveData.action",
            this.$qs.stringify(queryParam)
          )
          .then(res => {
            let data = res.data;
            if(data.result)
            {
              this.mDataList = data.data;
              this.drawCharts();
            }
            else
            {
              this.$dialog.alert({ mes: "数据为空", icon: "error" });
            }
            this.$dialog.loading.close();

          })
          .catch(e => {
            this.$dialog.loading.close();
            console.log(e);
          });
      },


    },
    created() {


    },
    // Called when a kept-alive component is activated.
    activated() {
      this.title = '';
      let param = this.$route.params;
      this.mCurveParam = param.data;
      this.title += this.mCurveParam.name;
      this.subTitle = "";
      if(this.mCurveParam.vName)
      {
        this.subTitle += this.mCurveParam.vName;
      }
      this.subTitle += "("+this.mCurveParam.dimname + ")";
      console.log("curve param is:", this.mCurveParam);

      // 获取数据
      this.getData();
    }
  }
</script>

<style scoped>
  .h-title {
    font-size: .3rem;
    color: #6f7acc;
  }

  .item-time {
    font-size: .25rem;
  }

  .item-head {
    font-size: .35rem;
    background-color: #9cc1cc;
  }
</style>




