<template>
  <div style="margin-top:1.45rem">
    <yd-navbar :title="this.$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div @click="goback" slot="left">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>
    <div class="main-view">
      <keep-alive>
        <router-view v-if="!isshow"></router-view>
      </keep-alive>
      <yd-cell-group v-show="isshow">
        <yd-cell-item arrow href="/mation/personSpace" type="link">
          <yd-icon slot="icon" name="water" custom></yd-icon>
          <span slot="left">个人资料</span>
        </yd-cell-item>
        <yd-cell-item arrow href="/mation/setDefaultPage" type="link">
          <yd-icon slot="icon" name="choose" custom></yd-icon>
          <span slot="left">默认首页</span>
        </yd-cell-item>
        <yd-cell-item arrow href="/mation/versionsMsg" type="link">
          <yd-icon slot="icon" name="banben" custom></yd-icon>
          <span slot="left">版本信息</span>
        </yd-cell-item>
      </yd-cell-group>
      <yd-button-group v-show="isshow" style="margin-top:1.5rem;padding:0 0.24rem;">
        <yd-button size="large" type="primary" bgcolor="#2278FF" color="#fff" @click.native="loginOut">退出当前账号</yd-button>
      </yd-button-group>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isshow: true
    };
  },
  methods: {
    goback() {
      if (this.$route.name === "mation") {
        console.log(this.$store.getters.defaultPageId)
        this.$router.push({
          name: "home" + this.$store.getters.defaultPageName,
          query:{
            menuId: this.$store.getters.defaultPageId
          }
        });
        this.$emit("changeDefaultPage");
      } else {
        this.$router.go(-1);
      }
    },
    loginOut() {
      // 清除app推送别名
      document.addEventListener(
        "deviceready",
        function() {
          cordova.exec(null, null, "XLDPlugin", "clearAlias", []);
        },
        false
      );
      this.$dialog.toast({
        mes: "退出成功",
        timeout: 1000,
        icon: "success",
        callback: () => {
          this.$store.commit("dataout");
          this.$router.push({ name: "login" });
        }
      });
    }
  },
  watch: {
    $route() {
      if (this.$route.name === "mation") {
        this.isshow = true;
      } else {
        this.isshow = false;
      }
    }
  }
};
</script>

<style>
.yd-cell-icon i {
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  font-size: 0.35rem !important;
  border-radius: 50%;
  color: #fff;
  background-color: #337cfd;
}
</style>



