<template>
    <article style="margin-top:-1.45rem">
        <div class="addressimg-box">
            <!-- <img src="../../../static/images/contact_bg.png" style="width:100%;"> -->
            <i class="yd-back-icon" style="color: rgb(255, 255, 255);" @click="goback"></i>
        </div>
        <yd-cell-item style="padding:.4rem .5rem;background-color:#fff">
            <yd-icon slot="icon" name="dianhua" custom style="font-size:.4rem;margin-right:.2rem;color:#2279ff;background-color:transparent"></yd-icon>
            <div slot="left" style="text-align:left">
                <span class="address-msg">{{ datauser.phone }}</span> <br>
                <span>手机号码</span>
            </div>
            <a :href="'tel://' + datauser.phone" class="address-cellpone icon-custom-dianhua" slot="right">
            </a>
        </yd-cell-item>
        <yd-cell-item style="padding:.4rem .5rem;background-color:#fff">
            <yd-icon slot="icon" name="dianhua1" custom style="font-size:.4rem;margin-right:.2rem;color:#2279ff;background-color:transparent"></yd-icon>
            <div slot="left" style="text-align:left;">
                <span class="address-msg">{{ datauser.mobile }}</span> <br>
                <span>电话号码</span>
            </div>
            <a :href="'tel://' + datauser.mobile" class="address-cellpone icon-custom-dianhua1" slot="right">
            </a>
        </yd-cell-item>
        <yd-cell-item style="padding:.4rem .5rem;background-color:#fff">
            <yd-icon slot="icon" name="mation" custom style="font-size:.4rem;margin-right:.2rem;color:#2279ff;background-color:transparent"></yd-icon>
            <div slot="left" style="text-align:left;">
                <span class="address-msg">{{ datauser.name }}</span> <br>
                <span>姓名</span>
            </div>
        </yd-cell-item>
        <yd-cell-item style="padding:.4rem .5rem;background-color:#fff">
            <yd-icon slot="icon" name="part" custom style="font-size:.4rem;margin-right:.2rem;color:#2279ff;background-color:transparent"></yd-icon>
            <div slot="left" style="text-align:left;">
                <span class="address-msg">生产信息接收组</span> <br>
                <span>部门</span>
            </div>
        </yd-cell-item>
    </article>
</template>

<script>
export default {
  props: ["datauser"],
  methods: {
    goback() {
      this.$router.go(-1);
    }
  },
  created() {
  }
};
</script>

<style>
.addressimg-box {
  position: relative;
  height: 4.5rem;
  background: url("../../assets/contact_bg.png") no-repeat;
  background-size: 100%;
}
.addressimg-box > i {
  position: absolute;
  top: 0.8rem;
  left: 0.4rem;
}
.address-cellpone {
  position: relative;
  width: 0.8rem;
  height: 0.8rem;
  box-shadow: 0 3px 8px #000;
  border-radius: 50%;
  background-color: #2279ff;
}
.address-cellpone::before {
  position: absolute;
  font-size: 0.4rem !important;
  color: #fff;
  top: 0.1rem;
  left: 0.2rem;
}
.address-msg {
    color:#2279ff;
}
</style>




