<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="!ismenu">
        <yd-icon name="mation" style="font-size:.54rem;color:#fff" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" style="color:#fff;font-size:.4rem" @click="showPop = true"></i>
    </yd-navbar>
    <calendar ref="calendar1" :events="calendar1.events" :value="calendar1.value" @select="select"></calendar>
    <div v-if="slectValue">
      <table class="table">
        <thead>
          <tr>
            <th>值班</th>
            <th>值班类型</th>
          </tr>
        </thead>
      </table>
    </div>
    <div v-if="slectValue">
      <table class="table">
        <tbody>
          <tr v-for="(value,index) in works[slectValue]" :key="index">
            <td>{{ value.dutyUser }}</td>
            <td>{{ value.type }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <yd-popup v-model="showPop" position="right">
      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">月份选择</h3>
        <select-button :data="fastTime" :value="defaultFastTime" @select="timeSelect"></select-button>
        <yd-cell-item>
          <span slot="left">开始时间：</span>
          <yd-datetime type="month" v-model="datetime" slot="right" @click.native="defaultFastTime = null"></yd-datetime>
        </yd-cell-item>
      </div>
      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;filterData()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
import calendar from "@/components/common/calendar.vue";
import { WORKDATA } from "./workData.js";
export default {
  components: {
    calendar
  },
  data() {
    return {
      defaultFastTime: this.$dateFormat(new Date(), "yyyy-MM"),
      fastTime: [
        {
          name: "当月",
          value: this.$dateFormat(new Date(), "yyyy-MM")
        },
        {
          name: "上个月",
          value: this.$dateFormat(
            new Date().getTime() - 30 * 24 * 60 * 60 * 1000,
            "yyyy-MM"
          )
        }
      ],
      showPop: false,
      datetime: "",
      calendar1: {
        value: [2018, 7, 20], //默认日期
        events: {},
        timestamp: Date.now()
      },
      works: {},
      slectValue: ""
    };
  },
  computed: {
    ismenu: function() {
      if (/(menu)/.test(this.$route.name)) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    select(value) {
      this.slectValue = value.join("-");
    },
    goback() {
      this.$router.go(-1);
    },
    filterData() {
      this.showPop = false;
      let value;
      if (this.defaultFastTime) {
        value = this.defaultFastTime.split("-");
      } else {
        value = this.datetime.split("-");
        value.push(1);
      }
      this.calendar1.value = value;
      this.slectValue = "";
      this.queryData();
    },
    reset() {
      this.defaultFastTime = this.fastTime[0].value;
    },
    timeSelect(s) {
      this.defaultFastTime = s[0];
    },
    queryData() {
      let bt = this.defaultFastTime
        ? this.defaultFastTime.split(",")[0]
        : this.datetime;
      let year = this.datetime.split("-")[0];
      let month = this.datetime.split("-")[1];
      if (month < 10) {
        month = month.replace("0", "");
      }
      let eventObj = {};
      let eventObjs = {};
      this.$get(
        "/appduty/getDutyOrder2.action",
        {
          timeStr: bt
        },
        data => {
          data.data.forEach(function(v, i) {
            if (v.records.length)
              // eventObj[`${year}-${month}-${v.day}`] = v.records[0].dutyUser;
              eventObj[`${year}-${month}-${v.day}`] = "值班";
            eventObjs[`${year}-${month}-${v.day}`] = v.records;
          });
          this.calendar1.events = eventObj;
          this.works = eventObjs;
        }
      );
      /* WORKDATA.data.forEach(function(v, i) {
        if (v.records.length)
          // eventObj[`${year}-${month}-${v.day}`] = v.records[0].dutyUser;
          eventObj[`${year}-${month}-${v.day}`] = "值班";
        eventObjs[`${year}-${month}-${v.day}`] = v.records;
      });
      this.calendar1.events = eventObj;
      this.works = eventObjs; */
    }
  },
  created() {
    this.datetime = this.$dateFormat(new Date(), "yyyy-MM-dd");
    this.calendar1.value = this.datetime.split("-");
    this.queryData();
  }
};
</script>


<style scoped>
.table {
  border-collapse: collapse;
  width: 100%;
}
.table th,
.table td {
  border: 1px solid #ccc;
  padding: 0.1rem 0.15rem;
  width: 50%;
}
.table thead tr {
  background-color: #fff;
  color: #2279ff;
}
.table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.table th > span,
.table td > span {
  display: block;
  white-space: nowrap;
  height: 0.35rem;
}
</style>




