<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
    </yd-navbar>

    <iframe id="show-iframe" frameborder=0  width="100%"  scrolling=auto :src="urlSrc"></iframe>
  </div>
</template>

<style scoped>


</style>

<script>
  export default {
    data () {
      return {
        urlSrc : '',
      }
    },
    computed:{
    ismenu:function(){
      if (this.$route.name.indexOf('menu')) {
        return true;
      } else {
        return false;
      }
    },
    },
    mounted(){
      const oIframe = document.getElementById('show-iframe');
      const deviceWidth = document.documentElement.clientWidth;
      const deviceHeight = document.documentElement.clientHeight;
      // oIframe.style.width = deviceWidth + 'px';
      oIframe.style.height = deviceHeight + 'px';
    },
    methods:{
      goback() {
        this.$router.go(-1);
      },
    },
    created(){
      // 发送过来的详细url
      let urlParam = this.$axios.defaults.baseURL+'/dsims/report/mpreport.html?id=';
      urlParam += this.$route.params.id;
      console.log("this is created and report url is:" + urlParam);
      // this.urlSrc = 'https://segmentfault.com/a/1190000008570622';
      this.urlSrc = urlParam;
    }
  }

</script>



