<template>
  <div>
    <yd-navbar :title="$route.meta.title" :fixed="true" bgcolor="#2279FF" color="#fff">
      <router-link to="/mation" slot="left" v-if="ismenu">
        <yd-icon name="mation" style="font-size:.54rem" custom></yd-icon>
      </router-link>
      <div slot="left" @click="goback" v-if="!ismenu">
        <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
      </div>
      <i class="icon-custom-shaixuan" slot="right" @click="showPop=true;" style="color:#fff;font-size:.4rem"></i>
    </yd-navbar>
    <yd-tab>
      <yd-tab-panel label="列表">
        <table>
          <thead>
            <tr>
              <th>名称</th>
              <th>值</th>
            </tr>
          </thead>
        </table>
        <div class="table-box">
          <table>
            <tbody>
              <tr v-for="(value,index) in keyList" :key="index">
                <td>{{ value.name }}</td>
                <td :id="value.ovid"></td>
              </tr>
            </tbody>
          </table>
        </div>
      </yd-tab-panel>
      <yd-tab-panel label="图形">
        <!-- <yd-lightbox class="light-imgbox">
          <yd-lightbox-img v-for="(item, key) in list" :key="key" :src="item.src"></yd-lightbox-img>
        </yd-lightbox>
        <div>
          <yd-button type="primary" @click.native="openPdf">预览PDF</yd-button>
        </div>
        <div style="margin-top:20px">
          <yd-button type="warning" @click.native="openPdf2">预览PDF2</yd-button>
        </div> -->
      </yd-tab-panel>

    </yd-tab>
    <yd-popup v-model="showPop" position="right">

      <div class="pop-custom">
        <h3 class="text-left" style="padding:.2rem">水库</h3>
        <select-button :data="reservoirData" :value="chooseReservoir" @select="reservoirSelect"></select-button>
      </div>

      <div class="pop-button">
        <div @click="reset">重置</div>
        <div @click="showPop=false;getValueList()">确定</div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showPop:false,
      chooseReservoir: "",
      reservoirData: [],
      keyList: [],
      list: [
        {
          src:
            "http://192.168.43.205:8080/XldWeb/monster/app/static/img/bgimage_1.jpg"
        },
        {
          src:
            "http://192.168.43.205:8080/XldWeb/monster/app/static/img/bgimage_1_1.jpg"
        }
      ]
    };
  },
  computed: {
    ismenu: function() {
      if (this.$route.name.indexOf("menu")) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    // 重置选择条件
    reset() {
      this.chooseReservoir = this.reservoirData[0].value;
    },
    // 选择完成事件
    reservoirSelect(s){
      this.chooseReservoir = s[0];
    },
    // 获取水库信息
    getValueList() {
      this.$get(
        "/appquery/queryOverviewInfo.action",
        {
          reservoirId: this.chooseReservoir
        },
        data => {
          // for (let key in data) {
          //   let tdDom = document.getElementById(key);
          //   tdDom.innerHTML = data[key];
          // }
          let _this = this;
          this.$nextTick().then(function() {
            _this.keyList.forEach(function(v, i) {
              let tdDom = document.getElementById(v.ovid);
              if (data[v.ovid]) tdDom.innerHTML = data[v.ovid];
            });
          });
        }
      );
    },
    // 打开PDF
    openPdf() {
      this.$showPDF(
        "http://192.168.43.205:8080/XldWeb/monster/app/static/img/test.pdf",
        "配置报告"
      );
    },
    openPdf2() {
      this.$showPDF(
        "http://192.168.43.205:8080/XldWeb/monster/app/static/img/pdftest.pdf",
        "管理手册"
      );
    }
  },
  created() {
    // 获取水库下拉框
    this.$get(
      "/query/getResers.action",
      {
        menuid: this.$route.query.menuId
      },
      data => {
        let _this = this;
        data.data.forEach((v,i) => {
          _this.reservoirData.push({
            name:v.pname,
            value:v.pid
          });
          if(i===0){
            _this.chooseReservoir = v.pid;
          }
        });
        this.getValueList(this.selectValue);
      }
    );
    // 获取水库信息的名称和单位
    this.$get(
      "/overview/getCollocationOverView.action",
      {
        // teamid: this.$store.getters.getTeamId
      },
      data => {
        this.keyList = data.data;
      }
    );
  }
};
</script>

<style scoped>
.icon-custom-mation {
  color: #fff;
}
table {
  width: 100%;
  border-collapse: collapse;
}
table th,
table td {
  border: 1px solid #ccc;
  width: 50%;
  padding: 0.1rem 0.15rem;
}
table thead tr {
  background-color: #73a8ff;
  color: #fff;
}
table tbody tr:nth-of-type(even) {
  background-color: #fff;
}
.table-box {
  max-height: 10.3rem;
  overflow-y: auto;
}
.light-imgbox {
  display: flex;
  justify-content: space-between;
  padding: 0.1rem;
  flex-wrap: wrap;
}
.light-imgbox > img {
  width: 49%;
  height: 3rem;
  margin-top: 0.1rem;
}
</style>



