<template>
  <div>
    <div class="person-name">
      <div class="person-name-content">
        <div style="float:left;margin-top:.6rem;margin-left:.4rem">
          <div style="float:left;line-height:.4rem;text-align:left">
            <span style="font-size:.35rem;font-weight:600" v-text="logindata.userName"></span><br>
            <span>登录名称</span>
          </div>
          <div style="float:right" class="person-right-icon">

          </div>
        </div>
        <div style="float:right;margin-top:.2rem;margin-right:.4rem">
          <img src="http://up.qqya.com/allimg/201710-t/17-101804_132321.jpg" class="chat-head">
        </div>
      </div>
    </div>
    <yd-cell-group style="padding:0 .3rem;">
      <yd-cell-item type="div">
        <yd-icon slot="icon" class="person-icon" name="wenjian" custom style="color:#8BBAFF;"></yd-icon>
        <span slot="left">姓名</span>
        <span slot="right" v-text="logindata.realName"></span>
      </yd-cell-item>
      <yd-cell-item type="div" @click.native="reshowgroup">
        <yd-icon slot="icon" class="person-icon" name="part" custom color="#FEB6B7"></yd-icon>
        <span slot="left">用户组</span>
      </yd-cell-item>
      <transition name="fade">
      <yd-cell-group style="padding-left:.5rem;margin-bottom:0" v-if="showgroup">
        <yd-cell-item type="div" v-for="(value,index) in logindata.teams" :key="index">
          <span slot="left">{{ value.teamName }}</span>
        </yd-cell-item>
      </yd-cell-group>
      </transition>
      <yd-cell-item type="div">
        <yd-icon slot="icon" class="person-icon" name="tag" custom style="color:#FFCE5B;"></yd-icon>
        <span slot="left">编号</span>
        <span slot="right" v-text="logindata.teams[0].teamId"></span>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script>
export default {
  data(){
    return {
      showgroup:true,
    }
  },
  computed: {
    logindata: function() {
      return this.$store.state.logindata.userConfig.userInfo;
    }
  },
  methods:{
    reshowgroup(){
      this.showgroup = !this.showgroup;
    }
  }
};
</script>

<style >
.chat-head {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
}
.person-right-icon::before {
  margin-left: 0.2rem;
  margin-top: 0.2rem;
  display: block;
  font-family: YDUI-INLAY;
  font-size: 0.34rem;
  color: #c9c9c9;
  content: "\E608";
}
.yd-cell-icon i.person-icon {
  font-size: 0.35rem !important;
  background-color: transparent;
}
.person-name {
  position: relative;
  height: 2rem;
  background: #2279ff;
  margin-top: -1px;
  margin-bottom: 0.6rem;
  border-bottom-right-radius: 20%;
}
.person-name-content {
  position: absolute;
  border-radius: 0.1rem;
  width: 84%;
  margin-left: 8%;
  height: 100%;
  background: #fff;
  top: 0.3rem;
}
</style>


