<template>
  <div class="btn-box">
    <span class="btn" :class="{'active':show}" @click="show = true">{{name1}}</span>
    <span class="btn" :class="{'active':!show}" @click="show = false">{{name2}}</span>
  </div>
</template>

<script>
  export default {
    props:{
      index:Number,
      name1:{
        default:'图表'
      },
      name2:{
        default:'表格'
      },
    },
    data(){
      return{
        show:true
      }
    },
    watch:{
      show:function(){
        this.$emit('switch',this.index,this.show);
      }
    }
  }
</script>


<style scoped>
  .btn-box {
    text-align: right;
    padding: 2px 0.2rem;
  }
  .btn {
    display: inline-block;
    border: 1px solid #2279ff;
    width: 1rem;
    padding: 3px;
    text-align: center;
  }
  .btn.active {
    background-color: #2279ff;
    color:#fff;
  }
  .btn-box>.btn:first-of-type {
    border-top-left-radius: .1rem;
    border-bottom-left-radius: .1rem;
    margin-right: -5px;
  }
  .btn-box>.btn:last-of-type {
    border-top-right-radius: .1rem;
    border-bottom-right-radius: .1rem;
  }
</style>



